import {
    Component,
    OnInit,
    OnDestroy
} from '@angular/core';
import {
    select,
    Store
} from '@ngrx/store';
import { BuildingStoreState } from '@mckinsey-converge/buildings';
import {
    AlertMode,
    SelectOption
} from '@mckinsey-converge/ui';
import { loadOrganisations } from '@mckinsey-converge/organisation';
import { BaseDirective,
    dateBuildingFormatString,
} from '@mckinsey-converge/base';
import {
    Booking,
    User,
    Building,
    BuildingCity,
    IEngineResponse,
    flattenDisplayOffices,
    selectCancelledBookingsResultsFromState,
    selectHomepageBookingsResultsFromState,
    selectPastBookingsResultsFromState,
    BookingStoreState,
    BookingFormData,
    clearBookingFormData,
    storeBookingFormData,
    BookingsService
} from '@mckinsey-converge/data-common';
import { TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import { Urls } from 'src/app/utilities/urls';
import {
    ActivatedRoute,
    Router
} from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { BookingDetailsModalComponent } from '../booking-details-modal/booking-details-modal.component';
import { loadRoomsForResults } from '@mckinsey-converge/rooms';
import { loadBookingsWithQuery, MyBookingsStoreState } from '@mckinsey-converge/bookings';
import { UserStoreState } from '@mckinsey-converge/user';
import {
    BehaviorSubject,
} from 'rxjs';
import {
    distinctUntilChanged,
    elementAt,
    map,
    take,
} from 'rxjs/operators';
import { LoadingModel } from '@mckinsey-converge/loading';
import { selectCurrentUser } from '@mckinsey-converge/user';
import {
    mapResultsToViewModels,
    BookingResultsViewModel
} from 'src/app/booking/manage-bookings/manage-bookings.viewmodel';
import {
    buildingStateSelector,
    selectBuildingOptionsGroupedByCity
} from '@mckinsey-converge/buildings';
import { DateNow } from '@mckinsey-converge/date-tz';

export type RoomBookingStoreState = BookingStoreState & BuildingStoreState & MyBookingsStoreState & UserStoreState;

@Component({
    selector: 'app-room-booking',
    template: `
        <div class="mck-column mck-content-expand mck-content-page-container">
            <header class="header">
                <a-topbar-header
                    [mode]="TopBarHeaderMode.Light">
                </a-topbar-header>
            </header>
            <div class="mck-column mck-stretch mck-overflow room-booking">
                <div class="mck-column mck-center">
                    <div class="mck-column heading-container mck-content-body">
                        <h1 class="heading">Meeting Room Booking</h1>
                        <div class="mck-row mck-center heading-icon-container">
                            <img alt="Booking Coachmark" class="heading-icon">
                        </div>
                    </div>
                </div>
                <div class="mck-column mck-center">
                    <div class="mck-column mck-center mck-content-body mck-shadow input-background">
                        <app-room-booking-form
                            #roomBookingForm
                            (submitForm)="routeToResults($event)">
                            <mck-modal-button-row
                                class="search-button"
                                primaryLabel="Search"
                                [secondaryLabel]="roomBookingForm.showCancel ? 'Cancel' : ''"
                                (secondarySubmit)="roomBookingForm.cancelSubmit.emit()"
                                [disableFixed]="true">
                            </mck-modal-button-row>
                        </app-room-booking-form>
                    </div>
                    <div class="mck-column mck-center mck-content-body mck-shadow your-bookings">
                        <div>
                            <h2>Your Bookings</h2>
                            <div *ngIf="showResults() else loading_message">
                                <div class="no" *ngIf="!showBookings">
                                    <img src="assets/icon/upcoming.svg">
                                    <div>You have no upcoming bookings</div>
                                </div>
                                <div *ngIf="showBookings">
                                    <app-booking-result-item
                                        *ngFor="let viewModel of upcomingBookings.slice(0,3); let i = index"
                                        [small]="true"
                                        [viewModel]="viewModel"
                                        [bookings]="upcomingBookings"
                                        [index]="i"
                                        (bookingSelect)="viewBooking(viewModel)">
                                    </app-booking-result-item>
                                </div>
                            </div>
                            <ng-template #loading_message>
                                <app-booking-result-item [small]="true" *ngFor="let i of [0, 1, 2]">
                                </app-booking-result-item>
                            </ng-template>
                        </div>
                        <div class="manage-button">
                           <button
                            mat-button class="mck-primary-button mck-modal-primary-button"
                            [disabled]="!showManageButtonA && !showManageButtonB && !showManageButtonC"
                            (click)="routeToManageBookings()"
                            >
                            Manage</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./room-booking.component.scss']
})
export class RoomBookingComponent extends BaseDirective implements OnInit, OnDestroy {
    public TopBarHeaderMode = TopBarHeaderMode;
    public AlertMode = AlertMode;

    public width = 16;

    // public showBanner = true;
    public showBookings = false;
    public showManageButtonA = false;
    public showManageButtonB = false;
    public showManageButtonC = false;
    public dateString: string;

    public homepageBookingResults = new BehaviorSubject<LoadingModel<IEngineResponse>>(LoadingModel.empty());
    public pastBookingResults = new BehaviorSubject<LoadingModel<IEngineResponse>>(LoadingModel.empty());
    public upcomingBookings: BookingResultsViewModel[];
    public buildings = new BehaviorSubject<SelectOption<any>[]>([]);
    public reloading = false;

    /** Current User */
    public currentUser: User;
    master_booking: BookingResultsViewModel;
    allBookings: any[];

    constructor(private store: Store<RoomBookingStoreState>,
                private router: Router,
                private route: ActivatedRoute,
                private _bookings: BookingsService,
                private dialog: MatDialog) {
        super();
        this.dateString = dateBuildingFormatString(DateNow(new Date()));
    }

    public ngOnDestroy() {
        if (typeof window === 'object') {
            window.onbeforeunload = undefined;
        }
				// clear interval
				this.clearInterval('refreshBookings');
    }

    public ngOnInit(): void {
        // only run on window onload
        if (typeof window === 'object') {
            window.onbeforeunload = (event) => {
                const e = event || window.event;
                this.store.dispatch(clearBookingFormData())
                return e;
              };
        }

        this.store.dispatch(loadOrganisations.request());

        this.subscription('buildings', this.store.pipe(
            select(buildingStateSelector),
            select(selectBuildingOptionsGroupedByCity),
            map((grouped: Map<BuildingCity, Building[]>) => {
                return flattenDisplayOffices(grouped);
            }),
            distinctUntilChanged()
        ).subscribe(next => {
            this.buildings.next(next);
            // console.log('on buildings ')
            // this.refresh('homepage');
        }));

        this.subscription('currentUser', this.store.select(selectCurrentUser)
        .pipe(take(1))
        .subscribe((user: User) => {
                // Once we know we have the user loaded
                this.currentUser = new User(user);
                this.store.dispatch(loadBookingsWithQuery({ 'email': this.currentUser.email, 'from': DateNow(new Date()).ms, 'target': 'homepage' }));
                // start the timer at the top of the minute;
                // const offset = DateNow(new Date()).startOfValue('minute').addValue({ minutes: 1 }).ms - DateNow(new Date()).ms;
                // fetch it on one minute interval now that there is room data.
                // setTimeout(() => {
                //     console.log('on setTimeout ')
                //     this.refresh('homepage')
                    this.interval('refreshBookings', () => {
											this.refresh('homepage')
										}, 60 * 1000);
                // }, offset + 5000)

            })
        );

        this.subscription('homepageResults', this.store.pipe(
            select(selectHomepageBookingsResultsFromState)
        ).subscribe(next => {
            this.homepageBookingResults.next(next);
            if (!next.isEmpty() && next.success && Array.isArray(next.success.results) && next.success.total) {
                this.reloading = false;
                this.showManageButtonA = true;
                this.upcomingBookings = mapResultsToViewModels(next.success.results).filter(b => !(b.booking.is_recurrent && b.booking.recurrence_type === 'master'));
                this.allBookings = next.success.results;

                if (this.upcomingBookings.length) {
                    this.showBookings = true;
                }
            } else {
                this.reloading = false;
                this.showManageButtonA = false;
                this.upcomingBookings = [];
                this.showBookings = false;
            }
        }));

        this.subscription('pastResults', this.store.pipe(
            select(selectPastBookingsResultsFromState)
        ).subscribe(next => {
            if (!next.isEmpty() && next.optionalSuccess?.total) {
                this.showManageButtonB = true;
            }
        }));

        this.subscription('cancelledResults', this.store.pipe(
            select(selectCancelledBookingsResultsFromState)
        ).subscribe(next => {
            if (!next.isEmpty() && next.optionalSuccess?.total) {
                this.showManageButtonC = true;
            }
        }));
    }

    public showResults() {
        // Don't show the loading state while we're reloading with Building details.
        return this.reloading || this.homepageBookingResults.value.hasLoaded();
    }

    public getBuilding(booking: Booking) {
        if (this.buildings && booking.building_zone) {
            return this.buildings.value.find(b => booking.building_zone === b.value)?.meta.building;
        }
        return '';
    }

    // public hideBanner() {
    //     this.showBanner = false;
    // }

    public learnMore() {
        window.open(Urls.LearnMore);
    }

    public routeToResults(next: BookingFormData) {
        this.store.dispatch(storeBookingFormData({
            payload: {
                ...next
            }
        }));
        this.store.dispatch(loadRoomsForResults.clear());
        this.router.navigate(['results'], { relativeTo: this.route });
    }

    public routeToManageBookings() {
        this.router.navigate(['manage'], { relativeTo: this.route });
    }

    public refresh(target: string) {
        if (!this.currentUser?.email) {
            return;
        }

        // tslint:disable-next-line: max-line-length
        this.store.dispatch(loadBookingsWithQuery({ 'email': this.currentUser.email, 'from': DateNow(new Date()).ms, target }));
    }


		/**
		 * Displays detailed information about a booking in a modal dialog.
		 * If the booking is part of a recurring series, it retrieves the master booking
		 * details and displays them along with the selected booking. The modal allows
		 * users to view and interact with the booking details. After the modal is closed,
		 * it checks for special flags to determine if the active form and result data should be cleared.
		 *
		 * @param {BookingResultsViewModel} viewModel - The view model containing booking information to display.
		 */
    public async viewBooking(viewModel: BookingResultsViewModel) {

			// Handle logic for recurring bookings to fetch the master booking details
    	// and set the master_booking property for displaying in the modal
			if(viewModel.booking.is_recurrent) {
					let booking = this.allBookings.find(bkn => bkn.id === viewModel.booking.recurrence_master_id && bkn.recurrence_type === 'master1') ;
					if (!booking) {
							booking = await this._bookings.show(viewModel.booking.recurrence_master_id);
					}
					this.master_booking = mapResultsToViewModels([booking])[0];
			}

			// Open a modal dialog to display the booking details along with master booking (if applicable)
			const dialogRefX = this.dialog.open(BookingDetailsModalComponent, {
					panelClass: 'mck-modal-wide-manage-booking',
					data: {
							booking: viewModel.booking,	// Selected booking information
							building: this.getBuilding(viewModel.booking),	// Building information for the booking
							master_booking: this.master_booking?.booking || null,	// Master booking details (if available)
							refresh: () => {
									this.refresh('homepage');	// Function to refresh the homepage after modal closure
							}
					},
					autoFocus: false,
					backdropClass: 'mck-modal-backdrop',
			});

			dialogRefX.afterClosed().subscribe((flag) => {
				// Clear form and result data if the flag indicates it's necessary
					flag !== 'DoNotClearActiveForm' && this.clearFormAndResultData();
			});
    }

		/**
		 * Clears active form and result data from the store.
		 *
		 * This function dispatches actions to clear the active form data and the loaded
		 * rooms data from the store. It is typically used after a modal interaction or
		 * a specific user action to reset the form state and loaded room information.
		 */
    public clearFormAndResultData() {
        this.store.dispatch(clearBookingFormData());
        this.store.dispatch(loadRoomsForResults.clear());
    }


}
