import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ViewEncapsulation,
} from '@angular/core';
import { BehaviorSubject, combineLatest, interval } from 'rxjs';
import { BaseDirective, HashMap, Point } from '@mckinsey-converge/base';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { select, Store } from '@ngrx/store';
import { BuildingStoreState } from '@mckinsey-converge/buildings';
import {
    Building,
    BuildingLevel,
    SettingsService,
    Space,
    mapBuildingLevelToOption,
    mapResultsToViewModels,
    RoomResultsViewModel,
    selectActiveForm,
    selectBookingLocations,
    selectMapFilteredResults,
    selectSelectedBuildingOptions,
    roomFormDataToQuery,
    FilteredSpaces,
    BookingStoreState,
    storeBookingFormData,
    BookingFormData,
    MapListener,
    selectFilteredDiffSizeResults
} from '@mckinsey-converge/data-common';
import { distinctUntilChanged, map, take, withLatestFrom } from 'rxjs/operators';
import { SelectOption } from '@mckinsey-converge/ui';
import { filterUndefined } from '@mckinsey-converge/rx-utils';
import { MatDialog } from '@angular/material/dialog';
import { OfficeInformationModalComponent } from 'src/app/map/office-information-modal/office-information-modal.component';
import {
    CENTER_POINT,
    generateMapListenersForSpace,
    generateMapStyleForSpace,
    MAX_ZOOM_LEVEL,
    MIN_ZOOM_LEVEL,
    roomToMapFeature,
} from 'src/app/map/map.utils';
import {
    loadRoomsForResults,
    loadRoomsForResultsMap,
    mapRoomToSelectOption,
    RoomStoreState,
} from '@mckinsey-converge/rooms';
import { LoadingModel } from '@mckinsey-converge/loading';
import { UserStoreState } from '@mckinsey-converge/user';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { trigger, state, style, transition, animate } from '@angular/animations';

export type MapSearchStoreState = BuildingStoreState &
    RoomStoreState &
    BookingStoreState &
    UserStoreState;

@Component({
    selector: 'app-map-search',
    template: `
        <div class="mck-column map-search-container">
            <div class="mck-content-expand-width map-filter">
                <app-map-filters
                    (selectedBuildingChange)="selectActiveBuilding($event)"
                    (selectedLevelChange)="selectBuildingLevel($event)"
                    (selectedSpaceChange)="selectActiveSpace($event)"
                    [selectedBuilding]="activeBuildingOption | async"
                    [selectedLevel]="activeLevelOption | async"
                    [selectedSpace]="activeSpaceOption | async"
                    [buildings]="bookingLocations | async"
                    [levels]="buildingLevelOptions | async"
                    [spaces]="spaceListOptions | async"
                    (searchClick)="focusOnRoom()"
                >
                </app-map-filters>
            </div>
            <div class="map-wrapper" [class.wrapper-height]="openHeader" [@expandHeight]="isOpen ? 'open' : 'closed'">
                <div class="mck-row mck-center skeleton" *ngIf="resultsLoading">
                    <ngx-skeleton-loader class="mck-skeleton-header"></ngx-skeleton-loader>
                </div>
                    <img
                        src="assets/icon/map-info.png"
                        (click)="displayOfficeInformation()"
                        class="map-info-button"
                    />
                    <app-map-controls
                    (toggleEvent)="propagateToggle()"
                        (zoomInClick)="zoomIn()"
                        (zoomOutClick)="zoomOut()"
                        (refreshClick)="resetZoomLevels()"
                    ></app-map-controls>
                    <a-map
                        [zoom]="zoom | async"
                        (zoomChange)="zoom.next($event)"
                        [center]="center | async"
                        (centerChange)="center.next($event)"
                        [src]="mapUrl | async"
                        [css]="style_map | async"
                        [listeners]="listeners | async"
                        [features]="features | async"
                        [focus]="(focusedMapSpace | async)?.id"
                    >
                    </a-map>
            </div>
        </div>
    `,
    styleUrls: ['./map-search.component.scss'],
    encapsulation: ViewEncapsulation.Emulated,
    animations: [
        trigger('expandHeight', [
            state(
                'open',
                style({
                    height: '470px',
                })
            ),
            state(
                'closed',
                style({
                    height: 'calc(100vh - 270px)',
                })
            ),
            transition('open <=> closed', animate('600ms ease-out')),
        ]),
    ],
})
export class MapSearchComponent extends BaseDirective implements OnInit {
    @Input()
    public openHeader: boolean = true;

    public zoom = new BehaviorSubject(MIN_ZOOM_LEVEL);

    public center = new BehaviorSubject<Point>(CENTER_POINT);
    public listeners = new BehaviorSubject<MapListener[]>([]);

    public focusedMapSpace = new BehaviorSubject<MapFeature | undefined>(undefined);
    public features = combineLatest([this.focusedMapSpace]).pipe(
        map((items) => items.filter((i) => !!i))
    );

    public focusedRoom = new BehaviorSubject<RoomResultsViewModel | undefined>(undefined);

    public resultBuildings = new BehaviorSubject<Building[]>([]);
    public resultSpaces = new BehaviorSubject<Space[]>([]);
    // public resultSpaces = new BehaviorSubject<LoadingModel<FilteredSpaces>>(
    //     LoadingModel.empty()
    // );

    public activeBuilding = new BehaviorSubject<Building | undefined>(undefined);
    public activeLevel = new BehaviorSubject<BuildingLevel | undefined>(undefined);
    public activeSpace = new BehaviorSubject<Space | undefined>(undefined);

    /** Mapping of elements to CSS styles */
    public style_map = new BehaviorSubject<HashMap<HashMap<string | number>>>({});

    public mapUrl = this.activeLevel.pipe(map((level) => level?.map_url ?? ''));

    public bookingLocations = new BehaviorSubject<SelectOption<any>[]>([]);
    public buildingLevelOptions = new BehaviorSubject<SelectOption<any>[]>([]);
    public spaceListOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    public activeBuildingOption = combineLatest([this.activeBuilding, this.bookingLocations]).pipe(
        map(([building, locations]) => locations.find((l) => l.value === building?.id))
    );

    public activeLevelOption = this.activeLevel.pipe(map(mapBuildingLevelToOption));
    public activeSpaceOption = this.activeSpace.pipe(map(mapRoomToSelectOption));
    public bookingFormData = new BehaviorSubject<BookingFormData | undefined>(undefined);
    public floorData: any;
    public floorDataParam: any;
    @Output() toggleEvent = new EventEmitter<void>();

    //to capture state for toggling expandHeight animation
    public isOpen = true;
    resultsLoading: any;

    propagateToggle() {
      this.toggleEvent.emit();
      this.isOpen = !this.isOpen;
    }
  

    constructor(
        private store: Store<MapSearchStoreState>,
        public dialog: MatDialog,
        private settingService: SettingsService,
        private router: Router,
        private location: Location,
        private route: ActivatedRoute
    ) {
        super();
        this.floorData = location.getState();
    }

    public ngOnInit(): void {
        // this.resultsLoading = this.resultSpaces.pipe(map((m) => m.isLoading));
        
        this.subscription(
            'bookingLocations',
            this.store.pipe(select(selectBookingLocations)).subscribe((next) => {
                this.bookingLocations.next(next);
                // update the map view MCK-1100 when location changes
                const self = this;
                setTimeout(() => {
                    self.selectActiveBuilding(self.bookingLocations.value[0]);
                }, 0);
            })
        );
        this.subscription(
            'bookingSpaceResults',
            combineLatest([
                this.store.pipe(
                    select(selectMapFilteredResults),
                    map((r: LoadingModel<FilteredSpaces>) => { 
                        this.resultsLoading = r.isLoading;
                       return r.success;})
                ),
                this.activeBuilding.pipe(filterUndefined<Building>()),
                this.activeLevel.pipe(filterUndefined<BuildingLevel>()),
            ]).subscribe(([filteredSpaces, building, level]) => {
                const spacesFromSelection = (filteredSpaces.spaces || []).filter(
                    (s) => !!s.map_id && s.zones.includes(building.id)
                );
                this.resultSpaces.next(spacesFromSelection);

                const listeners: MapListener[] = [];
                const styles: HashMap<HashMap<string | number>> = {};
                const colour_map = this.settingService.get('app.explore.colors') || {};
                spacesFromSelection.forEach((space) => {
                    const status = filteredSpaces.statusMap[space.id];
                    const mapStyle = generateMapStyleForSpace(space, status, colour_map);
                    styles[`#${mapStyle.id}`] = mapStyle.styles;
                    listeners.push(
                        ...generateMapListenersForSpace(
                            space,
                            () => {},
                            () => {},
                            status !== 'in-use' ? 
                                () => {
                                    this.activeSpace.next(space);
                                    this.focusOnRoom(status);
                                } 
                                : ()=> {}
                        )
                    );
                });
                this.style_map.next(styles);
                this.listeners.next(listeners);
            })
        );
        this.subscription(
            'bookingResults',
            this.resultSpaces
                .pipe(withLatestFrom(this.activeSpace))
                .subscribe(([resultSpaces, activeSpace]) => {
                    this.spaceListOptions.next(resultSpaces.map(mapRoomToSelectOption));
                })
        );

        this.subscription(
            'storeResultedBuildings',
            this.store.pipe(select(selectSelectedBuildingOptions)).subscribe((buildings) => {
                this.resultBuildings.next(buildings);
                // if we dont have active building yet, set it here.
                if (buildings.length > 0 && !this.activeBuilding.value) {
                    const firstBuilding = buildings[0];
                    this.activeBuilding.next(firstBuilding);
                }
            })
        );

        this.subscription(
            'activeBuildingChange',
            this.activeBuilding
                .pipe(
                    filterUndefined<Building>(),
                    map((b: Building) => b.levels)
                )
                .subscribe((levels) => {
                    this.buildingLevelOptions.next(levels.map(mapBuildingLevelToOption));
                    
                    if(this.activeLevel.value){
                        this.floorDataParam = {
                            display: this.activeLevel.value.name,
                            value: this.activeLevel.value.id,
                        };
                        this.selectBuildingLevel(this.floorDataParam);
                        setTimeout(() => {
                            this.refreshFloorNumber();
                        });
                    }
                    
                })
        );

        // if levels change and active level no longer part of list, pick first.
        this.subscription(
            'updateActiveLevelOnActiveBuildingChange',
            this.activeBuilding
                .pipe(
                    filterUndefined<Building>(),
                    distinctUntilChanged(),
                    withLatestFrom(this.activeLevel),
                    map(
                        ([building, activeLevel]) =>
                            (activeLevel && building.levels.find((l) => l.id === activeLevel.id)) ||
                            building.levels[0]
                    )
                )
                .subscribe((level) => {
                    this.activeLevel.next(level);
                })
        );

        // re-request rooms every minute to update space status.
        this.subscription(
            'updateSpaceStatusInterval',
            interval(60 * 1000)
                .pipe(withLatestFrom(this.store.select(selectActiveForm)))
                .subscribe(([, form]) => {
                    this.store.dispatch(loadRoomsForResultsMap.request(roomFormDataToQuery(form) ));
                })
        );

        // get the active form values
        this.subscription(
            'activeForm',
            this.store
                .pipe(select(selectActiveForm), distinctUntilChanged())
                .subscribe((next: BookingFormData | undefined) => {
                    if (next) {
                        this.bookingFormData.next(next);
                    }
                })
        );

        if (this.floorData?.payload) {
            this.floorDataParam = {
                display: this.floorData?.payload.name,
                value: this.floorData?.payload.id,
            };
            this.selectBuildingLevel(this.floorDataParam);
            setTimeout(() => {
                this.refreshFloorNumber();
            });
        }
    }

    public refreshFloorNumber(): void {
        this.activeLevelOption = this.activeLevel.pipe(map(mapBuildingLevelToOption));
    }

    public zoomIn() {
        if (this.zoom.value < MAX_ZOOM_LEVEL) {
            this.zoom.next(this.zoom.value + 1);
        }
    }

    public zoomOut() {
        if (this.zoom.value > MIN_ZOOM_LEVEL) {
            this.zoom.next(this.zoom.value - 1);
        }
    }

    public displayOfficeInformation() {
        this.dialog.open(OfficeInformationModalComponent, {
            data: this.activeBuilding.value,
            panelClass: 'mck-alert',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    public resetZoomLevels() {
        this.zoom.next(MIN_ZOOM_LEVEL);
        this.center.next(CENTER_POINT);
        this.focusedRoom.next(undefined);
        // this.focusedMapSpace.next(undefined);
    }

    public closeMapOverlayOutlet = () => this.focusedMapSpace.next(undefined);
    

    public selectActiveBuilding(selection: SelectOption<any>) {
        this.resultBuildings
            .pipe(
                map((all) => all.find((b) => b.id === selection.value)),
                take(1),
                filterUndefined<Building>()
            )
            .subscribe((next) => {
                this.activeBuilding.next(next);
            });
    }

    public selectBuildingLevel(selection: SelectOption<any>) {
        this.activeBuilding
            .pipe(
                map((b) => b.levels?.find((l) => l.id === selection?.value)),
                take(1),
                filterUndefined<BuildingLevel>()
            )
            .subscribe((next) => {
				this.store.dispatch(loadRoomsForResultsMap.request(roomFormDataToQuery(this.bookingFormData.value) ));
                this.activeLevel.next(next);

                /**
                 * update mapUrl as per active level on change of filter search
                 */
                this.mapUrl = this.activeLevel.pipe(map((level) => level?.map_url ?? ''))
            });
    }

    public selectActiveSpace(selection: SelectOption<any>) {
        this.resultSpaces
            .pipe(
                map((all) => all.find((s) => s.id === selection?.value)),
                take(1),
                filterUndefined<Space>()
            )
            .subscribe((next) => {
                this.activeSpace.next(next);
            });
    }

    public focusOnRoom(status?:string) {
        this.activeSpace.pipe(take(1), filterUndefined<Space>()).subscribe((next) => {
            let delay = 0;
            if (next.level.id !== this.activeLevel.value.id) {
                this.activeLevel.next(next.level);
                delay = 500;
            }
            this.timeout(
                'updateLevelAndRoom',
                () => {
                    const focusedRoom = mapResultsToViewModels(
                        [this.activeSpace.value],
                        [this.activeLevel.value],
                        [this.activeBuilding.value]
                    );
                    this.focusedRoom.next(focusedRoom[0]);
                    this.focusedMapSpace.next(roomToMapFeature(this.focusedRoom.value, this, status));
                    this.zoom.next(MAX_ZOOM_LEVEL);
                },
                delay
            );
        });
    }

    public roomSelected(room: RoomResultsViewModel) {
        // submit
        this.store.dispatch(
            storeBookingFormData({
                payload: {
                    ...this.bookingFormData.value,
                    selectedRoom: room,
                },
            })
        );

        this.router.navigate(['room'], { relativeTo: this.route });
    }
}
