import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ViewEncapsulation
} from '@angular/core';
import { SelectOption } from '@mckinsey-converge/ui';
import { FormControl } from '@angular/forms';
import { BaseDirective } from '@mckinsey-converge/base';
import { legendOptions } from '../map.utils';

@Component({
    selector: 'app-map-filters',
    template: `
        <div class="mck-column mck-content-expand-width map-filters-container">
            <div class="mck-row-flow search-input-container">
                <mck-autocomplete-select-option
                [selectedValue]="selectedSpace"
                [resetInput]="resetInput"
                noEntriesFoundLabel="There are no rooms for your current search."
                placeholder="Type to search (level, number or name) ..."
                [options]="spaces"
                [formControl]="spaceControl"
                (inputTextChange)="inputTextChange($event)"
                ></mck-autocomplete-select-option>
                <button
                    *ngIf="searchStringPresent"
                    (click)="clearRoomSearch()"
                    class="close-button mck-short mck-short-height mck-form-input">
                        <div class="close-button-icon"></div>
                </button>
                <button
                    (click)="searchClick.emit()"
                    class="search-button mck-short mck-short-height mck-form-input">
                        <div class="search-button-icon"></div>
                </button>
            </div>
            <div class="mck-row-flow controls-background office-level-row ">
                <mck-select
                    [showDivider]="true"
                    [selectedValue]="selectedBuilding"
                    [formControl]="buildingControl"
                    class="office-selector"
                    [options]="buildings"
                    [disabled]="buildings?.length <= 1"></mck-select>
                <mck-select
                    [showDivider]="true"
                    [selectedValue]="selectedLevel"
                    [formControl]="levelControl"
                    class="level-selector"
                    [options]="levels"
                    [disabled]="levels?.length <= 1"></mck-select>
                <mck-legend-dropdown
                    placeholder="Show legend"
                    placeholderOpen="Hide legend"
                    [options]="legendOptions"
                >
            </mck-legend-dropdown>
            </div>
        </div>
    `,
    styleUrls: ['./map-filters.component.scss'],
    encapsulation: ViewEncapsulation.Emulated
})
export class MapFiltersComponent extends BaseDirective implements OnInit {

    public legendOptions = legendOptions();

    @Input()
    public buildings: SelectOption<any>[];

    @Input()
    public selectedBuilding: SelectOption<any>;

    @Output()
    public selectedBuildingChange = new EventEmitter<SelectOption<any>>();

    @Input()
    public levels: SelectOption<any>[];

    @Input()
    public selectedLevel: SelectOption<any>;

    @Output()
    public selectedLevelChange = new EventEmitter<SelectOption<any>>();

    @Input()
    public spaces: SelectOption<any>[];

    @Output()
    public selectedSpaceChange = new EventEmitter<SelectOption<any>>();

    @Input()
    public selectedSpace: SelectOption<any>;

    @Output()
    public searchClick = new EventEmitter();

    public buildingControl = new FormControl();

    public levelControl = new FormControl();

    public spaceControl = new FormControl();

    public resetInput: number;

    public searchStringPresent = false;

    constructor() {
        super();
    }

    public ngOnInit(): void {
        this.subscription('buildingChanges',
            this.buildingControl.valueChanges.subscribe(value => this.selectedBuildingChange.emit(value)));
        this.subscription('levelChanges',
            this.levelControl.valueChanges.subscribe(value => this.selectedLevelChange.emit(value)));
        this.subscription('spaceChanges',
            this.spaceControl.valueChanges.subscribe(value => {
                this.selectedSpaceChange.emit(value);
                this.searchClick.emit();
            }));
    }

    public clearRoomSearch() {
        this.resetInput = Date.now();
        this.selectedSpace = null;
        this.searchStringPresent = false;
        // TODO, reset the focused room if required?
    }

    public inputTextChange(event: any) {
        this.searchStringPresent = !!event.length;
    }
}
