import { Injectable } from '@angular/core';
import {
  Actions,
  Effect
} from '@ngrx/effects';
import { createLoadingEffect } from '@mckinsey-converge/loading';
import { loadOrganisations } from './organisation.actions';
import { OrganisationService } from '@mckinsey-converge/data-common';


@Injectable()
export class OrganisationEffects {

  constructor(private actions: Actions,
              private organisationService: OrganisationService) {
  }

  @Effect()
  public loadOrganisations = createLoadingEffect(this.actions, loadOrganisations,
    () => this.organisationService.loadOrganisation())
}
