    /**
     * This function sets up start and end epoch times for a booking scheduling system.
     * It calculates and assigns various epoch times based on the current time,
     * available until time, and user-selected room's timezone.
     */
    public updateSchedulingEpochs() {

			const now: any = DateTime.now();

			// Calculate the time difference in minutes between current and available until time
			const {currentStatus, activeBookings, timeRemainingInActiveBooking, timeUntilNextBooking} = this.calculateRoomBookingTimeInfo();
			// this.diff = Math.abs(Math.round((this.availableUntil - now.ts) / 60000)); // recheck
			this.diff = timeUntilNextBooking;

			// Determine system availability based on time difference
			this.isAvailable = timeRemainingInActiveBooking < 15 &&  timeUntilNextBooking >= 15 // this.diff >= 15;

			// Calculate the next time slot for scheduling
			const addMin = now.get('minute') % WAITING_TIME === 0 ? 1 : 0;
			const adjustedTime = now.plus({ minutes: addMin, seconds: 0 }).setZone(this.userSelectedRoom?.building?.timezone);

			// Calculate and assign the start and end epoch times with 15 and 30-minute intervals
			const timeSlot = closestToTimeSlot(adjustedTime, WAITING_TIME);
			this.start_epoch = timeSlot.toLocaleString(DateTime.TIME_SIMPLE);
			this.end_epoch_15 = timeSlot.plus({ minutes: 15 }).toLocaleString(DateTime.TIME_SIMPLE);
			this.end_epoch_30 = timeSlot.plus({ minutes: 30 }).toLocaleString(DateTime.TIME_SIMPLE);
    }

