import {
    Component,
    Inject,
    ViewEncapsulation
} from '@angular/core';
import { HashMap } from '@mckinsey-converge/base';
import { 
    BookingRuleDetails,
    Building,
} from '@mckinsey-converge/data-common';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';
import {
    defaultMapBookingRules,
    defaultMapBookingSubtitle
} from 'src/app/map/map.utils';

@Component({
    selector: 'app-office-information-modal',
    template: `
        <mat-dialog-content>
            <div class="mck-column modal-heading">
                <div class="header">Booking in {{building?.name}}</div>
                <div class="info">{{targetLinks(info)}}</div>
            </div>
            <div class="mck-column modal-body">
                <ul class="modal-body-list">
                    <li
                        class="modal-body-content"
                        *ngFor="let item of rules" [innerHTML]="targetLinks(item)"></li>
                </ul>
                <div>
                    <div *ngFor="let group of other_rules">
                        <span class="modal-body-header">{{group.title}}</span>
                        <ul class="modal-body-list">
                            <li
                                class="modal-body-content"
                                *ngFor="let item of group.rules" [innerHTML]="targetLinks(item)"></li>
                        </ul>
                    </div>
                </div>
                <span class="modal-body-header">When booking from maps:</span>
                <ul class="modal-body-list">
                    <li
                        class="modal-body-content"
                        *ngFor="let item of map_rules" [innerHTML]="item"></li>
                </ul>
                <span class="modal-body-header">
                    <p *ngIf="contact">
                        For more info email: <a target="mail" [href]="'mailto:' + contact">{{contact}}</a>
                    </p>
                    <p *ngIf="customHtml" [innerHTML]="targetLinks(customHtml)"></p>
                </span>
            </div>
            <div class="mck-column modal-body-footer">
                <mck-modal-button-row primaryLabel="Ok" (primarySubmit)="closeModal()"></mck-modal-button-row>
            </div>
        </mat-dialog-content>
    `,
    styleUrls: ['./office-information-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class OfficeInformationModalComponent {
    constructor(@Inject(MAT_DIALOG_DATA) public building: Building,
                private dialogRef: MatDialogRef<OfficeInformationModalComponent>) {
    }


    /** Booking rule details */
    public get rules(): string[] {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.rules || [] as any;
    }

    /** Booking rule details */
    public get info(): string {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.info || defaultMapBookingSubtitle;
    }

    public get customHtml(): string | undefined {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.custom_html;
    }

    /** Booking map rule details */
    public get map_rules(): string[] {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.map_rules ||  defaultMapBookingRules as any;
    }

    /** Other special booking rules */
    public get other_rules(): { title: string, rules: string[] }[] {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        const rules: HashMap = (details as any)?.other_rules || {};
        return Object.keys(rules).reduce((a, v) => {
            
            a.push({ title: v, rules: rules[v] || [] });
            return a;
        }, []);
    }

    public get contact(): string {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.contact || '';
    }

    public get link(): { url?: string, name?: string } {
        const building: Building = this.building;
        const details: BookingRuleDetails = building?.booking_details;
        return details?.link || {};
    }

    public closeModal() {
        this.dialogRef.close();
    }

    public targetLinks(info: string) {
        const regex = /<a /ig;
        return info.replace(regex, '<a target="_blank" ');
    }
}
