import {
    Component,
    EventEmitter,
    OnInit,
    Output
} from '@angular/core';
import {
    instantBookOptions,
    roomDetailsRequired,
    roomEquipmentRequiredOptions,
    selectFormFilters,
    selectIsInternal,
    roomCateringAvailable,
    OrganisationService
} from '@mckinsey-converge/data-common';
import {
    select,
    Store
} from '@ngrx/store';
import { BaseDirective } from '@mckinsey-converge/base';
import { BehaviorSubject } from 'rxjs';
import { SelectOption } from '@mckinsey-converge/ui';
import {
    map,
    take
} from 'rxjs/operators';

@Component({
    selector: 'app-room-filters-form',
    template: `
        <div class="mck-column filters-container">
            <mck-switch-group
                title="Instant Book"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                [options]="instantBookOptions">
            </mck-switch-group>
            <mck-switch-group
                title="Room Equipment"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                [options]="roomEquipmentRequiredOptions">
            </mck-switch-group>
            <mck-switch-group
                *ngIf="isInternal"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                title="Internal Room Type"
                [options]="internalRoomTypeRequiredOptions">
            </mck-switch-group>
            <mck-switch-group
                *ngIf="!isInternal"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                title="External Room Type"
                [options]="externalRoomTypeRequiredOptions">
            </mck-switch-group>
            <mck-switch-group
                title="Room Details Required"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                [options]="roomDetailsRequired">
            </mck-switch-group>
            <mck-switch-group
                title="Catering Available"
                (optionChecked)="toggleOptionChecked($event)"
                [selectedOptions]="selectedOptions | async"
                [options]="roomCateringAvailable">
            </mck-switch-group>
            <mck-spacer size="48px" spacerClass="mck-spacer-content-expand-mobile"></mck-spacer>
            <!-- Inline button-row may be injected into this ng-content tag. -->
            <ng-content></ng-content>
        </div>
    `,
    styleUrls: ['./room-filters-form.component.scss']
})
export class RoomFiltersFormComponent extends BaseDirective implements OnInit {

    public instantBookOptions = instantBookOptions;
    public roomEquipmentRequiredOptions = roomEquipmentRequiredOptions;
    public roomDetailsRequired = roomDetailsRequired;
    public roomCateringAvailable = roomCateringAvailable;
    public internalRoomTypeRequiredOptions = [];
    public externalRoomTypeRequiredOptions = [];
    public isInternal = false;

    public selectedOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    @Output()
    public cancelSubmit = new EventEmitter();

    @Output()
    public filtersSubmit = new EventEmitter<SelectOption<any>[]>();

    constructor(private store: Store, private _org: OrganisationService) {
        super();
    }

    public ngOnInit(): void {
        this.subscription('bookingType',
            this.store.pipe(
                select(selectIsInternal)
            ).subscribe(next => {
                this.isInternal = next;
            }));
        this.subscription('filters',
            this.store.pipe(
                select(selectFormFilters)
            ).subscribe(next => {
                this.selectedOptions.next(next);
            }));

        this.externalRoomTypeRequiredOptions = this.getFilters('external_room_types')
        this.internalRoomTypeRequiredOptions = this.getFilters('internal_room_types')
    }

    public toggleOptionChecked(selectOption: SelectOption<any>) {
        this.selectedOptions.pipe(
            take(1),
            map(options => {
                const found = options.find(o => o.value === selectOption.value);
                if (found) {
                    // remove
                    return options.filter(o => o.value !== selectOption.value);
                }
                // add
                return [...options, selectOption];
            })
        ).subscribe(next => {
            this.selectedOptions.next(next);
        });
    }

    public submitFilters() {
        this.selectedOptions.pipe(
            take(1)
        ).subscribe(options => {
            this.filtersSubmit.emit(options);
        });
    }

    public getFilters(type:string) {
        const filters = this._org.getOrganizationFiltersByRoomType(type) || {}
        return Object.keys(filters).map((key) => {
            const display = filters[key]
            return {
                value: key,
                display,
                shortDisplay: display
            }
        })
    }
}
