import {
    Component,
    EventEmitter,
    Input,
    Output,
    OnInit
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
    Booking,
    Building,
    CateringOrder,
    Space,
    bookingStatusDetails,
    ApplicationService,
    CateringCategory,
    BookingsService,
    DataForConfirmation,
    getLastDateFromList
} from '@mckinsey-converge/data-common';
import {
    DialogEvent,
    HashMap,
    shorterLocalDateFormatString
} from '@mckinsey-converge/base';
import {
    BaseDirective,
    dateBuildingFormatString,
    startEndTimeWithZoneFormatString,
    humaniseDuration,
    toTitleCase
} from '@mckinsey-converge/base';
import { TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import {
    CateringDetailsModalComponent,
    CateringDetailsModalData
} from '@mckinsey-converge/legacy-overlays';
import { MatDialog } from '@angular/material/dialog';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

export interface BookingFlowEvent {
    /** Type of flow event */
    type: 'previous' | 'next' | 'reset' | 'close' | 'error';
    /** Step which the event was emitted from */
    step: string;
    /** Additional metadata for the event */
    metadata?: HashMap;
}

@Component({
    selector: 'booking-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss']
})
export class BookingCateringComponent extends BaseDirective implements OnInit  {
    /** Form fields for the booking */
    @Input() public form: FormGroup;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<FormGroup>();
    @Input() public booking: Booking;
    @Input() public isOccurence: boolean;
    /** Emitter for open catering */
    @Output() public showCatering = new EventEmitter<void>();
    dataForConfirmation: DataForConfirmation;
    isEditSeries: boolean = false;

    constructor(
        private _dialog: MatDialog,
        public _service: ApplicationService,
        public _bookings: BookingsService,
    ) {
        super();
    }

    public ngOnInit(): void {
     this.updateConfimatinModal();
    }

    /** Active order to display details for */
    public active_order: CateringOrder;
    public TopBarHeaderMode = TopBarHeaderMode;
    public confirmed: boolean = false;
    public next_disabled: boolean = true;

    public loading: boolean = false;


    public get building(): Building[] {
        return this.booking.room.building;
    };

    /** Field for storing catering details */
    public get catering_field() {
        return this.form && this.form.controls.catering ? this.form.controls.catering : null;
    }

    /** List of spaces selected for the booking */
    public get space_list(): Space[] {
        return this.form && this.form?.controls?.space_list?.value.length
            ? this.form.controls.space_list.value
            : [];
    }

    /** Date of the booking */
    public get dateTz(): number {
        return this.form && this.form.controls.dateTz
            ? this.form.controls.dateTz.value
            : DateNow(new Date()).startOfValue('minute');
    }

    /** Duration of the booking */
    public get duration(): number {
        return this.form && this.form.controls.duration ? this.form.controls.duration.value : 60;
    }

    /** Whether booking is all day */
    public get all_day(): boolean {
        return this.form && this.form.controls.all_day && this.form.controls.all_day.value;
    }


    public get type() {
        return toTitleCase(this.booking?.class);
    }

    public get dateString() {
        return dateBuildingFormatString(this.booking.startDateTz);
    }

    public get timeString() {
        return startEndTimeWithZoneFormatString(this.booking.startDateTz, this.booking?.displayEndDateTz);
    }

    public get durationString() {
        return humaniseDuration(this.booking?.duration);
    }

    public get status() {
        return bookingStatusDetails(this.booking);
    }

    /** Create a new catering order */
    public newOrder(): void {
        const order = new CateringOrder({ booking_timezone: this.booking?.timezone });
        this.active_order = order;
    }

    // Combine the same items for the same timeslot into the same order item
    public combineItems = (newItems: CateringCategory[], oldItems: CateringCategory[]) => {
        // Group and Sum the items in an Item ID keyed object
        const itemsObj = {};
        oldItems.forEach((i) => {
            if (itemsObj[i.id]) {
                itemsObj[i.id].setAmount(itemsObj[i.id].amount + i.amount);
            } else {
                itemsObj[i.id] = i;
            }
        });
        newItems.forEach((i) => {
            if (itemsObj[i.id]) {
                itemsObj[i.id].setAmount(itemsObj[i.id].amount + i.amount);
            } else {
                itemsObj[i.id] = i;
            }
        })
        // Convert the object to an array
        const items = [];
        for (const item in itemsObj) {
            items.push(itemsObj[item]);
        }
        // return the items consolidated by item ID.
        return items;
    }

    /** Handle changes to catering order */
    public handleOrderEvent(order: CateringOrder) {
        /* istanbul ignore else */
        if (order) {
            this.next_disabled = false;
            const orders = this.form.controls.catering.value;
            // When the order edit button is clicked we are editing an existing order
            const index = orders.findIndex((an_order) => {
                return order.id === an_order.id
            });

            // If editing an existing order row it will have an index, if not create a new row
            if (index < 0) {
                // Add the new order
                orders.push(order);
            } else {
                // Replace the existing order with the edited order
                orders.splice(index, 1, order);
            }

            // Setup the "reduced" data containers
            const reduceItems = {};
            const reduceOrders = {};
            orders.forEach((o) => {
                if (reduceItems[o.delivery_time]) {
                    // An order for this timeslot already exists, combine the same items to dedupe rows
                    reduceItems[o.delivery_time] = this.combineItems(o.items, reduceItems[o.delivery_time]);
                } else {
                    // Since this is a new timeslot, use the existing items
                    reduceItems[o.delivery_time] = o.items;
                    // Copy the order and clear its item contents, placeholder for the updated items
                    reduceOrders[o.delivery_time] = o;
                    reduceOrders[o.delivery_time].items = [];
                }
            });

            // Add the reduced items to the reduced orders
            let consolidatedOrders = [];
            for (const deliveryTime in reduceOrders) {
                const order = reduceOrders[deliveryTime];
                order.items = reduceItems[deliveryTime]
                consolidatedOrders.push(order);
            }

            this.form.controls.catering.setValue(consolidatedOrders);
        }
        this.active_order = null;
    }

    public closeCallback() {
        // Bail and close the catering component
        this.next_disabled = true;
        this.active_order = null;

        // Need to clone to avoid readonly errors
        this.form.controls.catering.setValue(this.booking.catering.map(order => order.clone()));
        this.event.emit();
    }

    public closeConfirmation() {
        this.event.emit();
    }

    /** Open modal for adding equipment requirements to booking */
    private openCateringDetailsModal() {
        const ref = this._dialog.open<CateringDetailsModalComponent, CateringDetailsModalData>(
            CateringDetailsModalComponent,
            {
                maxWidth: '95vw',
                width: '32em',
                data: {
                    spaces: this.form.controls.space_list.value,
                    notes_field: this.form.controls.notes,
                    catering: this.form.controls.catering,
                    booking: this.booking
                }
            }
        );
        this.subscription(
            'modal_events',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                // Catering requirements and charge code modal closed event
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    // Closed with the reason done
                    ref.close();

                    // Display the loading animation and the Booking Confrimation component
                    this.loading = true;
                    this.confirmed = true;

                    this.saveBooking();
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('modal_events'));
    }

    public saveBooking() {
        // Clone and append the catering and note data to the booking
        let booking = this.booking.clone();
        booking.catering = this.form.controls.catering.value;
        booking.notes = this.form.controls.notes.value;
        if(booking.notes.length || booking.catering.length) {

            const setup = { ...booking.setup };
            setup[booking.space.email] =  booking.setup[booking.space.email] || 900;
            booking.setup = setup;

            const breakdown = { ...booking.breakdown };
            breakdown[booking.space.email] = booking.breakdown[booking.space.email] || 900;
            booking.breakdown = breakdown;
        }


        // Save to the API and respond. && !this.isOccurence
        this._bookings.save(booking, booking.is_recurrent ).then(
            (bkn) => {
                this.booking = new Booking({ ...this.booking, ...bkn.toApiJSON() }); 
                // Stop the loading animation and show the booking confirmation component.
                this.updateConfimatinModal();

                this.loading = false;
                this.confirmed = true;
                this.event.emit(this.form)
            },
            (err) => {
                // Stop the loading animation and return to the catering flow with an error.
                this.loading = false;
                this.confirmed = false;
                this._service.notifyError(
                    `Error updating booking.`
                );
            }
        )
    }

    public handleOrderListEvent($event: any) {
        const orders = this.form.controls.catering.value;
        // Handle the Previous and Next buttons
        if ($event.type === 'previous') {
            // Bail and close the catering component
            this.next_disabled = true;
            this.active_order = null;
            // Need to clone to avoid readonly errors
            this.form.controls.catering.setValue(this.booking.catering.map(order => order.clone()));
            this.event.emit();
        }
        if ($event.type === 'next') {
            // Move on to the catering details modal
            this.next_disabled = false;
            if (orders.length) {
                this.openCateringDetailsModal();
            } else {
                // Display the loading animation and the Booking Confrimation component
                this.loading = true;
                this.confirmed = true;
                this.saveBooking();
            }
        }
    }

    public openCatering() {
        // Start catering from the Booking Confirmation page Add Catering button
        this.confirmed = false;
        this.next_disabled = true;
        this.showCatering.emit();
    }

    /**
     * Public method to update the confirmation modal data.
     * This method updates the dataForConfirmation object with the necessary information
     * for displaying the confirmation modal in the application.
     */
    public updateConfimatinModal() {

        // Prepare an object with booking details and related information for the confirmation modal
        this.dataForConfirmation = {
        booking: this.booking,  // Information about the booking
        status: this.status,  // Status of the booking (e.g., confirmed, pending, etc.)
        dateString: this.dateString,  // Date of the booking in string format
        endDateString: this.endDateString,   // End date of the booking in string format
        timeString: this.timeString,  // Time of the booking in string format
        durationString: this.durationString,  // Duration of the booking in string format
        type: this.type,  // Type of booking (e.g., internal, external, etc.)
        isEditSeries: this.isEditSeries,  // Boolean indicating whether it's an edit in a series of events
        // Function to check if there are conflicts with occurrences of the booking
        isConflictWithOccurences: this.isConflictWithOccurences.bind(this),
        // Function to open a modal in case of conflicts with recurring events
        // openRecurrenceConflictsModal: this.openRecurrenceConflictsModal.bind(this)
        }
    }

     /**
     * Checks if there are conflicts with the current booking and its occurrences.
     * Calculates the difference between total occurrences and available occurrences
     * to determine if there are conflicts.
     *
     * @returns {Object} An object containing conflict information:
     *   - is_conflict: A boolean indicating if there are conflicts (true if conflicts exist, false otherwise).
     *   - total_Conflicts: The total number of conflicts (the difference between total occurrences and available occurrences).
     */
    // public isConflictWithOccurences() {
    //     const conflicts = this?.booking?.room?.totalOccurrences - this?.booking?.room?.availableOccurrences;
    //     return { is_conflict: !!conflicts, total_Conflicts: conflicts };
    // }

    public isConflictWithOccurences() {
        let totalConflicts = 0;
    
        this.booking.multi_rooms.forEach(room => {
            totalConflicts += Math.max(room.totalOccurrences - room.availableOccurrences, 0);
        });
    

        const result = {
            is_conflict: totalConflicts > 0, 
            total_Conflicts: totalConflicts 
        };
        return result;
    }

    /**
 * Retrieves the end date string for the booking, considering recurrence if applicable.
 * If the booking is recurrent, it calculates the end date based on recurrence starts;
 * otherwise, it uses the booking's start date as the end date.
 *
 * @returns {string} The formatted end date string in a shorter local date format.
 */
  public get endDateString(): string {
    // Determine the array of date-time strings based on recurrence or single start date
    let recurrenceStart: Array<DateTZ>= [];
    const { is_recurrent, recurrence_starts, occurrence_details, timezone} = this.booking;
    if(is_recurrent && recurrence_starts.length) {
      recurrenceStart = this.booking.recurrence_starts;
    }
    else if(is_recurrent && !recurrence_starts.length && occurrence_details.length) {
      this.booking.occurrence_details.forEach(element => {
        recurrenceStart.push(new DateTZ({ date: element.start_epoch * 1000, is_local_tz: false, building_tz: timezone}));
      });
      recurrenceStart = recurrenceStart.sort((d1: DateTZ, d2: DateTZ) => d1.ms - d2.ms);
    }
    else {
      recurrenceStart = [this.booking.startDateTz];
    }


    // const endDtz = (this.booking.is_recurrent && this.booking.recurrence_starts.length) ? this.booking.recurrence_starts : [this.booking.startDateTz]
    // Get the last date from the list of date-time strings and format it in a shorter local date format
    return shorterLocalDateFormatString(getLastDateFromList(recurrenceStart));
  }

}
