import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
    OnInit
} from '@angular/core';
import { throttleTime, tap } from 'rxjs/operators';
import { BehaviorSubject, fromEvent } from 'rxjs';
import { ImageDirective } from '@mckinsey-converge/base';
import { setupRoomImages } from '@mckinsey-converge/data-common';
import { SpaceImageObject } from 'libs/base/src/lib/types.utilities'

@Component({
    selector: 'mck-responsive-inline-images',
    template: `
        <div class="">
            <div *ngIf="mobile">
                <carousel
                    class="carousel"
                    [cellWidth]="'100%'"
                    [height]="225"
                    [arrowsOutside]="false"
                    [autoplay]="true"
                    [dots]="true"
                    [loop]="false"
                >
                <div class="carousel-cell"
                    *ngFor="let img of foundImages"
                    [ngStyle]="{'background-image': 'url(' + img.path +'?'+ timestamp + ')'}">
                    <span *ngIf="showLabel(img.path)">Images Coming Soon</span>
                </div>
            </carousel>
            </div>
            <div *ngIf="!mobile" class="desktop-room-images">
                <div class="desktop-cell"
                    *ngFor="let img of foundImages"
                    [ngStyle]="{
                        'background-image': showLabel(img.path) ? 'linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 22%, rgba(0,0,0,0) 100%), url(' + img.path + ')' : 'url(' + img.path +'?'+ timestamp + ')'  
                    }">
                    <span *ngIf="showLabel(img.path)">Images Coming Soon</span>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./responsive-inline-images.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ResponsiveInlineImagesComponent extends ImageDirective implements OnInit {

    /** Input array of images */
    @Input() public rootFolderURL: string;
    @Input() public fileSlug: string;

    /** Ignore this param, it's only use is to bypass the image onload event in testing */
    @Input() public imagesLoaded: boolean;
    public loadImages = new BehaviorSubject<SpaceImageObject[]>([]);

    public foundImages: SpaceImageObject[] = [1,2,3].map(n => {
        return {path: `assets/img/rooms/placeholder-${n}.png`};
    });

    public mobile = false;

    constructor(private cdRef: ChangeDetectorRef,) {
        super();
    }

    public ngOnInit(): void {
        const breakpoint = 768;

        if (window) {
            this.mobile = window.innerWidth < breakpoint;
        }

        fromEvent(window, 'resize').pipe(throttleTime(500)).pipe(tap({
            next: val => {
                const target = val.target as Window;
                this.mobile = target.innerWidth < breakpoint;
            }
        })).subscribe();
        setupRoomImages(this, [1,2,3], this.rootFolderURL, this.fileSlug, this.imagesLoaded);
        this.subscription(
						'found_images',
						this.loadImages.subscribe((imgArr: SpaceImageObject[]) => {
							if(imgArr.length) this.foundImages =  imgArr;
							this.cdRef.detectChanges();
						}));
    }

    public showLabel(path) {
        return path.includes('placeholder');
    } 

    public get timestamp () {
        return (new Date()).getTime()
    }
}
