import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RoomBookingComponent } from 'src/app/booking/room-booking/room-booking.component';
import { RoomResultsComponent } from 'src/app/booking/room-results/room-results.component';
import { RoomDetailsComponent } from 'src/app/booking/room-details/room-details.component';
import { BookingCreateComponent } from './booking-create/booking-create.component';
import { BookingEditWrapperComponent } from './booking-create/booking-edit-wrapper.component';
import { RoomFiltersModalComponent } from 'src/app/booking/room-filters-modal/room-filters-modal.component';
import { ManageBookingsComponent } from './manage-bookings/manage-bookings.component';
import { OfficeFloorComponent } from './office-floor/office-floor/office-floor.component';

const routes: Routes = [
    {
        path: 'results',
        component: RoomResultsComponent,
    },
    {
        path: 'results/filter',
        component: RoomFiltersModalComponent,
    },
    {
        path: 'results/room',
        component: RoomDetailsComponent,
    },
    {
        path: 'create',
        component: BookingCreateComponent,
    },
    {
        path: 'edit',
        component: BookingEditWrapperComponent,
    },
    {
        path: 'manage',
        component: ManageBookingsComponent,
    },
    {
        path: 'catering/:id',
        data: { hasCatering: true },
        component: ManageBookingsComponent,
    },
    {
        path: 'booking/:id/:verb',
        component: ManageBookingsComponent,
    },
    {
        path: 'booking/:id',
        component: ManageBookingsComponent,
    },
    {
        path: 'floor-map',
        component: OfficeFloorComponent,
    },
    {
        path: 'results/booking-calendar',
        loadChildren: () => import('./booking-calendar/booking-calendar.module').then((m) => m.BookingCalendarModule),
    },
    {
        path: '**',
        component: RoomBookingComponent,
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class BookingRoutingModule {}
