import { Component, OnInit } from '@angular/core';
import { TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import { select, Store } from '@ngrx/store';
import {
    laterThatDayFromDateTz,
    loadRoomsWithQuery,
    RoomStoreState,
    mapRoomToSelectOption,
    loadRoomsForResultsMap,
} from '@mckinsey-converge/rooms';
import { BaseDirective, timeWithZoneFormatString } from '@mckinsey-converge/base';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import {
    Building,
    BuildingLevel,
    flattenBuildingsWithLevels,
    BookingStoreState,
    FilteredSpaces,
    SpaceFilterReason,
    clearRoomFilters,
    selectActiveForm,
    selectFilteredLaterDayResults,
    selectFilteredNextDayResults,
    selectFilteredResults,
    selectFormFilters,
    BookingFormData,
    roomFormDataToQuery,
    selectFilteredDiffSizeResults,
    OrganisationService,
} from '@mckinsey-converge/data-common';
import { LoadingModel } from '@mckinsey-converge/loading';
import { debounceTime, distinctUntilChanged, filter, map, shareReplay } from 'rxjs/operators';
import {
    buildingStateSelector,
    BuildingStoreState,
    loadBuildings,
} from '@mckinsey-converge/buildings';
import * as cloneDeep from 'lodash/cloneDeep';
import { filterUndefined, resultCountObservable } from '@mckinsey-converge/rx-utils';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { RoomBookingModalComponent } from 'src/app/booking/room-booking-modal/room-booking-modal.component';
import { RoomFiltersModalComponent } from 'src/app/booking/room-filters-modal/room-filters-modal.component';
import { SelectOption } from '@mckinsey-converge/ui';
import { trigger, state, style, transition, animate } from '@angular/animations';
import {
    BookingRulesLink,
    RoomResultsCoachmarkViewmodel,
} from 'src/app/booking/room-results/room-results-coachmark.viewmodel';
import { UserStoreState } from '@mckinsey-converge/user';
import { FormControl } from '@angular/forms';
import { Location } from '@angular/common';

export type RoomResultsState = BookingStoreState &
    RoomStoreState &
    BuildingStoreState &
    UserStoreState;

@Component({
    selector: 'app-room-results',
    template: `
        <div
            class="mck-column mck-content-expand mck-content-page-container "
            *ngIf="!this?.payload?.floorMap"
        >
            <header class="header">
                <a-topbar-header [mode]="TopBarHeaderMode.Light"> </a-topbar-header>
            </header>
            <div class="mck-column mck-scroll mb-block">
                <div [@collapseHeight]="isOpen ? 'open' : 'closed'">
                    <div class="header-container mck-center">
                        <div class="mck-content-body hideable" [class.open]="open">
                            <app-searched-location
                                [filters]="filters | async"
                                [data]="data | async"
                                (changeClick)="changeSearchResults()"
                                (filterClick)="filterResults()"
                                [canFilter]="(selectedToggle | async) === 0"
                            ></app-searched-location>
                        </div>
                        <div class="tap" (click)="open = !open">
                            <div>
                                {{ open ? '(tap to close)' : '(tap to open)' }}
                                <img src="assets/icon/minimize-bar.svg" [class.open]="open" />
                            </div>
                        </div>
                    </div>
                    <div class="mck-center">
                        <div class="mck-content-body results-header">
                            <app-results-header
                                (selectedToggleChange)="selectedToggleChange($event)"
                                [resultsCount]="mainResultsCount | async"
                            >
                            </app-results-header>
                        </div>
                    </div>
                </div>
                <div
                    class="mck-column mck-center results-container mb-block"
                    *ngIf="(selectedToggle | async) === 0"
                >
                    <div class="mck-content-expand-width">
                        <div class="mck-column map-filters-container">
                            <div class="mck-row-flow search-input-container">
                                <mck-autocomplete-select-option
                                    [selectedValue]="selectedSpace"
                                    [resetInput]="resetInput"
                                    noEntriesFoundLabel="There are no rooms for your current search."
                                    placeholder="Type to search (level, number or name) ..."
                                    [options]="mainResultsSpaces"
                                    [formControl]="spaceControl"
                                    (inputTextChange)="inputTextChange($event)"
                                    (filteredOptionsChange)="filteredRoomsChange($event)"
                                    (emptyBoxEvent)="changeInput($event)"
                                ></mck-autocomplete-select-option>
                                <button
                                    *ngIf="searchStringPresent"
                                    (click)="clearRoomSearch()"
                                    class="close-button mck-short mck-short-height mck-form-input"
                                >
                                    <div class="close-button-icon"></div>
                                </button>
                                <button
                                    class="search-button mck-short mck-short-height mck-form-input"
                                >
                                    <div class="search-button-icon"></div>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div
                        class="mck-column results-container-list mck-content-expand-width mck-content-body"
                        #resultsContainer
                    >
                        <app-room-results-section
                            [results]="mainResultsFiltered"
                            [collapsable]="false"
                            [rules_link]="rules_link"
                            [levelBuildings]="flattenLevelBuildings"
                            (coachMarkAction)="onCoachmarkAction($event)"
                        >
                        </app-room-results-section>
                        <app-room-results-section
                            [results]="diffSizeResults"
                            [collapsable]="true"
                            [rules_link]="rules_link"
                            [levelBuildings]="flattenLevelBuildings"
                            collapseTitle="Available in a different room size ({{
                                diffSizeCount | async
                            }})"
                            (coachMarkAction)="onCoachmarkAction($event)"
                        >
                        </app-room-results-section>
                        <app-room-results-section
                            [results]="laterThatDayResults"
                            [collapsable]="true"
                            [rules_link]="rules_link"
                            [levelBuildings]="flattenLevelBuildings"
                            collapseTitle="Available a bit later that day ({{
                                laterThatDayCount | async
                            }})"
                            collapseDescription="{{ laterThatDayDisplay | async }}"
                            [offset]="1 * 60"
                            (coachMarkAction)="onCoachmarkAction($event)"
                        >
                        </app-room-results-section>
                        <app-room-results-section
                            [results]="nextBusinessDayResults"
                            [collapsable]="true"
                            [rules_link]="rules_link"
                            [levelBuildings]="flattenLevelBuildings"
                            collapseTitle="Available the next business day ({{
                                nextBusinessDayCount | async
                            }})"
                            [offset]="day_offset * 24 * 60"
                            (coachMarkAction)="onCoachmarkAction($event)"
                        >
                        </app-room-results-section>
                    </div>
                </div>
                <div
                    class="mck-column mck-center results-container"
                    *ngIf="(selectedToggle | async) === 1"
                >
                    <div
                        class="results-container-map-container mck-content-body mck-content-expand-height"
                        [@expandWidth]="isOpen ? 'open' : 'closed'"
                        >
                        <div
                            class="results-container-map mck-content-expand-width mck-content-body"
                            class="expand-width"
                        >
                            <app-map-search
                                [openHeader]="close"
                                (toggleEvent)="toggle()"
                            ></app-map-search>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./room-results.component.scss'],
    animations: [
        trigger('collapseHeight', [
            state(
                'open',
                style({
                    height: '*',
                    opacity: 1,
                })
            ),
            state(
                'closed',
                style({
                    overflow: 'hidden',
                    opacity: 0,
                    height: '0px',
                })
            ),
            transition('open <=> closed', animate('600ms ease-out')),
        ]),
        trigger('expandWidth', [
            state(
                'open',
                style({
                    maxWidth: '1024px',
                })
            ),
            state(
                'closed',
                style({
                    maxWidth: '96%',
                })
            ),
            transition('open <=> closed', animate('600ms ease-out')),
        ]),
    ],
})
export class RoomResultsComponent extends BaseDirective implements OnInit {
    public TopBarHeaderMode = TopBarHeaderMode;

    public day_offset = 1;

    public data = new BehaviorSubject<BookingFormData | undefined>(undefined);
    public filters = new BehaviorSubject<SelectOption<any>[]>([]);

    public mainResults = new BehaviorSubject<LoadingModel<FilteredSpaces>>(LoadingModel.empty());
    public mainResultsFiltered = new BehaviorSubject<LoadingModel<FilteredSpaces>>(
        LoadingModel.empty()
    );
    public laterThatDayResults = new BehaviorSubject<LoadingModel<FilteredSpaces>>(
        LoadingModel.empty()
    );
    public nextBusinessDayResults = new BehaviorSubject<LoadingModel<FilteredSpaces>>(
        LoadingModel.empty()
    );
    public diffSizeResults = new BehaviorSubject<LoadingModel<FilteredSpaces>>(
        LoadingModel.empty()
    );
    public mainResultsSpaces: SelectOption<any>[];

    public laterThatDayDisplay = new BehaviorSubject<string | undefined>(undefined);

    public mainCoachMarkDisplay = this.mainResults.pipe(
        map((r) => r.optionalSuccess?.reason || SpaceFilterReason.None),
        map((r) => new RoomResultsCoachmarkViewmodel(r, this.rules_link))
    );

    public rules_link?: BookingRulesLink;

    public mainResultsCount = resultCountObservable(
        this.mainResultsFiltered,
        this.mainResultsFiltered.pipe(map((r) => r.optionalSuccess?.spaces))
    );
    public laterThatDayCount = resultCountObservable(
        this.laterThatDayResults,
        this.laterThatDayResults.pipe(map((r) => r.optionalSuccess?.spaces))
    );
    public nextBusinessDayCount = resultCountObservable(
        this.nextBusinessDayResults,
        this.nextBusinessDayResults.pipe(map((r) => r.optionalSuccess?.spaces))
    );
    public diffSizeCount = resultCountObservable(
        this.diffSizeResults,
        this.diffSizeResults.pipe(map((r) => r.optionalSuccess?.spaces))
    );

    public selectedToggle = new BehaviorSubject<number>(0);

    public flattenLevelBuildings: Observable<{
        flattenedLevels: BuildingLevel[];
        buildings: Building[];
    }>;

    public open: boolean = true;
    public editing_form = new BehaviorSubject(false);

    public spaceControl = new FormControl();

    public selectedSpace: SelectOption<any>;
    public resetInput: number;

    public searchStringPresent = false;
    public payload: any = {
        payload: { name: 'test', id: 'test-id' },
        building: {},
        floorMap: true,
    };

    public isOpen = true;

    public toggle() {
        this.isOpen = !this.isOpen;
    }

    constructor(
        private store: Store<RoomResultsState>,
        private router: Router,
        private route: ActivatedRoute,
        private dialog: MatDialog,
        private _org: OrganisationService,
        private location: Location
    ) {
        super();
        this.payload = location.getState();
    }

    public ngOnInit(): void {
        this.subscription(
            'filters',
            this.store.pipe(select(selectFormFilters), distinctUntilChanged()).subscribe((next) => {
                return this.filters.next(next);
            })
        );

        this.subscription(
            'activeForm',
            combineLatest([this.editing_form, this.store])
                .pipe(
                    filter(([edit]) => !edit),
                    map(([_, store]) => store),
                    select(selectActiveForm),
                    distinctUntilChanged()
                )
                .subscribe((next: BookingFormData | undefined) => {
                    if (next) {
                        const day = next.dateTz.dateWeekday - 1;
                        this.day_offset = day >= 4 ? 7 - day : 1;
                        const building = this._org.buildings.find(
                            (_) => _.id === next.location[0]?.meta?.building?.id
                        );
                        this.rules_link =
                        building?.setting('booking.rules_link') ||
                        next.location.find(
                            office => office?.meta?.building?._settings?.discovery_info?.booking?.rules_link
                        )?.meta?.building?._settings?.discovery_info?.booking?.rules_link;
                        // next.location[0]?.meta?.building?.settings?.discovery_info?.booking
                        //     ?.rules_link;
                        const nday = laterThatDayFromDateTz(next.dateTz);
                        this.laterThatDayDisplay.next(`at ${timeWithZoneFormatString(nday)}`);
                        // this.store.dispatch(loadRoomsWithQuery(roomFormDataToQuery(next)));
                        this.data.next(next);
                      
                        //Dispatches actions to load rooms based on the provided value.
                        this.despatchLoadRooms();

                        // this.data.next(next);
                    } else {
                        //  no form data, likely visiting directly. navigate back to start
                        this.router.navigate(['../'], {
                            replaceUrl: true,
                            relativeTo: this.route,
                        });
                    }
                })
        );

        this.subscription(
            'results',
            this.store.pipe(select(selectFilteredResults)).subscribe((next) => {
                this.mainResults.next(next);
                this.mainResultsFiltered.next(next);
                // Populate results for Search
                this.mainResultsSpaces = next.success?.spaces
                    ? next.success?.spaces?.map(mapRoomToSelectOption)
                    : [];
            })
        );

        this.subscription(
            'laterResults',
            this.store.pipe(select(selectFilteredLaterDayResults)).subscribe((next) => {
                this.laterThatDayResults.next(next);
            })
        );

        this.subscription(
            'nextDayResults',
            this.store.pipe(select(selectFilteredNextDayResults)).subscribe((next) => {
                this.nextBusinessDayResults.next(next);
            })
        );

        this.subscription(
            'diffSize',
            this.store.pipe(select(selectFilteredDiffSizeResults)).subscribe((next) => {
                this.diffSizeResults.next(next);
            })
        );

        this.flattenLevelBuildings = this.store.pipe(
            select(buildingStateSelector),
            select(loadBuildings.selectors.optionalSuccess),
            filterUndefined(),
            map(flattenBuildingsWithLevels),
            shareReplay(1)
        );

        this.subscription(
            'spaceChanges',
            this.spaceControl.valueChanges.subscribe((value) => {
                const mainResultsFiltered = cloneDeep(this.mainResults.value);
                const filteredRooms = this.mainResults.value.success?.spaces?.filter((s) => {
                    if (s.id === value.value) {
                        return true;
                    }
                    return false;
                });
                mainResultsFiltered.success.spaces = filteredRooms;
                this.mainResultsFiltered.next(mainResultsFiltered);
            })
        );

        // if coming from kiosk map url, route there
        if (this.payload?.floorMap) {
            this.router.navigate([`../home/floor-map`], {
                state: { ...this.payload },
                queryParams: {
                    display: '',
                },
            });
        }

        this.subscription(
            'spaceChanges',
            this.selectedToggle.subscribe((value) => {
                //Dispatches actions to load rooms based on the provided value.
                this.despatchLoadRooms();
            })
        );
    }

    /**
     * Dispatches actions to load rooms based on the provided value.
     * @param {number} value - The value indicating the mode of operation.
     *                         0: Load rooms for regular view.
     *                         1: Load rooms for results map view.
     */
    public despatchLoadRooms() {
        const selectedMenuIndex = this.selectedToggle.value;  // List/Map/Calendar
        const isForFloorMap = this.payload?.floorMap;
         // If value is 0 and floorMap is not present in payload
        if(selectedMenuIndex === 0 && !isForFloorMap) {
            // Dispatch action to load rooms with query generated from form data
            this.store.dispatch(loadRoomsWithQuery(roomFormDataToQuery(this.data.value)));
        }
        
        // If value is 1 or floorMap is present in payload
        if(selectedMenuIndex === 1 || isForFloorMap) {
            // Dispatch action to load rooms for results map view with query generated from form data
            this.store.dispatch( loadRoomsForResultsMap.request(roomFormDataToQuery(this.data.value)));
        }
    }

    public clearRoomSearch() {
        this.resetInput = Date.now();
        this.selectedSpace = null;
        const mainResults = cloneDeep(this.mainResults.value);
        this.mainResultsFiltered.next(mainResults);
        this.searchStringPresent = false;
    }

    public inputTextChange(event: any) {
        this.searchStringPresent = !!event.length;
    }

    public changeInput(event) {
        if (event) {
            this.clearRoomSearch();
        }
    }

    public filteredRoomsChange(event: SelectOption<any>[]) {
        const roomIds = event.map((o) => o.value);
        const mainResultsFiltered = cloneDeep(this.mainResults.value);
        const filteredRooms = this.mainResults?.value?.success?.spaces
            ? this.mainResults?.value?.success?.spaces?.filter((s) => {
                  if (roomIds.includes(s.id)) {
                      return true;
                  }
                  return false;
              })
            : [];
        mainResultsFiltered.success.spaces = filteredRooms;
        this.mainResultsFiltered.next(mainResultsFiltered);
    }

    public changeSearchResults() {
        this.editing_form.next(true);
        const dialog_ref = this.dialog.open(RoomBookingModalComponent, {
            panelClass: 'mck-modal',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        dialog_ref.afterClosed().subscribe(() => this.editing_form.next(false));
    }

    public filterResults() {
        this.dialog.open(RoomFiltersModalComponent, {
            panelClass: 'mck-modal',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    public onCoachmarkAction(reason: SpaceFilterReason) {
        switch (reason) {
            case SpaceFilterReason.Filters:
                this.store.dispatch(clearRoomFilters());
                break;
            case SpaceFilterReason.OfficeRules:
            case SpaceFilterReason.DateTime:
                this.changeSearchResults();
                break;
        }
    }

    public selectedToggleChange(event: SelectOption<any>) {
        this.selectedToggle.next(event.value);
    }
}
