import {
    Component,
    OnInit,
} from '@angular/core';
import {
    select,
} from '@ngrx/store';
import {
    Booking,
    BookingFormData,
    selectActiveForm,
    checkCollisions,
    getStatusErrorMessage,
    clearBookingFormData,
    BOOKING_STATUS,
		Space,
		getLastDateFromList
} from '@mckinsey-converge/data-common';
import {
    distinctUntilChanged,
} from 'rxjs/operators';
import { BookingFormBaseComponent } from './booking-form-base.component';
import { ConflictsModalComponent, ConflictsModalComponentData } from '@mckinsey-converge/ui';
import { MatDialog } from '@angular/material/dialog';
import { DialogEvent, shorterLocalDateFormatString } from '@mckinsey-converge/base';

@Component({
    selector: 'app-booking-create',
    templateUrl: './booking-form-base.component.html',
    styleUrls: ['./booking-create.component.scss']
})
export class BookingCreateComponent extends BookingFormBaseComponent implements OnInit {


    public ngOnInit(): void {
        super.ngOnInit();
        this.subscription('activeForm', this.store.pipe(
            select(selectActiveForm),
            distinctUntilChanged()
        ).subscribe((next: BookingFormData | undefined) => {
            if (next && next?.dateTz && next?.selectedRoom?.space) {
                this.data.next(next);
                this.booking = this.bookingFormToBooking(next);
            }
        }));

				// in the parent component
				this.updateConfimatinModal();
    }


    /**
     * Save booking changes
     */
    public saveChanges() {
        this.loading = true;
        this.booking = this.bookingFormToBooking(this.data.value);
        // const booking_form_data = this.data.value;
        this.booking.status = BOOKING_STATUS.approved;
        checkCollisions(
            this._spaces,
            this.booking
        ).then((bookable: boolean) => {
            if (!bookable) {
                // Refactored for
                //	https://acaprojects.atlassian.net/browse/MCK-1498
                //	https://mckinsey.atlassian.net/browse/CNG-97
                // API is retuning bookings ouside of requested time range
                // TODO check booking date and times for conflict manually
                this.booking.status = BOOKING_STATUS.unavailable;
                return;
            }

            // this.booking['opt_out'] = false;
            this._bookings_service.save(this.booking).then(
                (bkn) => {
									// update template properties and booking
									this.handleBookingSuccessResponse(bkn);
                },
                (err) => {
                    if (err.status === 409) {
                      this.handleBookingErrorMessage();
                    } else {
											this.loading = false;
                    	this.confirmed = false;
                      this.displayWarningBanner(getStatusErrorMessage(err.status));
                    }
                }
            );
        },(err) => {
                this.timeout('error', () => {
									this.handleBookingErrorMessage();
                }, 500);
            }
        );
    }

}
