import {
    createReducer,
    on
} from '@ngrx/store';
import { 
    BookingFormState,
    clearBookingFormData,
    clearRoomFilters,
    openBookingSurvey,
    storeBookingFormData,
    storeRoomFilters
} from '@mckinsey-converge/data-common';
import { closestToTimeSlotTz } from '@mckinsey-converge/base';
import { DateTZ } from '@mckinsey-converge/date-tz';

export const initialBookingFormState: BookingFormState = {
    activeFormFilters: []
};

export const bookingFormReducer = createReducer(initialBookingFormState,
    on(storeBookingFormData, (state: BookingFormState, action) => {
        return {
            ...state,
            activeForm: {
                ...state.activeForm,
                ...action.payload
            }
        } as BookingFormState;
    }),
    on(clearBookingFormData, state => ({
        ...state,
        activeForm: 
        {
            location: state?.activeForm?.location,
            timezone: state?.activeForm?.timezone,
            dateTz: closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false,  building_tz: state?.activeForm?.timezone, }), 15)
            
        }
    }) as BookingFormState),
    on(openBookingSurvey, state => ({
        ...state,
        bookingCompleted: new Date()
    }) as BookingFormState),
    on(storeRoomFilters, (state: BookingFormState, action) => {
        return {
            ...state,
            activeFormFilters: action.payload
        } as BookingFormState;
    }),
    on(clearRoomFilters, (state: BookingFormState) => ({
        ...state,
        activeFormFilters: initialBookingFormState.activeFormFilters
    }) as BookingFormState)
);
