import {
    Component,
    Inject,
    OnInit,
    ViewEncapsulation
} from '@angular/core';
import {
    BaseDirective,
  } from '@mckinsey-converge/base';
import {
    Booking,
    BookingsService,
    DEFAULT_BOOKING_DURATION,
} from '@mckinsey-converge/data-common';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';
import {
    FormControl,
    FormGroup,
    Validators
} from '@angular/forms';
import * as cloneDeep from 'lodash/cloneDeep';

interface extendData {
    booking: Booking;
    maximum: number
}

@Component({
    selector: 'app-booking-duration-modal',
    template: `
        <mat-dialog-content class="booking-duration-content">
            <div class="fixed warning banner" *ngIf="showWarningBanner">
                <img src="assets/icon/booking_cancelled.svg"/>
                <p class="banner-text">Your booking failed to update, please try again</p>
                <img src="assets/icon/close-red.svg" (click)="hideWarningBanner()"/>
            </div>
            <div class="mck-column modal-heading">
                <button name="CloseButton" class="close-button" mat-icon-button (click)="closeModal()">
                    <div class="close-image"></div>
                </button>
                <div class="info">Extend booking duration</div>
            </div>
            <div class="mck-column modal-body">
                <mck-duration-field
                    class="time-inputs"
                    label="End time"
                    name="end-time"
                    [startDate]="formGroup.controls.dateTz.value.ms"
                    [required]="true"
                    [ngModel]="formGroup.controls.duration.value"
                    [ngModelOptions]="{ standalone: true }"
                    (ngModelChange)="formGroup.controls.duration.setValue($event)"
                    [extended]="true"
                    [durationGroups]="durationGroups"
                    [timezone]="data.booking?.timezone"
                >
                </mck-duration-field>
            </div>
            <div class="mck-column modal-body-footer">
                <mck-modal-button-row
                    [disableFixed]="true"
                    primaryLabel="Extend"
                    (primarySubmit)="updateBookingDuration()"
                    secondaryLabel="Back"
                    (secondarySubmit)="closeModal()"
                ></mck-modal-button-row>
            </div>
        </mat-dialog-content>
    `,
    styleUrls: ['./booking-duration-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class BookingDurationModalComponent extends BaseDirective implements OnInit {

    public loading: string;
    public showWarningBanner: boolean = false;

    public get durationGroups() {
        const step = 15;
        const steps = Math.floor(this.data.maximum / step);
        const max = (steps * step) + this.data.booking.duration;

        return [{
            start: this.data.booking.duration,
            step,
            max
        }];
    }

    public formGroup = new FormGroup({
        date: new FormControl(this.data.booking.date, [Validators.required]),
        dateTz: new FormControl(this.data.booking.startDateTz, [Validators.required]),
        duration: new FormControl(DEFAULT_BOOKING_DURATION, [Validators.required]),
    });

    constructor(
            @Inject(MAT_DIALOG_DATA) public data: extendData,
            private dialogRef: MatDialogRef<BookingDurationModalComponent>,
            public _bookings: BookingsService,
        ) {
        super();
    }

    ngOnInit() {
        this.formGroup.setValue({
            date: this.data.booking.date,
            dateTz: this.data.booking.startDateTz,
            duration: this.data.booking.duration,
        });
    }

    public closeModal(booking?: Booking) {
        this.dialogRef.close(booking);
    }

    public updateBookingDuration() {
        const formData = this.formGroup.getRawValue();
        this.loading = 'Saving...';

        // clone booking to allow editing
        // const booking = this.data.booking.clone(); // this line commented for bug appearing in CNG-352. Used cloneDeep instead
        const booking = cloneDeep(this.data?.booking);
        booking.endDateTz = booking.startDateTz.addValue({ minutes: formData.duration });
        this._bookings.save(booking).then(
            () => {
                this.loading = null;
                this.closeModal(booking);
            },
            (err) => {
                this.loading = null;
                this.displayWarningBanner()
            }
        );
    }


    public displayWarningBanner() {
        this.showWarningBanner = true;
        this.timeout('close banner', () => {
            this.showWarningBanner = false;
        }, 10000);
    }

    public hideWarningBanner() {
        this.showWarningBanner= false;
    }
}
