import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { MAT_LABEL_GLOBAL_OPTIONS } from '@angular/material/core';

import { ComposerModule } from '@placeos/composer';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { environment } from '../environments/environment';
import { SharedContentModule } from './shared/shared.module';

import { ShellWrapperComponent } from './shell/shell.component';
import {
    ActionReducer,
    MetaReducer,
    StoreModule
} from '@ngrx/store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { BookingModule } from 'src/app/booking/booking.module';
import { BookingsModule } from '@mckinsey-converge/bookings';
import { BuildingsModule } from '@mckinsey-converge/buildings';
import { EffectsModule } from '@ngrx/effects';
import { DEFAULT_SETTINGS } from 'src/environments/settings';
import { provideMockSettingsObject } from '@mckinsey-converge/data-common';
import { OrganisationModule } from '@mckinsey-converge/organisation';
import { localStorageSync } from 'ngrx-store-localstorage';
import { UiModule } from '@mckinsey-converge/ui';
import { RoomsModule } from '@mckinsey-converge/rooms';
import { ENVIRONMENT_TOKEN } from '@mckinsey-converge/environment';
import { LegacyOverlaysModule } from '@mckinsey-converge/legacy-overlays';
import { UserModule } from '@mckinsey-converge/user';
import { MapModule } from 'src/app/map/map.module';

/**
 * Cache some state in local storage. Not for use in production.
 */
export function localStorageSyncReducer(reducer: ActionReducer<any>): ActionReducer<any> {
    // Keys needed to fix prod reloading.  MCK-1229
    let keys = [
        'buildings',
        'bookingForm',
        'rooms',
    ];

    if (!environment.production) {
        keys = [
            'bookings',
            'buildings',
            'organisation',
            'bookingForm',
            'rooms',
            'user'
        ];
    }

    return localStorageSync({
        keys,
        rehydrate: true,
    })(reducer);
}

const metaReducers: Array<MetaReducer<any, any>> = [localStorageSyncReducer];


@NgModule({
    declarations: [
        AppComponent,
        ShellWrapperComponent
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        AppRoutingModule,
        HttpClientModule,
        FormsModule,
        ComposerModule,
        SharedContentModule,
        LegacyOverlaysModule,
        StoreModule.forRoot({}, { metaReducers }),
        StoreDevtoolsModule.instrument({
            maxAge: 100,
            logOnly: environment.production
        }),
        EffectsModule.forRoot(),
        BookingsModule,
        BookingModule,
        BuildingsModule,
        MapModule,
        OrganisationModule,
        RoomsModule,
        UiModule,
        UserModule
    ],
    providers: [
        { provide: MAT_LABEL_GLOBAL_OPTIONS, useValue: { float: 'nevet' } },
        provideMockSettingsObject(DEFAULT_SETTINGS),
        { provide: ENVIRONMENT_TOKEN, useValue: environment }
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}
