import { createReducer } from '@ngrx/store';
import { LoadingModel } from '@mckinsey-converge/loading';
import { loadBuildings } from './buildings.actions';
import { BuildingState } from './buildings.types';

export const initialBuildingState: BuildingState = {
    data: LoadingModel.empty()
};

export const buildingsReducer = createReducer(initialBuildingState,
    ...loadBuildings.ons
);
