import {
    Component,
    ViewChild
} from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { SelectOption } from '@mckinsey-converge/ui';
import { Store } from '@ngrx/store';
import { storeRoomFilters } from '@mckinsey-converge/data-common';
import { RoomFiltersFormComponent } from 'src/app/booking/room-filters-form/room-filters-form.component';

@Component({
    selector: 'app-room-filters',
    template: `
        <div>
        <mck-modal header="Filter the Results" class="filters-modal-container">
            <app-room-filters-form
                #roomFiltersForm
                (cancelSubmit)="close()"
                (filtersSubmit)="submitFilters($event)">
            </app-room-filters-form>
            <mat-dialog-actions class="filters-modal-actions">
                <mck-modal-button-row
                    (primarySubmit)="roomFiltersForm.submitFilters()"
                    (secondarySubmit)="roomFiltersForm.cancelSubmit.emit($event)"
                    primaryLabel="Filter"
                    secondaryLabel="Cancel">
                </mck-modal-button-row>
            </mat-dialog-actions>
        </mck-modal>
        </div>
    `,
    styleUrls: ['./room-filters-modal.component.scss']
})
export class RoomFiltersModalComponent {

    @ViewChild(RoomFiltersFormComponent)
    public form: RoomFiltersFormComponent;

    constructor(
        private store: Store,
        private dialogRef: MatDialogRef<RoomFiltersModalComponent>) {
    }

    public close() {
        this.dialogRef.close();
    }

    public submitFilters(filters: SelectOption<any>[]) {
        this.store.dispatch(storeRoomFilters({
            payload: filters
        }));
        this.close();
    }
}
