import { Injectable } from '@angular/core';
import {
  Actions,
  Effect,
  ofType
} from '@ngrx/effects';
import {
  Organisation,
  OrganisationService
} from '../../../data-common/src/lib/organisation';
import { loadBuildings } from './buildings.actions';
import {
  createLoadingEffect,
  LoadingAction,
  Payload
} from '@mckinsey-converge/loading';
import { loadOrganisations } from '../../../organisation/src/lib/organisation.actions';
import { map } from 'rxjs/operators';


@Injectable()
export class BuildingsEffects {

  constructor(private actions: Actions,
              private organizationService: OrganisationService) {

  }

  @Effect()
  public loadBuildings = createLoadingEffect(this.actions, loadBuildings,
    (action) => this.organizationService.loadBuildingsWithOrg(action));

  @Effect()
  public loadBuildingsAfterOrg = this.actions.pipe(
    ofType(loadOrganisations.success),
    map((action: LoadingAction<Payload<Organisation>>) => loadBuildings.request(action.payload)));
}
