import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ViewEncapsulation,
} from '@angular/core';
import {
    distinctUntilChanged,
    first,
} from 'rxjs/operators';
import {
    ApplicationIcon,
    BookingFormState,
    SettingsService,
    openBookingSurvey,
    clearBookingFormData,
    BookingFormData,
    SeriesAction,
    selectActiveForm,
    OrganisationService
} from '@mckinsey-converge/data-common';
import { loadRoomsForResults } from '@mckinsey-converge/rooms';

import { loadOrganisations } from '@mckinsey-converge/organisation';
import { BuildingStoreState } from '@mckinsey-converge/buildings';
import { BaseDirective } from '@mckinsey-converge/base';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import {
    NavigationEnd,
    Router,
    ActivatedRoute
} from '@angular/router';
import {
    generateFeedbackUrl,
    Urls
} from '../../utilities/urls';
import { SurveyModalComponent } from '../survey-modal/survey-modal.component';
import {
    Store, select
} from '@ngrx/store';

export enum TopBarHeaderMode {
    Light = 'light',
    Dark = 'dark',
}

export interface RoutePayload {
    /** IE ['/home', 'results'] */
    route: string[];
    /** IE { display: '/list' } */
    queryParams?: object;
}

export type RoomBookingStoreState = BookingFormState & BuildingStoreState;

@Component({
    selector: 'a-topbar-header',
    template: `
        <div [class]="'topbar-header ' + page + ' ' + mode">
            <div class="block home" *ngIf="!back && !modal && !backModal">
                <button mat-icon-button name="home-button" class="home-button"
                        [attr.aria-label]="'Home'"
                        (click)="goToHome()">
                    <img class="icon" src="{{mode == 'dark' ? 'assets/img/home.svg' : 'assets/img/home-light.svg'}}" />
                </button>
            </div>
            <div class="block company-logo-container" *ngIf="!back && !modal && !backModal">
                <div class="logo" *ngIf="logo">
                    <i class="logo-icon" *ngIf="logo.type === 'icon'" [class]="logo.class"
                       aria-label="McKinsey & Company">{{ logo.content }}</i>
                    <img class="logo-icon" *ngIf="logo.type === 'img'" [src]="logo.src | safe: 'resource'"
                         aria-label="McKinsey & Company"
                    />
                </div>
            </div>
            <div class="block back-container" *ngIf="back">
                <button mat-icon-button name="home-button" class="home-button"
                    [attr.aria-label]="'Home'"
                    (click)="goToBackRoute()">
                    <img class="icon" src="{{mode == 'dark' ? 'assets/icon/back-arrow.svg' : 'assets/icon/back-arrow-light.svg'}}" />
                </button>
            </div>
            <div class="block back-container" *ngIf="backModal">
                <button mat-icon-button name="home-button" class="home-button"
                    [attr.aria-label]="'Home'"
                    (click)="closeCallback.emit(true)">
                    <img class="icon" src="{{mode == 'dark' ? 'assets/icon/back-arrow.svg' : 'assets/icon/back-arrow-light.svg'}}" />
                </button>
            </div>
            <div class="block back-container" *ngIf="modal">
                <button mat-icon-button name="close-button" class="close-button"
                    [attr.aria-label]="'Close'"
                    (click)="closeCallback.emit(true)">
                    <img class="icon" src="{{mode == 'dark' ? 'assets/icon/close.svg' : 'assets/icon/close.svg'}}" />
                </button>
            </div>
            <div class="block actions-container">
                <button mat-icon-button name="feedback-button" class="feedback-button"
                    matTooltip="Feedback"
                    [attr.aria-label]="'Feedback'"
                    (click)="goToFeedback()"
                >
                    <img class="icon" src="assets/img/chat-{{mode}}.svg" alt="Feedback Icon" />
                    <span>Feedback</span>
                </button>
                <button mat-icon-button name="help-button" class="help-button"
                        matTooltip="Help"
                        [attr.aria-label]="'Help'"
                        (click)="goToHelp()"
                    >
                        <img class="icon" src="assets/img/question-{{mode}}.svg" alt="Help Icon" />
                        <span>Help</span>
                </button>
            </div>
        </div>
    `,
    styleUrls: ['./topbar-header.component.scss'],
    // encapsulation: ViewEncapsulation.None,
})
export class TopbarHeaderComponent extends BaseDirective implements OnInit {
    /** Logo of the application organisation */
    public logo: ApplicationIcon;
    /** Whether to show menu */
    @Input() public menu: boolean;
    /** Whether to show modal close icon */
    @Input() public modal: boolean = false;
    /** Whether t show modal back icon */
    @Input() public backModal: boolean = false;
    /** Whether to disable the survey triggers */
    @Input() public noSurvey: boolean = false;
    /** Name of the page being rendered */
    @Input() public page: string;
    /** The theme to use **/
    @Input() public mode: TopBarHeaderMode = TopBarHeaderMode.Dark;
    /** Override the home route. **/
    @Input() public homeOverride: string | undefined;
    /** Show history back instead of logo block */
    @Input() public back: RoutePayload;
    /** Emitter for changes to menu state */
    @Output() public menuChange = new EventEmitter<boolean>();
    /** Emitter for the close button */
    @Output() public closeCallback = new EventEmitter<boolean>();
    /** Emitter for the back button */
    @Output() public onBack = new EventEmitter<void>();
    /** Name of the currently active page */
    public currentPage: string;
    /** Survey modal state */
    public modalOpen: boolean = false;
    isEditSeries: boolean = false;

    public get feedback() {
        const feedback_url = this._org.getOrganizationFiltersByRoomType('feedback_url');
        return `${feedback_url}${this.currentPage}`;
        // return generateFeedbackUrl(this.currentPage);
    }

    public surveyDialogRef: MatDialogRef<SurveyModalComponent, any> = null;

    public bookingCompleted: Date = new Date();
    public feedbackId: string = null;

    constructor(private store: Store<RoomBookingStoreState>,
        private _settings: SettingsService,
        private _dialog: MatDialog,
        private route: ActivatedRoute,
        private _org: OrganisationService,
        private _router: Router) {
        super();
    }

    public ngOnInit(): void {
        this.store.dispatch(loadOrganisations.request());
        this._settings.initialised.pipe(first(_ => _)).subscribe(() => {
            this.logo = this._settings.get(`app.logo_${this.mode}`) || { type: 'icon' };
        });
        this.timeout('Getting Title', () => this.updateRoute(document.title));
        this._router.events.subscribe((event: NavigationEnd) => {
            /* istanbul ignore else */
            if (event instanceof NavigationEnd) {
                this.timeout('Getting Title', () => this.updateRoute(document.title));
            }
        });

        // if the survey is allowed (don't in modals! due to duplicate appearances)
        if (!this.noSurvey) {
            // get the active form values
            this.subscription('openSurveyModal', this.store.pipe(
                distinctUntilChanged()
            ).subscribe((next: any | undefined) => {
                const feedbackId = next.organisation?.data?.success?._settings?.discovery_info?.feedback_id;
                if (feedbackId && this.feedbackId !== feedbackId) {
                    this.feedbackId = feedbackId;
                }
                if (next.bookingForm?.bookingCompleted && this.bookingCompleted.getTime() < next.bookingForm.bookingCompleted.getTime()) {
                    this.bookingCompleted = new Date(next.bookingForm.bookingCompleted.getTime());
                    this.openFeedbackModal();
                }
            }));
        }


        this.subscription('activeForm', this.store.pipe(
            select(selectActiveForm),
            distinctUntilChanged()
        ).subscribe((next: BookingFormData | undefined) => {
            if (next) {
                this.isEditSeries = next.action === SeriesAction.EDIT;
            }
        }));
    }

    public updateRoute(location) {
        location = location.substr(0, location.indexOf(' |'));
        this.currentPage = location;
    }

    /** Set menu state to `true` */
    public showMenu() {
        this.menu = true;
        this.menuChange.emit(this.menu);
    }

    public async goToHome() {
        this.isEditSeries && this.clearFormAndResultData();
        await this._router.navigate([this.homeOverride ?? '/']);
        // await this._router.navigate(['/', 'home'], { relativeTo: this.route });
        this._dialog.closeAll();
    }


	public clearFormAndResultData() {
		this.store.dispatch(clearBookingFormData());
		this.store.dispatch(loadRoomsForResults.clear());
	}

    public goToHelp() {
        const help_url = this._org.getOrganizationFiltersByRoomType('help_url');
        window.open(help_url || Urls.Help);
    }

    public goToFeedback() {
        // Over-engineered button action to mimic/test booking confirmation event.
        this.store.dispatch(openBookingSurvey());
    }

    public openFeedbackModal() {
        if (!this.modalOpen && !this.surveyDialogRef) {
            this.modalOpen = true;
            this.surveyDialogRef = this._dialog.open(SurveyModalComponent, {
                disableClose: true,
                panelClass: 'mck-modal-survey',
                data: this.feedbackId,
                autoFocus: false,
                backdropClass: 'mck-modal-backdrop',
            });
            this.surveyDialogRef.afterClosed().subscribe(() => {
                this.modalOpen = false;
                this.surveyDialogRef = null;
            });
        }

    }

    public async goToBackRoute() {
        this.onBack.emit();
        await this._router.navigate(this.back.route, { queryParams: this.back.queryParams });
    }

}
