import { Injectable } from '@angular/core';
import {
    Actions,
    Effect,
    ofType
} from '@ngrx/effects';
import { SpacesService } from '@mckinsey-converge/data-common';
import {
    createLoadingEffect,
    LoadingAction
} from '@mckinsey-converge/loading';
import {
    loadLaterThatDayResults,
    loadNextDayResults,
    loadRoomsForResults,
    loadRoomsForResultsMap,
    loadRoomsWithQuery,
    loadByIdResults,
    loadDiffSizeResults
} from './rooms.actions';
import { mergeMap } from 'rxjs/operators';
import {
    laterThatDayFromDateTz,
    nextBusinessDay,
} from './rooms.utils';
import {
    RoomQueryOptions,
    roomQueryOptionsToSpaceAvailabilityOptions,
    roomByIdQueryOptionsToSpaceAvailabilityOptions
} from './rooms.types';

@Injectable()
export class RoomsEffects {
    constructor(private actions: Actions,
                private spaceService: SpacesService) {
    }

    @Effect()
    public afterRequestResultsLoadOthersEffect = this.actions.pipe(
        ofType(loadRoomsWithQuery),
        mergeMap((action: LoadingAction<RoomQueryOptions>) => {
            return [
                loadRoomsForResults.request({
                    ...action,
                    dateTz: action.dateTz
                }),
                // loadRoomsForResultsMap.request({
                //     ...action,
                //     dateTz: action.dateTz
                // }),
                loadLaterThatDayResults.request({
                    ...action,
                    dateTz: laterThatDayFromDateTz(action.dateTz)
                }),
                loadNextDayResults.request({
                    ...action,
                    dateTz: nextBusinessDay(action.dateTz)
                }),
                loadDiffSizeResults.request({
                    ...action,
                    dateTz: action.dateTz,
                    diff_capacity: true
                }),
            ];
        })
    );

    @Effect()
    public loadRoomsForResultsEffect = createLoadingEffect(this.actions, loadRoomsForResults,
        (action) => this.spaceService.available(roomQueryOptionsToSpaceAvailabilityOptions(action), 'rooms'));
   
    @Effect()        
    public loadRoomsForResultsMapEffect = createLoadingEffect(this.actions, loadRoomsForResultsMap,
        (action) => this.spaceService.available(roomQueryOptionsToSpaceAvailabilityOptions(action), 'rooms for maps', false));

    @Effect()
    public loadLaterThatDayResultsEffect = createLoadingEffect(this.actions, loadLaterThatDayResults,
        (action) =>
            this.spaceService.available(roomQueryOptionsToSpaceAvailabilityOptions(action), 'laterResults'));

    @Effect()
    public loadNextDayResultsEffect = createLoadingEffect(this.actions, loadNextDayResults,
        (action) =>
            this.spaceService.available(roomQueryOptionsToSpaceAvailabilityOptions(action), 'nextDay'));

    @Effect()
    public loadDiffSizeResultsEffect = createLoadingEffect(this.actions, loadDiffSizeResults,
        (action) =>
            this.spaceService.available(roomQueryOptionsToSpaceAvailabilityOptions(action), 'diffSize'));

    @Effect()
    public loadRoomByIdResultsEffect = createLoadingEffect(this.actions, loadByIdResults,
        (action) => 
            this.spaceService.available(roomByIdQueryOptionsToSpaceAvailabilityOptions(action), 'byId', false));

}
