	/**
	 * Check if there are any booking collisions with the new booking time
	 * @param room_id 'sys-' id of room in question
	 * @param date proposed booking time
	 * @param duration proposed booking length (in minutes)
	 */
	private async findMaxExtension(): Promise<number> {
		const setupInSeconds = this._data.booking.setup[this._data.booking.room.email] || 0;
		const breakdownInMinutes = this._data.booking.breakdown[this._data.booking.room.email] ? this._data.booking.breakdown[this._data.booking.room.email] / 60 : 0;

		// Add a seconds, since the end time ends in :59, and add the setup time
		const endTz = this._data.booking.endDateTz.addValue({ seconds: setupInSeconds + 1 });// (this._data.booking.date + this._data.booking.duration*60000)*0.001 -1
		const params = {
			room_ids: this._data.booking.room.id,
			dateTz: endTz, // (((end + 60 + setupSeconds) * 1000)),
			duration: 60 + breakdownInMinutes,
			hide_bookings: true,
		};

		return await this._spaces
			.available(params, null, false)
			.then(
				(list) => {
					// measure gap in booking instead and have options
					const gapInMinutes = ((list[0].available_until - (endTz.seconds - 60)) / 60);
					const value = Math.min(60, gapInMinutes);
					return value >= 15 ? value : 0;
				},
				() => {
					return 0;
				}
			);
	}

