	public getLocation(booking: Booking): any {

		if (this.buildings && booking.building_zone) {
			return this.buildings.find(b => booking.building_zone === b.value);
		}
		return [];
	}


	/**
 * Dispatches data for getting the current status of the booking floor.
 *
 * This function prepares and dispatches necessary data to retrieve the current status
 * of the booking floor.
 *
 * @param {Object} booking - The booking details object, containing room, date, duration, etc.
 * @param {boolean} editingSeries - A boolean indicating whether the booking is part of a series and being edited.

 */
	public dispatchToGetFloorCurrentStatus(booking, editingSeries: boolean = false) {
		const timezone = booking.timezone;
		const { capacity, internal_or_external } = booking.room;
		const roomOption = capacity < 3 ? 0 : (capacity > 2 && capacity < 10) ? 1 : 2;
		const roomSize = roomSizeOptions[roomOption];

		// Process recurrence data if the booking is part of a series being edited
		let recurringBookingData = {};
		let recurrenceStart = [];
		if(booking.is_recurrent && editingSeries) {

			this._data.master_booking.occurrence_details.forEach(element => {
				recurrenceStart.push(new DateTZ({ date: element.start_epoch * 1000, is_local_tz: false, building_tz: timezone}));
			});
			recurrenceStart = recurrenceStart.sort((d1: DateTZ, d2: DateTZ) => d1.ms - d2.ms);

			const {
				id,
				recurrence_period,
				recurrence_interval,
				recurrence_count,
				is_recurrent,
				recurrence_exceptions,
				recurrence_pattern,
				recurrence_days,
				recurrenEndFromOcurrences,
				occurrence_details,
				occurrence_edits,
				occurrence_ids,
				recurrence_type,
				recurrence_master_id,
				edits } = this._data.master_booking

			const rec_endTz = recurrenEndFromOcurrences.bind(this._data.master_booking);
			const recurrence_endTz = rec_endTz(occurrence_details)




			recurringBookingData = {
				id:id,
				recurrence_period: recurrence_period,
				recurrence_interval: recurrence_interval || 1,
				recurrence_endTz: recurrence_endTz, //this._data.master_booking.recurrence_endTz,
				recurrence_count: recurrence_count || 1,
				is_recurrent: is_recurrent,
				recurrence_starts: recurrenceStart,
				recurrence_exceptions: recurrence_exceptions || [],
				recurrence_pattern: recurrence_pattern || "weekly", //Helper value to store the last selected pattern (daily, weekly, monthly),
				recurrence_days: recurrence_days || [],

				occurrence_details: occurrence_details,
        occurrence_edits: occurrence_edits || null,
        occurrence_ids: occurrence_ids || null,
        recurrence_type: recurrence_type || null,
        recurrence_master_id: recurrence_master_id,
				edits: edits
			}
		};


		const formGroup: BookingFormData = {
			location: [this.getLocation(booking)],
			//date: booking.date,
			// dateTz: recurrenceStart[0], //building_tz: timezone
			dateTz: recurrenceStart.length >=1 ? recurrenceStart[0] : closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false }),	15	).toZone(timezone),
			duration: booking.duration,
			bookingType: internal_or_external === 'internal' ? bookingTypeOptions[0] : bookingTypeOptions[1],
			roomSize: roomSize,
			selectedRoom: null,
			timezone: timezone,
			// buildings: buildings,
			...this._data?.master_booking?.toJSON(),
			...recurringBookingData,
			opt_out:booking.opt_out,
			action: 'edit_booking'

		};

		// Clear existing form and result data, then dispatch the updated form data
		this.clearFormAndResultData();
		this.store.dispatch(storeBookingFormData({ payload: formGroup }));

		// Dispatch a request to load available rooms based on the updated form data
		this.store.dispatch(loadRoomsForResultsMap.request(roomFormDataToQuery(formGroup)))
	}

	/**
 * Opens a modal for editing a recurring series of bookings.
 */
	public editRecurringSeries() {
		// Dispatch the current status of the booking floor for editing the recurring series
		this.dispatchToGetFloorCurrentStatus(this._data.booking, true);

		const dialog_ref = this.dialog.open(RoomBookingModalComponent, {
				panelClass: 'mck-modal',
				autoFocus: false,
				backdropClass: 'mck-modal-backdrop',
				data: {
					navigateTo: this.navigateT0Search.bind(this),
					action: SeriesAction.EDIT, // Indicates the action type (editing a series)
				}
		});

		// Subscribe to the modal's closed event to handle the result
		dialog_ref.afterClosed().subscribe((flag) => {
			// Close the modal if the operation was successful (flag is truthy)
			flag && this.close(flag); //console.log('ROOM MODAL CLOSED')
		});

	}

