	public confirmChanges(data: DateDurationData) {
		// Confirm the changes returned from the room availability modal.
		this.mobile = data.mobile;
		this.setAlternativeWeekStartDate(data.dateTz);

		// New booking flow has this.data
		if (this.data.value) {
			this.fetchRooms(true, this.alternativeWeekStartDateTz);
			// submit
			this.store.dispatch(storeBookingFormData({
				payload: {
					...this.data.value,
					dateTz: data.dateTz,
					duration: data.duration,
				}
			}));
		} else if (data.save) {
			// update booking date/duration
			const booking = this._data.booking.clone();
			const pendingChanges = booking.duration !== data.duration || booking.startDateTz.ms !== data.dateTz.ms;
			// booking.duration = data.duration;
			booking.startDateTz = data.dateTz; // date = data.dateTz.ms;
			booking.endDateTz = data.dateTz.addValue({ minutes: data.duration });
			this.loading = true;

			const roomRules = rulesForSpace({
				time: booking.date,
				duration: booking.duration,
				user: this.currentUser,
				rules: this._data.booking.room.building?.booking_rules,
				space: booking.room
			});

			// Rooms requiring approval are reset to tentative when the time changes.
			if (pendingChanges && !roomRules?.auto_approve) {
				// https://acaprojects.atlassian.net/browse/MCK-1481
				/**
				 * Having 2 ways of containing the status is icky but until PlaceOS
				 * depreciates approval_status it's necessry.
				 */
				// booking.approval_status[booking.room.email] = "tentative";
				booking.status = "tentative";
				booking.time_changed = true;
			}
			else if (roomRules.reason === SpaceFilterReason.OfficeRules) {
				// this.alertIfItHasConflict(false, true);
				this.invalidDueToOfficeRule = true;
				this.loading = false;
				return;
			}

			// booking.save() won't use the toAPIJSON conversion so use the booking service.
			this._bookings.save(booking).then(
				(bkn) => {
					// update booking
					this.updateBookingState(booking);
					this.form = generateBookingForm(booking, this.available_fields);
					this.loading = false;
					this.displayInfoBanner();
					booking.room.building.name
					this._service.analytics.track('update', {
						booking_id: booking.id,
						office: booking.room.building.name,
						room: booking.room.global_name,
						start_time: booking.startDateTz.ms,
						end_time: booking.endDateTz.ms,
						catering: !!booking.catering.legth,
						headcount: booking.head_count,
					});
				},
				(err) => {
					this.loading = false;
					this.expandedAvailability = false;
					this.displayWarningBanner(getStatusErrorMessage(err?.status));
				}
			);
		}
	}

	/**
	 *  Open dialog if booking date and time is out of office rule
	 *  TO DO :  Redundant code - same validaiton been applied over room detail while creating new booking - need to handle more graceful way. better if user been prohibited from choose future date if out of office rule on show avaialability calender itself
	 *  related to - https://acaprojects.atlassian.net/browse/MCK-2419
	 */

	alertIfItHasConflict(isBookable, isAvailable) {
		// this.isAlertDisplayed = true;
		const data = {
			headerText: !isBookable ? SpaceConflictAlertModalData.NotBookableHeaderText : !isAvailable ? SpaceConflictAlertModalData.NotAvailableHeaderText : '',
			message: SpaceConflictAlertModalData.NotBookableDueToOfficeRule,
			buttonLabel: 'Close',
			callback: () => null,
			shouldClose: false
		};
		const dialog = this.dialog.open(ConflictAlertModalComponent,
			{
				data: data,
				panelClass: 'mck-alert',
				autoFocus: false,
				backdropClass: 'mck-modal-backdrop',
				disableClose: !isBookable
			});
	}



