	public setAlternativeWeekStartDate(bookingDate: DateTZ) {
		if (!bookingDate) {
			return;
		}

		if (this.mobile) {
			this.alternativeWeekStartDateTz = bookingDate.clone();
		} else {
			this.alternativeWeekStartDateTz = bookingDate.clone().startOfValue('week').startOfValue('day');
		}
	}

	public updateCalendarWeekView(alternativeWeekStartDateTz: DateTZ) {
		this.alternativeWeekStartDateTz = alternativeWeekStartDateTz.clone();
		this.fetchRooms(true, this.alternativeWeekStartDateTz);
	}

	public fetchRooms(allWeek: boolean = true, alternativeWeekStartDateTz: DateTZ = null) {
		if (!this._data?.booking?.space?.id) {
			return
		}

		const day = 24 * 60;
		const week = day * 7;
		const date = (alternativeWeekStartDateTz || this._data?.booking?.startDateTz).clone(); // new DateTZ({date: alternativeWeekStartDateTz || this._data?.booking?.date});
		const startOfDay = date.startOfValue('day').setValue({ second: 0, millisecond: 0 });
		const startOfWeek = date.startOfValue('week');

		// Query for the entire day for this room
		const query = {
			dateTz: allWeek ? startOfWeek : startOfDay,
			duration: allWeek ? week : day,
			locations: this._data?.booking?.space?.id,
			capacity: this._data?.booking?.space?.capacity,
		};
		this.store.dispatch(loadByIdResults.request(query));
	}

	public addCatering() {
		// For the Add Catering button
		this.showCatering = true;
	}

	public saveBookingWithCatering(form: FormGroup) {
		// Response from the catering modal closing
		// Close catering and confirmation modals and just show booking details modal
		if (!form) {
			this.showCatering = false;
			this.showDetails = false;
			return;
		}
		// Update the booking state.
		const booking = this._data.booking.clone();
		booking.catering = form.controls.catering.value;

		if (booking.catering.length) {
			const setup = {};
			setup[booking.room.email] = booking.setup[booking.room.email] || 900;
			const breakdown = {};
			breakdown[booking.room.email] = booking.breakdown[booking.room.email] || 900;

			booking.setup = setup
			booking.breakdown = breakdown
		}

		this.updateBookingState(booking);
	}

