import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    OnChanges,
    Output,
    ViewContainerRef,
    ViewChild
} from '@angular/core';
import {
    FormControl,
    FormGroup,
    FormGroupDirective,
    Validators
} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';

import { closestToTimeSlot, closestToTimeSlotTz, resetSecondsOnTimestamp } from '@mckinsey-converge/base';

import {
    distinctUntilChanged,
    map,
    skip,
    take,
    withLatestFrom
} from 'rxjs/operators';
import {
    select,
    Store
} from '@ngrx/store';
import {
    buildingStateSelector,
    selectBuildingOptionsGroupedByCity
} from '@mckinsey-converge/buildings';
import {
    Building,
    BuildingCity,
    flattenDisplayOffices,
    toggleSelectedByGroup,
    storeBookingFormData,
    BookingFormData,
    bookingTypeOptions,
    DEFAULT_BOOKING_DURATION,
    defaultRoomSizeOption,
    internalBookingTypeOption,
    roomSizeOptions,
    selectActiveForm,
    ApplicationService,
    roomSizeOptionsKiosk,
		RecurrencePeriod,
        generateBookingForm,
        SeriesAction,
        setRecurrenceStartsTime
} from '@mckinsey-converge/data-common';
import { BehaviorSubject } from 'rxjs';
import {
    BaseTemplateComponent,
    SelectOption
} from '@mckinsey-converge/ui';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { DateTime } from 'luxon';
import { ActivatedRoute, Router } from '@angular/router';
import { Booking } from '../../../../../../libs/data-common/src';
import * as cloneDeep from 'lodash/cloneDeep';
@Component({
    selector: 'app-room-booking-form',
    templateUrl: './room-booking-form.component.html',
    styleUrls: ['./room-booking-form.component.scss']
})
export class RoomBookingFormComponent extends BaseTemplateComponent implements OnInit, OnChanges {
    @ViewChild('formRef')
    formRef: FormGroupDirective;

    @Output()
    public submitForm = new EventEmitter<BookingFormData>();

    @Input()
    public showCancel = false;

    @Input()
    public triggerSubmit: number;

    /**
     * Used only if showCancel is true.
     */
    @Output()
    public cancelSubmit = new EventEmitter();

    // fromDate is the selected date
    public fromDate: number = closestToTimeSlot(DateTime.now(), 15).valueOf(); // R--- depreciate
    /** DateTZ object of the date and time that the date and time picker will begin it's options from */
    public fromDateTz: DateTZ = closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false }), 15);
    public bookingTypeOptions = bookingTypeOptions;
    public roomSizeOptions = roomSizeOptions;
    public localTimezone = DateNow(new Date()).building_tz;
    public localStart = "";
    public localEnd = ""

    public formGroup: FormGroup = new FormGroup({
        id: new FormControl('', []), // used in case of recurring series edit
        location: new FormControl([], [Validators.required]),
        date: new FormControl(this.fromDate, [Validators.required]),
        dateTz: new FormControl(this.fromDateTz, [Validators.required]), // starts out as the next closest available time interval
        duration: new FormControl(DEFAULT_BOOKING_DURATION, [Validators.required]),
        bookingType: new FormControl(internalBookingTypeOption),
        roomSize: new FormControl(defaultRoomSizeOption),
        selectedRoom: new FormControl({}),
        timezone: new FormControl(""),
        offset: new FormControl(""),

		recurrence_period: new FormControl(RecurrencePeriod.WEEKLY),
        recurrence_interval: new FormControl( 1),
        recurrence_endTz: new FormControl( new DateTZ({ date: 1 }).addValue({days: 1})),
        recurrence_count: new FormControl( 0),
        is_recurrent: new FormControl(null),
        recurrence_starts: new FormControl( []),
        recurrence_exceptions: new FormControl([]),
        recurrence_pattern: new FormControl(RecurrencePeriod.WEEKLY), //Helper value to store the last selected pattern (daily, weekly, monthly),
        recurrence_days: new FormControl( []),

        ignore: new FormControl('', []), // used in case of recurring series edit
        // TODO, there's a few missing from interface BookingFormData, libs/data-common/src/lib/booking/booking.form.data.ts
    });


    public options = new BehaviorSubject<SelectOption<any>[]>([]);

    public selectedOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    constructor(private store: Store,
        // private _service: ApplicationService,
        vcRef: ViewContainerRef, private _snackBar: MatSnackBar) {
        super(vcRef);
        // if(this._service?.kioskFormFilter) {
        //     this.formGroup.setValue({
        //         roomSize: roomSizeOptions[3]
        //     })
        // }
    }

    public ngOnInit(): void {


        super.ngOnInit();
        // R--- Complicated, need the TZ aware inputs and to not init those inputs until the building timezone is available.
        // R--- See if there's any way to make the timezone retrieval less fragile.

        // if we have an active form, refill the information here once.
        // Doesn't run on initial steps, only if you go back to the home page
        this.subscription('reduxData', this.store.select(selectActiveForm)
            .pipe(take(1))
            .subscribe((next: BookingFormData | undefined) => {
                if (next) {
                    // set the fromDate correctly
                    this.fromDateTz = next.is_recurrent ? next.dateTz : closestToTimeSlotTz(DateNow(), 15); // R--- shouldn't this actually be the current date start of day to make all the options available?

                    const form = cloneDeep(next);
                    this.formGroup.patchValue({
                        ...form,
                    }, { emitEvent: true });

                    // this.formGroup.markAllAsTouched();
                    // this.formGroup.updateValueAndValidity();

                }
            })
        );

        // R--- I only see this fire when the modal opens or going to the home page
        this.subscription('formChanges', this.formGroup.valueChanges
            .pipe(
                skip(1), // initial form changes
                distinctUntilChanged())
            .subscribe((next) => {
                const timezone = next.timezone || this.localTimezone
                /**
                 * When the selected date changes update the time picker's start time
                 * to the nearest available slot of the current day
                 */
                this.fromDateTz = next.is_recurrent ? next.dateTz : closestToTimeSlotTz(DateNow(), 15);

                /**
                 * When the new selected date arrives, update the coachmark displayed below the date time picker
                 */
                this.updateCoachmarkTimes(timezone);

            })
        );

        this.subscription('buildings', this.store.pipe(
            select(buildingStateSelector),
            select(selectBuildingOptionsGroupedByCity),
            map((grouped: Map<BuildingCity, Building[]>) => {
                return flattenDisplayOffices(grouped);
            }),
            distinctUntilChanged()
        )
            .subscribe(next => {
                this.options.next(next);
            }));
    }

    public ngOnChanges(changes: any) {
        if (changes.triggerSubmit && this.formRef) {
            // Trigger the form submit and validation
            this.formRef.onSubmit(undefined);
        }
    }

    public onSubmitForm() {
        if(this.formGroup.controls?.is_recurrent?.value){
            setRecurrenceStartsTime(this.formGroup)
        }
        // Check for past date time
        const formGroupData = this.formGroup.value;
        const is_past = this._checkDateInPast();
        if (is_past) {
            this.openSnackBar('Start Time updated to after current time, please click search again.', 'Close');
            return;
        } else {
            this.formGroup.setValue({
                ...formGroupData,
                date: resetSecondsOnTimestamp(formGroupData.dateTz.ms),
                dateTz: formGroupData.dateTz.setValue({ millisecond: 0, second: 0 })
            });
        }
        this.formGroup.markAllAsTouched();
        if (this.formGroup.valid) {
            this.submitForm.emit(this.formGroup.value);
        }
    }

    public openSnackBar(message: string, action: string) {
        this._snackBar.open(message, action, {
            duration: 5000,
            panelClass: ['yellow-snackbar']
        });
    }

    // Fires when the location selector value is changed
    public toggle(option: SelectOption<any>) {
        // update the selected value
        this.selectedOptions.pipe(
            take(1),
            withLatestFrom(this.options),
            map(([selectedOptions, options]) => {
                return toggleSelectedByGroup(options, selectedOptions, option);
            })
        ).subscribe(next => {
            // deselecting the location option sets the timezone to local so convert.
            const formGroupDate = this.formGroup.value;
            if (!next.length) {
                this.formGroup.setValue({
                    ...this.formGroup.value,
                    timezone: this.localTimezone,
                    date: formGroupDate.dateTz.ms,
                    dateTz: formGroupDate.dateTz.toZone(this.localTimezone)
                });
            } else {
                const buildingTimezone = next[0]?.meta?.building?.timezone || next[0]?.meta?.city?.timezone;
                this.formGroup.setValue({
                    ...this.formGroup.value,
                    timezone: buildingTimezone,
                    date: formGroupDate.dateTz.ms,
                    dateTz: formGroupDate.dateTz.toZone(buildingTimezone)
                });
            }
            this._checkDateInPast();
            // Must fire last or location picker will be buggy.
            this.selectedOptions.next(next);
        });
    }

    private _checkDateInPast() {
        // Check for past date time
        const formGroupData = this.formGroup.value;
        const date = new DateTZ({ date: this.formGroup.value.dateTz.ms, building_tz: formGroupData.timezone, is_local_tz:false }).addValue({ seconds: 30 })
        const now = DateNow(new Date())
        if (now.isAfterDate(date)) {
            // Reset the start time if in the past
            const closestTz = closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: formGroupData.timezone }).addValue({ minutes: 1 }), 15)
            const prevClosestTz = closestTz.subtractValue({minutes: 15})
            const prevDateTz = prevClosestTz.addValue({ seconds: 30 });
            // R-- why do I do this on submit? move to a another method.
            if(now.isAfterDate(prevDateTz)){
                this.fromDateTz = closestTz;
            }else{
                this.fromDateTz = prevClosestTz;
            }

            this.formGroup.setValue({
                ...formGroupData,
                date: resetSecondsOnTimestamp(this.fromDateTz.ms),
                dateTz: this.fromDateTz.setValue({ millisecond: 0, second: 0 })
            });
            return true;
        }
        return false;
    }

    private updateCoachmarkTimes(timezone: string) {
        // Resolve the problem that locally I might have the same TZ offset but the names don't match.
        const localTzOffset = DateTime.now().setZone(this.localTimezone).offset;
        const buildingTzOffset = DateTime.now().setZone(timezone).offset;

        this.fromDateTz = new DateTZ({ date: this.fromDateTz.ms, is_local_tz: false, building_tz: timezone });

        if (localTzOffset !== buildingTzOffset) {
            const startDate = this.formGroup.value.dateTz.clone();
            this.localStart = startDate.formatLocalDate("h:mma");
            this.localEnd = startDate.addValue({ minutes: this.formGroup.value.duration }).formatLocalDate("h:mma");
        } else {
            // No location is selected so coachmark is hidden
            this.localStart = "";
            this.localEnd = "";
        }
    }
}
