import {
    Component,
    Input,
    OnInit,
    OnChanges,
    EventEmitter,
    Output,
    ViewEncapsulation,
} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import {
    BehaviorSubject,
    combineLatest,
} from 'rxjs';
import {
    map,
    take,
} from 'rxjs/operators';
import { filterUndefined } from '@mckinsey-converge/rx-utils';
import {
    select,
    Store
} from '@ngrx/store';
import {
    CENTER_POINT,
    generateMapStyleForSpace,
    legendOptions,
    MAX_ZOOM_LEVEL,
    MIN_ZOOM_LEVEL,
    spaceToMapFeature,
} from 'src/app/map/map.utils';
import {
    Building,
    BuildingLevel,
    SettingsService,
    Space,
    SpaceStatus,
    selectMapFilteredResults,
    FilteredSpaces
} from '@mckinsey-converge/data-common';
import {
    Point,
    HashMap,
    BaseDirective,
} from '@mckinsey-converge/base';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { OfficeInformationModalComponent } from 'src/app/map/office-information-modal/office-information-modal.component';
import { MapSearchStoreState } from '../../map/map-search/map-search.component';
import { LoadingModel } from '@mckinsey-converge/loading';
import {
    generateFeedbackUrl,
    Urls
} from '../../utilities/urls';

export enum TopBarHeaderMode {
    Light = 'light',
    Dark = 'dark',
}

@Component({
    selector: 'room-location-preview',
    template: `
        <div class="room-location mck-row" [class.booking-view]="!expanded && viewMode === 'booking_preview'">
            <div class="map-wrapper" [class.expanded]="expanded">
                <header class="header" *ngIf="expanded">
                    <a-topbar-header
                        [mode]="mode"
                        [backModal]="true"
                        [noSurvey]="true"
                        (closeCallback)="collapse()">
                    </a-topbar-header>
                </header>
                <div class="sub" *ngIf="expanded">
                    <div>
                        <div class="title">
                            <div class="title-name">
                                <img src="assets/icon/white-location.svg">
                                {{building.name}}, {{level.name}}
                            </div>
                            <div class="title-description">The pinpoint indicates your selected room's location on the floor. No futher interaction is available.</div>
                        </div>
                        <div class="map-controls">
                            <div class="mck-row">
                                <button
                                    name="info-button"
                                    mat-button
                                    (click)="displayOfficeInformation()"
                                    color="primary"
                                    class="mck-warning-button mck-square map-info-button">
                                    <div class="map-icon-info"></div>
                                </button>
                                <mck-legend-dropdown
                                    placeholder="Show legend"
                                    placeholderOpen="Hide legend"
                                    [options]="legendOptions"
                                ></mck-legend-dropdown>
                            </div>
                            <app-map-controls
                                (zoomInClick)="zoomIn()"
                                (zoomOutClick)="zoomOut()"
                                (refreshClick)="resetZoomLevels()"
                            ></app-map-controls>
                        </div>
                    </div>
                </div>
                <a-map
                    [class.np]="!expanded"
                    [zoom]="zoomLevel"
                    [src]="mapUrl"
                    [focus]="focusedRoom | async"
                    [center]="center | async"
                    [css]="style_map | async"
                    [features]="features | async"
                >
                </a-map>
                <div class="footer mck-column mck-center" *ngIf="expanded">
                    <div>
                        <button mat-button color="primary" class="mck-primary-button" (click)="collapse()">Close</button>
                    </div>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./room-location-preview.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class RoomLocationPreviewComponent extends BaseDirective implements OnInit, OnChanges {
    public legendOptions = legendOptions();

    @Input()
    public mapUrl: string;

    @Input()
    public focus: any; // @todo fix

    @Input()
    public expanded: boolean;

    @Input()
    public building: Building;

    @Input()
    public space: Space;

    @Input()
    public level: any; // @todo fix

    /** The theme to use **/
    @Input() public mode: TopBarHeaderMode = TopBarHeaderMode.Dark;

    // Use "booking_preview" to show smaller height with zoom icon
    @Input() public viewMode: String = null;

    @Output()
    public handleExpand: EventEmitter<any> = new EventEmitter();

    public currentPage: string;

    public get feedback() {
        return generateFeedbackUrl(this.currentPage);
    }

    public get zoomLevel() {
        return this.expanded ? this.minZoom.value : this.zoom.value;
    }

    constructor(
        private store: Store<MapSearchStoreState>,
        public dialog: MatDialog,
        private settingService: SettingsService) {
        super();
    }

    public focusedRoom = new BehaviorSubject<MapFeature | undefined>(undefined);
    public featuresRoom = new BehaviorSubject<MapFeature | undefined>(undefined);
    public zoom = new BehaviorSubject(MIN_ZOOM_LEVEL);
    public minZoom = new BehaviorSubject(MAX_ZOOM_LEVEL / 3);
    public features = combineLatest([this.featuresRoom]).pipe(map(items => items.filter(i => !!i)));
    public setIntervalHandle: any = null;
    public count = 0;
    public center = new BehaviorSubject<Point>(CENTER_POINT);
    /** Mapping of elements to CSS styles */
    public style_map = new BehaviorSubject<HashMap<HashMap<string | number>>>({});

    public activeBuilding = new BehaviorSubject<Building | undefined>(undefined);
    public activeLevel = new BehaviorSubject<BuildingLevel | undefined>(undefined);
    public activeSpace = new BehaviorSubject<Space | undefined>(undefined);

    public ngOnChanges(changes: any): void {
        if (changes.expanded && changes.expanded.currentValue) {
            this.activeSpace.pipe(
                take(1),
                filterUndefined<Space>(),
                map(spaceToMapFeature)
            ).subscribe(next => {
                this.featuresRoom.next(next);
            });
        } else if (changes.expanded && changes.expanded.previousValue && !changes.expanded.currentValue) {
            this.featuresRoom.next(undefined);
        }
    }

    public ngOnInit(): void {
        this.timeout('Getting Title', () => this.updateRoute(document.title));

        // MCK-1850 And it's even slower when loading form scratch via an email link, so 10 second interval.
        this.timeout('Zoom to Room', () => this.initMapZoom(), 2000);
        this.activeSpace.next(this.space);
        this.activeBuilding.next(this.building);
        this.activeLevel.next(this.level);

        this.subscription('bookingSpaceResults',
            combineLatest([
                this.store.pipe(
                    select(selectMapFilteredResults),
                    map((r: LoadingModel<FilteredSpaces>) => r.success)
                ),
                this.activeBuilding.pipe(filterUndefined<Building>()),
                this.activeLevel.pipe(filterUndefined<BuildingLevel>())
            ]).subscribe(([filteredSpaces, building, level]) => {
                const spacesFromSelection = (filteredSpaces.spaces || [])
                    .filter(s => !!s.map_id && s.zones.includes(building.id) && s.zones.includes(level.id));

                const styles: HashMap<HashMap<string | number>> = {};
                // const colour_map = this.settingService.get('app.explore.colors') || {};
                // spacesFromSelection.forEach(space => {
                //     const status = filteredSpaces.statusMap[space.id] || SpaceStatus.Available;
                //     const mapStyle = generateMapStyleForSpace(space, status, colour_map);
                //     styles[`#${mapStyle.id}`] = mapStyle.styles;
                // });
                this.style_map.next(styles);
                
            })
        );

    }

    public initMapZoom() {
        const self = this;
        // Doesn't reliably zoom to room until that component is finished loading and processing elements.
        // But this alone is not enough when loading from an email link due to how slow the loading is. See the ngOnInit
        this.setIntervalHandle = setInterval(() => {
            if (!this.focus) {
                return;
            }
            
            self.zoomToRoom();
            self.count ++;
            if (self.count > 4) {
                clearInterval(self.setIntervalHandle);
                self.setIntervalHandle = null;
            }
        }, 1000);
    }

    public updateRoute(location) {
        location = location.substr(0, location.indexOf(' |'));
        this.currentPage = location;
    }

    public goToHelp() {
        window.open(Urls.Help);
    }

    public zoomToRoom(): void {
        this.focusedRoom.next(this.focus);
        this.zoom.next(MAX_ZOOM_LEVEL);
    }

    public zoomIn() {
        if (this.minZoom.value < MAX_ZOOM_LEVEL) {
            this.minZoom.next(this.minZoom.value + 1);
        }
    }

    public zoomOut() {
        if (this.minZoom.value > MIN_ZOOM_LEVEL) {
            this.minZoom.next(this.minZoom.value - 1);
        }
    }

    public resetZoomLevels() {
        this.minZoom.next(MIN_ZOOM_LEVEL);
        this.zoom.next(MIN_ZOOM_LEVEL);
        this.center.next(CENTER_POINT);
        this.focusedRoom.next(undefined);
    }

    public collapse() {
        this.handleExpand.emit(false);
        const self = this;
        self.count = 0;

        this.zoom.next(MAX_ZOOM_LEVEL);
        this.center.next(CENTER_POINT);
        this.focusedRoom.next(undefined);

        // Doesn't reliably zoom to room until that component is finished loading and processing elements.
        this.setIntervalHandle = setInterval(() => {
            self.zoomToRoom();
            self.count ++;
            if (self.count > 4) {
                clearInterval(self.setIntervalHandle);
                self.setIntervalHandle = null;
            }
        }, 200);
    }

    public displayOfficeInformation() {
        this.dialog.open(OfficeInformationModalComponent,
            {
                data: this.building,
                panelClass: 'mck-alert',
                autoFocus: false,
                backdropClass: 'mck-modal-backdrop',
            });
    }
}
