import { LoadingModel } from '@mckinsey-converge/loading';
import { Organisation } from '@mckinsey-converge/data-common';
import { createReducer } from '@ngrx/store';
import { loadOrganisations } from './organisation.actions';

export interface OrganisationState {
  data: LoadingModel<Organisation>
}

const initialState: OrganisationState = {
  data: LoadingModel.empty()
};

export const organisationReducer = createReducer(initialState,
  ...loadOrganisations.ons);
