import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    OnChanges,
    Output,
    ViewContainerRef,
    ViewChild
} from '@angular/core';
import {
    AbstractControl,
    FormControl,
    FormGroup,
    FormGroupDirective,
    Validators
} from '@angular/forms';
import {
    BehaviorSubject,
    Observable,
    of,
    Subject
} from 'rxjs';
import {
    catchError,
    debounceTime,
    distinctUntilChanged,
    first,
    map,
    switchMap,
    take,
    withLatestFrom,
} from 'rxjs/operators';
import {
    Store
} from '@ngrx/store';
import {
    checkCollisions,
    Booking,
    User,
    UsersService,
    BookingFormData,
    selectActiveForm,
    SpacesService,
} from '@mckinsey-converge/data-common';
import {
    matchToHighlight
} from '@mckinsey-converge/base';

import {
    BaseTemplateComponent,
    SelectOption
} from '@mckinsey-converge/ui';
import { selectCurrentUser } from '@mckinsey-converge/user';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
    selector: 'app-room-booking-details-form',
    templateUrl: './room-booking-details-form.component.html',
    styleUrls: ['./room-booking-details-form.component.scss']
})
export class RoomBookingDetailsFormComponent extends BaseTemplateComponent implements OnInit, OnChanges {
    @ViewChild('formRef')
    public formRef: FormGroupDirective;
    /** Number of characters needed before a search will start */
    @Input() public limit = 3;
    /** When the value changes, the form is submitted */
    @Input() public triggerSubmit = '';
    /** Need booking details */
    @Input() public booking: Booking = null;
    /** Data returned to parent */
    @Output() public submitForm = new EventEmitter<BookingFormData>();
    /** Data returned to parent */
    @Output() public validateForm = new EventEmitter<boolean>();
    /** Current User */
    public currentUser: User;
    /** Host list to display */
    public host_list: User[];
    /** User list to display from the Attendees search */
    public user_list: User[];
    /** Whether user list is loading */
    public loading: boolean;
    /** Whether to show the attendees hint */
    public showHint = false;
    /** Attendee Input / Search String  */
    public searchString: string;
    /** List of users from an API search */
    public searchResults: Observable<User[]>;
    /** Subject holding the value of the search */
    public searchSubject = new Subject<string>();
    /** Host field options */
    public hostOptions = [];
    /** Attendees options */
    public options = new BehaviorSubject<SelectOption<any>[]>([]);
    /** Selected Attendees */
    public selectedOptions = new BehaviorSubject<SelectOption<any>[]>([]);
    public selectedUsers = new BehaviorSubject<SelectOption<any>[]>([]);
    public needs_charge_code = false;
    public has_delegate = false;
    public available: boolean = null;
    public notesHandle = null;
    public noteValidationClass = '';
    public selectedCompanies = new BehaviorSubject<SelectOption<any>[]>([]);
    public companyOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    /** Data collected on this form */
    public bookingFormGroup = new FormGroup({
        organiser: new FormControl(),
        title: new FormControl('', [ Validators.minLength(1)]), //Validators.pattern("[^=`~!@#$%*{};:'\",_.<>]*")
        attendees: new FormControl(),
        code: new FormControl('', [Validators.minLength(4), Validators.pattern(/^[a-zA-Z0-9]*$/)]),
        notes: new FormControl(),
        head_count: new FormControl(1, [Validators.required, Validators.min(1)]),
        user_list: new FormControl(),
        host_list: new FormControl(),
        company: new FormControl(),
        opt_out: new FormControl()
    });
    action: string;
    // Indecates that processing request to check availability of breakdown if special requirement being added by user
    checkingBreakdown: boolean = false;

    public get invalid_charge_error(): string {
        if (this.bookingFormGroup.controls.code.errors?.required) {
            return 'Charge Code is required';
        }
        if (this.bookingFormGroup.controls.code.errors?.minlength) {
            return 'Charge Code must be at least four characters';
        }
        if (this.bookingFormGroup.controls.code.errors?.pattern) {
            return 'Charge Code cannot contain spaces or special characters';
        }
        return '';
    }

    public get headCountControl(): AbstractControl {
        return this.bookingFormGroup.get('head_count');
    }

    constructor(
        private _spaces: SpacesService,
        private _snackBar: MatSnackBar,
        private store: Store,
        vcRef: ViewContainerRef,
        private _users: UsersService) {
        super(vcRef);
    }

    public ngOnInit(): void {
        super.ngOnInit();
        this.subscription('currentUser', this.store.select(selectCurrentUser)
            .pipe(take(1))
            .subscribe((user: User) => {
                // Once we know we have the user loaded.
                this.currentUser = new User(user);
            })
        );

        this._users.initialised.pipe(first((_) => _)).subscribe(() => {
            const user: User = this._users.current;
            const delegates = user.delegates.map((del) => {
                // the kludgy or new User is for when the user service isn't ready.
                const ifUser = this._users.find(del.email);
                return ifUser ? 
                    new User({ email: ifUser.email, name: ifUser.name, opt_out: del.opt_out }) : 
                    new User({ email: del.email, name: del.email.split('@')[0].split('_').map((s) => s.charAt(0).toUpperCase() + s.substring(1))
                    .join(' ')
                    , opt_out: del.opt_out });

                // (del) => new User({ email: del.email, name: del.email.split('@')[0].split('_').join(' '), opt_out: del.opt_out })
                });
            if (delegates.length) {
                this.has_delegate = true;
            }
            this.host_list = [user].concat(delegates);
            this.hostOptions = this.host_list.map(h => ({ display: h.name, value: h.email, meta: { user: h } }));
        });

        // if we have an active form, refill the information here once.
        this.subscription('reduxData', this.store.select(selectActiveForm)
            .pipe(take(1))
            .subscribe((next: BookingFormData | undefined) => {
                if (next) {
                    // Get the Equipment charge code mandatory requirement
                    const { filters, settings } = next.selectedRoom?.space;
                    const roomBasedEqCode = filters?.equipment_code || settings?.equipment_code;
                    const officeBasedEqCode = next.selectedRoom?.building?.required?.equipment_code;
                    this.needs_charge_code = roomBasedEqCode !== undefined ? roomBasedEqCode : officeBasedEqCode !== undefined ? officeBasedEqCode: false;
                    

                    if (this.needs_charge_code) {
                        this.bookingFormGroup.controls.code.setValidators([Validators.required, Validators.minLength(4), Validators.pattern(/^[a-zA-Z0-9]*$/)]);
                    }
                    // build the initial options
                    const options = [];
                    const attendees = next?.attendees || [];
                    const organiser = next?.organiser || { display: this.currentUser.name , value: this.currentUser.email, meta: { external: false, user: this.currentUser }};
                    const company = next?.company || [];

                    const opt_out = next?.opt_out || false;
                    this.action = next.action || 'new';
                    // Load the saved form
                    this.bookingFormGroup.setValue({
                        organiser,
                        title: next.title || '',
                        attendees, // options will display current user
                        code: next.code || '',
                        notes: next.notes || '',
                        head_count: next.head_count || '',
                        user_list: [],
                        host_list: [],
                        company,
                        opt_out: opt_out
                    });
                    this.selectedOptions.next(attendees);
                    this.options.next(attendees);
                    this.selectedCompanies.next(company);
                    this.companyOptions.next(company);
                }
            })
        );

        // Listen for input changes
        this.searchResults = this.searchSubject.pipe(
            debounceTime(500),
            distinctUntilChanged(),
            switchMap((query) => {
                this.loading = true;
                return query.length >= this.limit
                    ? this._users.query({
                        q: query,
                        cache: 60 * 1000
                    })
                    : Promise.resolve([]);
            }),
            catchError((_) => of([])),
            map((list: User[]) => {
                this.loading = false;
                return list;
            })
        );

        // Process API results
        this.subscription(
            'search_results',
            this.searchResults.subscribe((list) => {
                this.user_list = list;
                this.user_list.forEach((i: any) => {
                    i.match_name = matchToHighlight(i.match_name);
                    i.match_email = matchToHighlight(i.match_email);
                    return i;
                });

                // Build options from returned users
                const options = this.user_list.map(u => {
                    return {
                        value: u.email,
                        display: `${u.name} ${u.email}`,
                        groupId: 'one',
                        groupHeader: true,
                        shortDisplay: `${u.name}`,
                        meta: { external: false, user: u },
                    };
                });
                // Add the manual option to the beginning of the array
                options.unshift({
                    value: this.searchString,
                    display: this.searchString,
                    groupId: 'one',
                    groupHeader: true,
                    shortDisplay: this.searchString,
                    meta: { external: true, user: null },
                });
                // this.loading = false;
                this.options.next(options);
            })
        );

        this.bookingFormGroup.valueChanges.subscribe(val => {

            clearTimeout(this.notesHandle);
            if (val?.notes?.length) {
                this.checkingBreakdown = true;
               
                // Check for conflicts if adding a note and default setup and breakdown times.
                this.notesHandle = setTimeout(() => {
                    this.validateForm.emit(false);
                    const booking = this.booking.clone();
                    const setup = {};
                    setup[booking.space.email] = booking.setup[booking.space.email] || 900;
                    const breakdown = {};
                    breakdown[booking.space.email] = booking.breakdown[booking.space.email] || 900;

                    const mockBooking = new Booking({
                        id: booking.id,
                        room: booking.space,
                        start_epoch: booking.startDateTz.seconds,
                        end_epoch: booking.endDateTz.seconds,
                        setup,
                        breakdown,
                    });

                    checkCollisions(
                        this._spaces,
                        mockBooking
                    ).then((bookable: boolean) => {
                        this.checkingBreakdown = false;
                        this.available = bookable;
                        // this.validateForm.emit(true);
                        this.validateForm.emit(this.bookingFormGroup.valid);
                        this.noteValidationClass = bookable === false ? 'booked' : 'available';
                        // this.loading = false;
                    }).catch(() => {
                        this.checkingBreakdown = false;
                        this.available = false;
                        this.validateForm.emit(false);
                        this.noteValidationClass = 'booked';
                        // this.loading = false;

                        return;
                    });
                }, 200);
            } else {
                this.checkingBreakdown = false;
                this.available = null;
                this.noteValidationClass = '';
                this.validateForm.emit(this.bookingFormGroup.valid);
            }

          });
    }

    public setOrganiserOptOut(opt_out) {
        this.bookingFormGroup.controls.opt_out.setValue(opt_out);
    }

    public updateOrganiser(user) {
        console.log(user);
    }

    /** Listen for changes */
    public ngOnChanges(changes: any) {
        if (changes.triggerSubmit && this.formRef) {
            // Trigger the form submit and validation
            this.formRef.onSubmit(undefined);
        }
    }

     /**
     * 
     * @param key : formcontrol name 
     * Function trim value being submitted with only spaces/beginning/end 
     */
    public trim(key: string): void {
        this.bookingFormGroup.get(key).setValue(this.bookingFormGroup.get(key).value.trim());
    }

    /** Toggle an attendee on/off */
    public toggle(option: SelectOption<any>) {
        const inputValue = option.value
            .split(' ')
            .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
            .join(' ');
        // Check for character limit
        if (
            // this.selectedOptions.value.some((option: SelectOption<any>) => option.value === inputValue) ||
            inputValue.length > 50
        ) {
            this._snackBar.open('Attendee name cannot exceed 50 characters', 'Close', {
                duration: 5000,
            });
            return;
        }

        this.selectedOptions
            .pipe(
                take(1),
                withLatestFrom(this.options),
                map(([selectedOptions, options]) => {
                    let opts = selectedOptions.slice();
                    const found = opts.some((v) => inputValue === v.value);
                    if (found) {
                        opts = opts.filter((v) => inputValue !== v.value);
                    } else {
                        const color = inputValue.toLowerCase().includes('mckinsey.')
                            ? 'internal'
                            : 'external';
                        opts.push({
                            ...option,
                            value: inputValue,
                            display: inputValue,
                            shortDisplay: inputValue,
                            meta: {
                                ...option.meta,
                                color,
                            },
                        });
                    }

                    return opts;
                })
            )
            .subscribe((next) => {
                if (!this.bookingFormGroup.controls.head_count.touched) {
                    this.bookingFormGroup.controls.head_count.setValue(next.length + 1);
                }
                this.selectedOptions.next(next);
            });
    }

    public clearHeadcount() {
        this.headCountControl.setValue(1);
    }

    /** Return the data to the listening parent */
    public onSubmitForm() {
        this.bookingFormGroup.markAllAsTouched();
        // a bit of an issue that the form state isn't saved unless valid
        if (this.bookingFormGroup.valid) {
            const form_data = this.bookingFormGroup.value;
            form_data.user_list = this.user_list;
            form_data.host_list = this.host_list;
            form_data.has_delegate = this.has_delegate;
            this.submitForm.emit(form_data);
        }
    }

    /** Take the input and feed it back to the parent to add to attendees array */
    public updateAttendeeOptionsFromUserInput(v: string) {
        const l = v.length;
        const options = this.selectedOptions.value.slice();
        if (!l) {
            this.options.next([]);
            return;
        }
        if (l > 2) {
            // Triggers the API call to search for the user input
            this.searchSubject.next(v);
        }

        options.unshift({
            value: v,
            display: v,
            groupId: 'one',
            groupHeader: true,
            shortDisplay: v,
            meta: { external: true },
        });
        this.searchString = v;
        this.options.next(options);
    }

    public toggleAttendeesHint() {
           this.showHint = !this.showHint;
    }

    public updateCompanyOptionsFromUserInput(v: string) {
        if (!v.length) {
            this.companyOptions.next([]);
            return;
        }
        this.companyOptions.next([{
            value: v,
            display: v,
            groupId: 'two',
            groupHeader: true,
            shortDisplay: v,
        }]);
    }

    public toggleCompany(option: SelectOption<any>) {
        this.selectedCompanies.pipe(
            take(1),
            withLatestFrom(this.companyOptions),
            map(([selectedCompanies, options]) => {
                let opts = selectedCompanies.slice();
                const found = opts.some(v => option.value === v.value);
                if (found) {
                    opts = opts.filter(v => option.value !== v.value);
                } else {
                    opts.push(option);
                }

                return opts;
            })
        ).subscribe(next => {
            this.selectedCompanies.next(next);
        });
    }
}
