import {
    Booking
} from '@mckinsey-converge/data-common';


export class BookingResultsViewModel {
    constructor(public booking: Booking) {
    }
}

/**
 * For each result from results, we find its level and building from the lists.
 */
export const mapResultsToViewModels = (results: Booking[]): BookingResultsViewModel[] => {
    if (!results || !results[0]?.space) {
        return [];
    }
    return results.map((booking: Booking) => {
        return new BookingResultsViewModel(
            booking,
        );
    });    
}

