import { Component, OnInit } from '@angular/core';
import { filter, first, map, distinctUntilChanged, shareReplay } from 'rxjs/operators';
import {
    ApplicationLoadingState,
    BaseDirective,
    closestToTimeSlot,
    closestToTimeSlotTz,
    LoadingState,
} from '@mckinsey-converge/base';
import {
    ApplicationIcon,
    ApplicationService,
    Building,
    BuildingCity,
    defaultRoomSizeOption,
    DEFAULT_BOOKING_DURATION,
    flattenDisplayOffices,
    internalBookingTypeOption,
    mapBuildingToSelectOption,
    mapCityToAllSelectOption,
    mapResultsToViewModels,
    OrganisationService,
    SpacesService,
    storeBookingFormData,
    availabilityOptionsToQuery,
    bookingTypeOptions,
    selectFilteredDiffSizeResults,
    flattenBuildingsWithLevels,
    clearRoomFilters,
    allBookingTypeOption,
    roomSizeOptions,
    roomSizeOptionsKiosk,
} from '@mckinsey-converge/data-common';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { loadUser, selectHasUserData, UserStoreState } from '@mckinsey-converge/user';
import { ComposerService } from '@placeos/composer';
import { ActivatedRoute, Router, NavigationStart, Event as NavigationEvent } from '@angular/router';

import {
    DateNow,
    DateTZ,
    convertLocalTimestampToTimezonedDateTz,
} from '@mckinsey-converge/date-tz';
import { DateTime } from 'luxon';
import { SelectOption } from '@mckinsey-converge/ui';
import { selectCurrentUser } from '@mckinsey-converge/user';
import {
    buildingStateSelector,
    loadBuildings,
    selectBuildingOptionsGroupedByCity,
} from '@mckinsey-converge/buildings';
import { loadDiffSizeResults, loadRoomsForResults, loadRoomsForResultsMap } from '@mckinsey-converge/rooms';
import { timeStamp } from 'console';
import { filterUndefined } from '@mckinsey-converge/rx-utils';

@Component({
    selector: 'app-shell-wrapper',
    templateUrl: './shell.component.html',
    styleUrls: ['./shell.component.scss'],
})
export class ShellWrapperComponent extends BaseDirective implements OnInit {
    /** Loading state of the application */
    public loading_state: ApplicationLoadingState;
    /** Logo of the application organisation */
    public logo: ApplicationIcon;

    private userLoading = new BehaviorSubject<boolean>(true);
    public isQRCode = new BehaviorSubject<boolean>(true);
    public buildingOptions = new BehaviorSubject<SelectOption<any>[]>([]);
    public zone_id: any;
    public floor: any;

    constructor(
        private _service: ApplicationService,
        private placeOs: ComposerService,
        private _org: OrganisationService,
        private activatedRoute: ActivatedRoute,
        private _spaces: SpacesService,
        private router: Router,
        private store: Store<UserStoreState>
    ) {
        super();

        // Initialize roomID
        let roomId = null;
        if (this.activatedRoute.snapshot.queryParamMap.get('id')) {
            roomId = this.activatedRoute.snapshot.queryParamMap.get('id');
        } else if (localStorage.oauth_redirect && localStorage.oauth_redirect.includes('?id=')) {
            roomId = localStorage.oauth_redirect.split('=')[1];
        }
        // roomId ? this.isQRCode.next(false) : this.isQRCode.next(true);

        // Initialize zoneID
        let zone_id = null;
        if (this.activatedRoute.snapshot.queryParamMap.get('zone_id')) {
            zone_id = this.activatedRoute.snapshot.queryParamMap.get('zone_id');
        } else if (
            localStorage.oauth_redirect &&
            localStorage.oauth_redirect.includes('?zone_id=')
        ) {
            zone_id = localStorage.oauth_redirect.split('=')[1];
            if (zone_id.includes('&')) {
                zone_id = zone_id.split('&')[0];
            }
        }

        this.zone_id = zone_id;

        // Initialize floor number
        let floor = null;
        if (this.activatedRoute.snapshot.queryParamMap.get('floor')) {
            floor = this.activatedRoute.snapshot.queryParamMap.get('floor');
        } else if (localStorage.oauth_redirect && localStorage.oauth_redirect.includes('&floor=')) {
            floor = localStorage.oauth_redirect.split('&floor=')[1];
        }
        this.floor = floor;
        (floor && zone_id) || roomId ? this.isQRCode.next(false) : this.isQRCode.next(true);
    }

    /** Whether the user needs to login */
    public get show_login(): Observable<boolean> {
        return this.userLoading;
    }

    /** Whether a module is still loading data */
    public get is_loading(): Observable<boolean> {
        return this.userLoading;
    }

    /** List of modules with a loading state */
    public get loading_state_list(): LoadingState[] {
        const list: LoadingState[] = [];
        for (const mod in this.loading_state) {
            if (this.loading_state[mod]) {
                list.push(this.loading_state[mod]);
            }
        }
        return list;
    }

    /**
     * Function: To check and fetch room details if available for the scanned roomId
     * @param buildings
     * @param roomId
     */
    public checkRoomId(buildings: Building[], roomId: string) {
        let formGroup = {
            location: [],
            date: closestToTimeSlot(DateTime.now(), 15).valueOf(),
            dateTz: closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: true }), 15),
            duration: DEFAULT_BOOKING_DURATION,
            bookingType: bookingTypeOptions[0],
            roomSize: defaultRoomSizeOption,
            selectedRoom: null,
            timezone: null,
            buildings: buildings,
        };

        const params = {
            room_ids: roomId,
        };

        const availibityParams = {
            room_ids: roomId,
            dateTz: closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false }), 15),
            duration: DEFAULT_BOOKING_DURATION,
            hide_declined: true,
        };
        const query = availabilityOptionsToQuery(availibityParams);
        this._spaces.queryRooms(query).subscribe(
            (res) => {
                //Convert raw data into API object
                const list = res.map((i) => this._spaces.process(i));
                this._spaces.setSpaceList(list);
                /**
                 * Building form data to resue to navigate to result page if room is not available!
                 */
                const bookingRoom = mapResultsToViewModels(
                    list,
                    list[0].building.levels,
                    buildings
                );
                const city = list[0].building.city;
                const timezone = list[0].timezone;
                const grouped_by_city = {
                    key: { name: city, timezone },
                    value: buildings?.filter((bld) => bld.city === city) || [],
                };
                const city_buildings = grouped_by_city.value.map((bld) =>
                    mapBuildingToSelectOption(grouped_by_city.key.name, bld)
                );
                const options = [mapCityToAllSelectOption(grouped_by_city.key), ...city_buildings];

                const seledtedZone: SelectOption<any> = options.filter(
                    (e) => e.value === list[0].building.id
                )[0];
                const buildingTimezone = list[0].timezone;
                // form group data
                formGroup = {
                    location: [seledtedZone],
                    date: closestToTimeSlot(DateTime.now(), 15).valueOf(),
                    dateTz: closestToTimeSlotTz(
                        new DateTZ({ date: Date.now(), is_local_tz: false }),
                        15
                    ).toZone(buildingTimezone),
                    duration: DEFAULT_BOOKING_DURATION,
                    bookingType:
                        list[0].internal_or_external === 'internal'
                            ? bookingTypeOptions[0]
                            : bookingTypeOptions[1],
                    roomSize: defaultRoomSizeOption,
                    selectedRoom: bookingRoom[0],
                    timezone: buildingTimezone,
                    buildings: buildings,
                };

                this.store.dispatch(
                    storeBookingFormData({
                        payload: formGroup,
                    })
                );
                this.router.navigate([`../home/results/room`], {
                    state: { payload: { ...formGroup, offset: 0 } },
                });
                localStorage.oauth_redirect = localStorage.oauth_redirect.split('?')[0];
                this.isQRCode.next(true);
            },
            (error) => {
                // console.log(error);
                this.isQRCode.next(true);
                // this.router.navigate([`home/`]); // to navigate to default route if room dont exists
                /**
                 *  Navigate to room result page if no room/id exist in the db/ removed from the db in future -
                 */
                this.store.dispatch(storeBookingFormData({ payload: formGroup }));
                this.router.navigate([`../home/results`], {
                    state: { payload: { ...formGroup, offset: 0 } },
                    queryParams: { display: 'list' },
                });
            }
        );
    }

    public ngOnInit(): void {
        // when qr code lands on a url for the second time, reload it (to compensate for different browser/device combination default behaviors)
        this.subscription(
            'navigationEventShell',
            this.router.events
                .pipe(filter((event) => event instanceof NavigationStart))
                .subscribe((event: NavigationEvent) => {
                    if (event instanceof NavigationStart) {
                        if (
                            event.navigationTrigger === 'popstate' &&
                            (event.url.includes('home?zone_id=') || event.url.includes('home?id='))
                        ) {
                            this.userLoading.next(true);
                            window.location.reload();
                        }
                    }
                })
        );

        let roomId = null;
        if (this.activatedRoute.snapshot.queryParamMap.get('id')) {
            roomId = this.activatedRoute.snapshot.queryParamMap.get('id');
        } else if (localStorage.oauth_redirect && localStorage.oauth_redirect.includes('?id=')) {
            roomId = localStorage.oauth_redirect.split('=')[1];
        }

        if (roomId) {
            this.subscription(
                'buildings',
                this._org
                    .listen('buildings')
                    .pipe(first((_) => !!_ && !!_.length))
                    .subscribe((bld_list: Building[]) => {
                        this.checkRoomId(bld_list, roomId);
                    })
            );
        }

        if (this.zone_id && this.floor) {
            this.subscription(
                'buildings',
                this._org
                    .listen('buildings')
                    .pipe(first((_) => !!_ && !!_.length))
                    .subscribe((bld_list: Building[]) => {
                        const building = bld_list.filter((el) => el.id === this.zone_id);

                        const city = building[0].city;
                        const timezone = building[0].timezone;

                        const grouped_by_city = {
                            key: { name: city, timezone },
                            value: building || [],
                        };
                        const city_buildings = grouped_by_city.value.map((bld) =>
                            mapBuildingToSelectOption(grouped_by_city.key.name, bld)
                        );
                        const options = [
                            mapCityToAllSelectOption(grouped_by_city.key),
                            ...city_buildings,
                        ];

                        const seledtedZone: SelectOption<any> = options.filter(
                            (e) => e.value === building[0].id
                        )[0];
                        const buildingTimezone = building[0].timezone;
                        // form group data
                        const addMin = DateTime.now().get('minute') % 5 === 0 ? 1 : 0;
                        const formGroup = {
                            location: [seledtedZone],
                            date: closestToTimeSlot(
                                DateTime.now().plus({ minutes: addMin }),
                                15
                            ).valueOf(),
                            dateTz: closestToTimeSlotTz(
                                new DateTZ({
                                    date: DateTime.now().plus({ minutes: addMin }),
                                    is_local_tz: false,
                                    building_tz: buildingTimezone,
                                }),
                                15
                            ),
                            duration: 35,
                            bookingType: allBookingTypeOption, // all rooms (including internal and external both)
                            roomSize: roomSizeOptionsKiosk[3],
                            selectedRoom: null,
                            timezone: buildingTimezone,
                            buildings: bld_list,
                        };

                        const query2 = {
                            // capacity: 3,
                            locations: this.zone_id,
                            // capacity_max: 9,
                            // diff_capacity: true,
                            hide_declined: true,
                            dateTz: closestToTimeSlotTz(
                                new DateTZ({
                                    date: DateTime.now().plus({ minutes: addMin }),
                                    is_local_tz: false,
                                    building_tz: buildingTimezone,
                                }),
                                15
                            ),
                            duration: 35,
                        };

                        //clear search filters first if any applied previously
                        this.store.dispatch(clearRoomFilters());

                        this.store.dispatch(loadRoomsForResults.clear());
                        this.store.dispatch(storeBookingFormData({ payload: formGroup }));

                        this.store.dispatch(loadRoomsForResults.request(query2));
                        localStorage.oauth_redirect = '';
                        // To do: navigate to floor-map route
                        this.router.navigate([`../home/results`], {
                            state: {
                                payload: this.floor,
                                building: building,
                                floorMap: true,
                                url: window.location.href,
                            },
                            queryParams: { display: '' },
                        });
                        this.isQRCode.next(true);
                    })
            );
        }

        this.subscription(
            'loading',
            this._service.listen<ApplicationLoadingState>('loading').subscribe((loading) => {
                this.loading_state = loading;
            })
        );

        this._service.initialised.pipe(first((_) => _)).subscribe(() => {
            this.logo = this._service.setting('app.logo_light') || { type: 'icon' };
        });

        /**
         * Load current user into redux. this is meant to remove old way of loading user from user's service.
         */
        this.subscription(
            'currentUser',
            combineLatest([
                this.store.pipe(select(selectHasUserData)),
                this.placeOs.initialised,
                this.isQRCode, // added to track room details if QR code used to open app
            ])
                .pipe(
                    filter(([_, initialized, isQRCodeScanedAndReady]) => {
                        console.log(_);
                        return initialized && isQRCodeScanedAndReady;
                    })
                )
                .subscribe(([next]) => {
                    this.userLoading.next(!next);
                    if (!next) {
                        this.store.dispatch(loadUser.request());
                    }
                })
        );
    }
}
