import {
    Component,
    EventEmitter,
    Input,
    Output
} from '@angular/core';

import {
    ApplicationLink,
    ApplicationService
} from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';

import { DateNow } from '@mckinsey-converge/date-tz';


@Component({
    selector: 'a-overlay-menu',
    templateUrl: './overlay-menu.component.html',
    styleUrls: ['./overlay-menu.component.scss']
})
export class OverlayMenuComponent extends BaseDirective {
    /** Whether to show the menu */
    @Input() public show: boolean;
    /** Emitter for changes to show */
    @Output() public showChange = new EventEmitter<boolean>();

    /** List of links to display on the menu */
    public get menu_items(): ApplicationLink[] {
        const settings = this._service.setting('app.general.menu') || { items: [] };
        return settings.items || [];
    }

    /** Copyright details for the application */
    public get copyright(): string {
        return (this._service.setting('app.general.menu') || {}).copyright || '';
    }

    public get settings() {
        const settings = this._service.setting('app.general.menu') || { position: '' };
        return settings;
    }

    constructor(private _service: ApplicationService) {
        super();
    }

    /** Current year to display for copyright */
    public get year(): number {
        return DateNow(new Date()).dateYear;
    }

    /** Close the overlay menu */
    public close() {
        this.show = false;
        this.showChange.emit(this.show);
    }

    public delayedClose() {
        this.timeout('close', () => this.close(), 50);
    }

    public cancelClose() {
        this.timeout('cancel_close', () => this.clearTimeout('close'), 10);
    }
}
