import {
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges
} from '@angular/core';

import {
    humaniseDuration,
    timeFormatString,
    timeWithGmtOffsetFormatString,
    weekDayMonthYearFormat
} from '@mckinsey-converge/base';
import {
    truncateLocationList,
    BookingFormData,
    getLastDateFromList
} from '@mckinsey-converge/data-common';
import { SelectOption } from '@mckinsey-converge/ui';
import { DateTZ } from '@mckinsey-converge/date-tz'

@Component({
    selector: 'app-searched-location',
    template: `
        <div class="mck-row search-location-container">
            <div class="mck-column search-location-content">
                <span>{{name}}</span>
                <span>{{date}}</span>
                <span>{{timing}}</span>
                <span class="content-bottom">{{types}}</span>
                <span class="filters-empty" *ngIf="!filterDisplay && canFilter">No filters applied</span>
                <span class="filters-unavailable" *ngIf="!canFilter">Filtering unavailable for this view</span>
                <span class="filters" *ngIf="filterDisplay">{{filterDisplay}}</span>
            </div>
            <div class="mck-column mck-space-between">
                <button mat-stroked-button color="primary"
                        class="mck-stroked-button mck-short mck-column mck-center mck-primary"
                        (click)="changeClick.emit()">
                    Change
                </button>
                <button
                    [disabled]="!canFilter"
                    mat-button class="mck-primary-button mck-short mck-column mck-center"
                    (click)="filterClick.emit()">Filter {{filterCount}}
                </button>
            </div>
        </div>
    `,
    styleUrls: ['./searched-location.component.scss']
})
export class SearchedLocationComponent implements OnInit, OnChanges {

    @Input()
    public data: BookingFormData;

    @Input()
    public filters: SelectOption<any>[] = [];

    @Output()
    public changeClick = new EventEmitter();

    @Output()
    public filterClick = new EventEmitter();

    @Input()
    public canFilter = true;

    public name = '';
    public date = '';
    public timing = '';
    public types = '';
    public filterDisplay = '';
    public filterCount = '';

    constructor() {
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.data || changes.filters) {
            this.computeDisplay();
        }
    }

    public ngOnInit(): void {
        this.computeDisplay();
    }


    public computeDisplay() {
        if (!this.data) {
            return;
        }
        const { duration, dateTz, location, roomSize, bookingType, is_recurrent, recurrence_endTz } = this.data;

       
        // this.requestedEndDateTz =  new DateTZ({ date: endDate.date.valueOf(), is_local_tz, building_tz })

        const group = location.find(l => l.groupHeader);
        if (group) {
            this.name = group.shortDisplay;
        } else {
            this.name = truncateLocationList(location);
        }

        // R--- blocked until TZ booking form inputs
        const building_tz = location[0]?.meta?.building?.timezone || location[0]?.meta?.city?.timezone;
        const startDate = new DateTZ({date: dateTz.ms, is_local_tz: false, building_tz })

        if(is_recurrent) {
            const occEndDate = getLastDateFromList( this?.data?.recurrence_starts );
        	const endRecDate = new DateTZ({date: occEndDate.ms, is_local_tz: false, building_tz });
          this.date = `From ${weekDayMonthYearFormat(startDate)} until ${weekDayMonthYearFormat(endRecDate)}` ;
        }
        else {
            this.date = weekDayMonthYearFormat(startDate);
        }

        const startDisplay = startDate.formatDate(`${timeFormatString()}`).toLocaleLowerCase();
        const endDate = startDate.addValue({ minutes: duration });
        const endDisplay = timeWithGmtOffsetFormatString(endDate);
        this.timing = `${startDisplay}-${endDisplay}`; // - TODO correct for DST?

        const durationDisplay = humaniseDuration(duration, 'short');
        this.types = `${durationDisplay}, ${bookingType.display}, ${roomSize.display}`;

        this.filterDisplay = this.filters.map(f => f.shortDisplay).join(', ');
        this.filterCount = this.filters.length > 0 ? `(${this.filters.length})` : '';
    }
}
