import {
    Component,
    Input,
} from '@angular/core';
import {
    BaseDirective,
} from '@mckinsey-converge/base';
import {
    Booking,
} from '@mckinsey-converge/data-common';

@Component({
    selector: 'catering-order-table',
    template: `
    <div class="catering-orders-table" *ngIf="booking.cateringOrders.length">
        <table>
            <tr class="even">
                <td class="header time">
                    Delivery
                </td>
                <td class="header">Charge Code</td>
                <td class="header">Order</td>
            </tr>
            <tr *ngFor="let order of booking.cateringOrders; let i = index" [class]="oddEvenClass(i)">
                <td>
                    {{formatDeliveryTime(order)}}
                </td>
                <td class="code">
                    {{order?.charge_code}} 
                </td>
                <td>
                    {{listItems(order)}}
                </td>
            </tr>
        </table>
    </div>
    `,
    styleUrls: ['./order-table.component.scss']
})
export class OrderTableComponent extends BaseDirective {
    /** The theme to use **/
    @Input() public booking: Booking;

    public listItems(order) {
        return order.items.map(i => `${i.name}${i.amount > 1 ? ` (${i.amount})` : ''}`).join(', ');
    }

    public formatDeliveryTime(order) {
        return this.booking.startDateTz.addValue({ minutes: order?.delivery_time }).formatDate('h:mma').toLocaleLowerCase();
    }

    public oddEvenClass(x) {
        return x % 2 === 0 ? 'odd' : 'even';
    }
}
