import {
    Component,
    Inject,
    OnInit,
    Output,
    EventEmitter,
    ViewEncapsulation
} from '@angular/core';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';

import {
    AbstractControl,
    FormControl,
    FormGroup,
    Validators
} from '@angular/forms';
import {
    dateBuildingFormatString,
    startEndTimeWithZoneFormatString,
    humaniseDuration,
    closestToTimeSlotTz
} from '@mckinsey-converge/base';
import {
    Space,
    SpacesService,
    DEFAULT_BOOKING_DURATION,
    checkCollisions,
    Booking
} from '@mckinsey-converge/data-common';
import { DateTZ, convertLocalTimestampToTimezonedDateTz, convertTimezonedTimestampToLocalDateTz, DateNow } from '@mckinsey-converge/date-tz';

interface ModalData {
    dateTz: DateTZ,
    duration: number,
    pendingDateTz: DateTZ,
    pendingDuration: number,
    space?: Space,
    rules?: any,
    bookingId?: string,
    currentBooking?: Booking,
}

@Component({
    selector: 'app-room-availability-preview-modal',
    template: `
        <div>
        <mat-dialog-content class="av-modal">
            <div class="mck-column modal-heading">
                <button name="CloseButton" class="close-button" mat-icon-button (click)="closeModal()">
                    <div class="close-image"></div>
                </button>
                <div class="header">New Booking Date/Time</div>
                <form [formGroup]="formGroup">
                    <div class="mck-row-flow">
                        <date-tz-date-picker
                            label="Start date"
                            [required]="true"
                            class="mck-form-input time-inputs-calendar"
                            formControlName="dateTz"
                            (onSelect)="updateDatePickerValue($event)">
                        </date-tz-date-picker>
                        <mck-spacer size="16px" [mode]="'vertical'" [autoSize]="true"
                                spacerClass="mck-spacer-content-collapse-tablet"></mck-spacer>
                        <date-tz-time-field
                            class="time-inputs"
                            label="Start time"
                            [required]="true"
                            name="start-time"
                            [fromDateTz]="fromDateTz"
                            [ngModel]="formGroup.controls.dateTz.value"
                            [ngModelOptions]="{ standalone: true }"
                            (ngModelChange)="setValue($event, formGroup.controls.dateTz)">
                        </date-tz-time-field>
                        <mck-spacer size="16px" [mode]="'vertical'" [autoSize]="true"></mck-spacer>
                        <date-tz-duration-field
                            class="time-inputs"
                            label="End time"
                            name="end-time"
                            [startDateTz]="formGroup.controls.dateTz.value"
                            [max_length]="data.rules?.max_length"
                            [required]="true"
                            [ngModel]="formGroup.controls.duration.value"
                            [ngModelOptions]="{ standalone: true }"
                            (ngModelChange)="setValue($event, formGroup.controls.duration)">
                        </date-tz-duration-field>
                    </div>
                </form>
                <p *ngIf="available === true" class="available">Room is available for selected date/time.</p>
                <p *ngIf="available === false">Room is unavailable for selected date/time.</p>
                <p *ngIf="available === null" class="checking pulsate">Checking availability for selected date/time...</p>
                <p *ngIf="localTimezone !== buildingTimezone" class="coachmark">
                    Please be aware that this booking starts at {{localStart}} and ends at {{localEnd}} in your local timezone.
                </p>
            </div>
            <div class="mck-column modal-body">
                <div>
                    <div>
                        <h3>Old</h3>
                        <p>Date: {{dateString}}</p>
                        <p>Time: {{timeString}}</p>
                        <p>({{durationString}})</p>
                        <img src="assets/icon/next-arrow.svg" />
                    </div>
                    <div class="new">
                        <h3>New</h3>
                        <p>Date: {{pendingDateString}}</p>
                        <p>Time: {{pendingTimeString}}</p>
                        <p>({{pendingDurationString}})</p>
                    </div>
                </div>
            </div>
        </mat-dialog-content>
        <mat-dialog-actions>
                <mck-modal-button-row
                    [primaryDisabled]="!available"
                    primaryLabel="Confirm" (primarySubmit)="confirm()"
                    secondaryLabel="Cancel" (secondarySubmit)="closeModal()"
                ></mck-modal-button-row>
        </mat-dialog-actions>
        </div>
    `,
    styleUrls: ['./room-availability-preview-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class RoomAvailabilityPreviewModalComponent implements OnInit {
    @Output() public submitForm = new EventEmitter<any>();
    public dateString: string;
    public timeString: string;
    public durationString: string;
    public pendingDateString: string;
    public pendingTimeString: string;
    public pendingDurationString: string;
    public mapBookingRules: readonly string[];
    public mapBookingSubtitle: string;
    public fromDateTz: DateTZ = closestToTimeSlotTz(new DateTZ(), 15);
    public available: boolean = null;
    public formGroup = new FormGroup({
        dateTz: new FormControl(this.fromDateTz, [Validators.required]),
        duration: new FormControl(DEFAULT_BOOKING_DURATION, [Validators.required])
    });
    public localTimezone = "";
    public buildingTimezone = "";
    public localStart = "";
    public localEnd = ""

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: ModalData,
        private dialogRef: MatDialogRef<RoomAvailabilityPreviewModalComponent>,
        private _spaces: SpacesService,
    ) {}

    public get pending_date(){
        return this.data.pendingDateTz; // new DateTZ({ date: this.data.pendingDate.valueOf(), is_local_tz: false, building_tz: this.data?.space?.timezone })
    }

    public ngOnInit(): void {
        console.log('Here 1');
        this.formGroup.setValue({
            dateTz: this.data.pendingDateTz,
            duration: this.data.pendingDuration,
        });
        console.log('Here 2');
        this.updateDisplay();
    }

    // Method run when the confirming the modal.
    public applyChanges(changes: any): void {
        this.data.pendingDateTz = changes.pendingDateTz; // new Date(changes.pendingDate.valueOf());
        this.data.pendingDuration = changes.pendingDuration;
        this.updateDisplay();
    }

    public updateDisplay() {
        // this.data.dateTz is the existing time in building time
        // this.data.pendingDateTz is the new time in building time
        const dateTz = this.data.dateTz;
        this.dateString = dateBuildingFormatString(dateTz);
        this.timeString = startEndTimeWithZoneFormatString(dateTz, dateTz.addValue({ minutes: this.data.duration }));
        this.durationString = humaniseDuration(this.data.duration);

        const pendingDateTz = this.data.pendingDateTz;
        this.pendingDateString = dateBuildingFormatString(pendingDateTz);
        this.pendingTimeString = startEndTimeWithZoneFormatString(pendingDateTz, pendingDateTz.addValue({ minutes: this.data.pendingDuration }));
        this.pendingDurationString = humaniseDuration(this.data.pendingDuration);
        this.fromDateTz = closestToTimeSlotTz(pendingDateTz, 15); // building value

        const setup = {};
        setup[this.data.space.email] = this.data?.currentBooking?.setup[this.data?.space?.email] || 0;
        const breakdown = {};
        breakdown[this.data.space.email] = this.data?.currentBooking?.setup[this.data?.space?.email] || 0;

        this.updateCoachmarkTimes(this.data?.space?.timezone);

        const mockBooking = new Booking({
            id: this.data.bookingId,
            start_epoch: this.data.pendingDateTz.seconds,
            end_epoch: this.data.pendingDateTz.addValue({ minutes: this.data.pendingDuration }).seconds,
            room: this.data.space,
            setup,
            breakdown, 
        });

        checkCollisions(
            this._spaces,
            mockBooking
        ).then((bookable: boolean) => {
            this.available = bookable;
        }).catch(() => {
            this.available = false;
        });
    }

    public closeModal() {
        this.dialogRef.close(null);
    }

    public updateDatePickerValue(dateTz: DateTZ) {
        const pending_date: DateTZ = this.formGroup.value.dateTz
        const day = pending_date.setValue({ 
            day: dateTz.dateDay,
            month: dateTz.dateMonth,
            year: dateTz.dateYear
         });
        this.setValue(day, this.formGroup.controls.dateTz);
    }

    public setValue($value, formControl: AbstractControl) {
        // valid options are in building time
        formControl.setValue($value); // bulding time
        this.submitForm.emit(this.formGroup.value);
    }

    public confirm() {
        const setup = {};
        setup[this.data.space.email] = this.data?.currentBooking?.setup[this.data?.space?.email] || 0;
        const breakdown = {};
        breakdown[this.data.space.email] = this.data?.currentBooking?.setup[this.data?.space?.email] || 0;

        const buildingDate = convertLocalTimestampToTimezonedDateTz(this.data.pendingDateTz.ms, this.data?.currentBooking?.timezone);
        const mockBooking = new Booking({
            id: this.data.bookingId,
            start_epoch: buildingDate.seconds,
            end_epoch: buildingDate.addValue({ minutes: this.data.pendingDuration }),
            room: this.data.space,
            setup,
            breakdown, 
        });

        checkCollisions(
            this._spaces,
            mockBooking
        ).then((bookable: boolean) => {
            this.available = bookable;
            this.dialogRef.close({
                ...this.formGroup.value,
            });            
        }).catch(() => {
            this.available = false;
        });
    }

    private updateCoachmarkTimes(timezone: string) {
        if (timezone) {
            this.buildingTimezone = timezone;
            const localizedStartDate = this.formGroup.value.dateTz;
            const localizedEndDate = localizedStartDate.addValue({ minutes: this.formGroup.value.duration });
            this.localStart = localizedStartDate.formatLocalDate("h:mma");
            this.localEnd = localizedEndDate.formatLocalDate("h:mma");
        } else {
            this.buildingTimezone = this.localTimezone.slice(0);
            this.localStart = "";
            this.localEnd = "";
        } 
    }
}
