import {
    Component,
    OnInit,
    ViewEncapsulation
} from '@angular/core';
import { ActivatedRoute, GuardsCheckEnd, NavigationEnd, NavigationStart, Router } from '@angular/router';
import { BaseDirective } from '@mckinsey-converge/base';
import {
    ApplicationService,
    BookingFormData,
    BookingsService,
    Building,
    OrganisationService,
    SettingsService,
    SpacesService,
    selectActiveForm
} from '@mckinsey-converge/data-common';
import { Store, select } from '@ngrx/store';
import { distinctUntilChanged, first } from 'rxjs/operators';

import { RoutePayload, TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import { RoomResultsState } from '../room-details/room-details.component';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'booking-calendar-wrapper',
    templateUrl: './booking-calendar-wrapper.html',
    styleUrls: ['./booking-calendar.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class BookingCalendarWrapperComponent extends BaseDirective implements OnInit {
    public TopBarHeaderMode = TopBarHeaderMode;

    public backRoute: RoutePayload = {
        route: ['/home', 'results'],
        queryParams: { display: 'list' },
    };
    orgBuildings: Building[] = [];
    
    constructor(
        private _org: OrganisationService,
        private _spaces: SpacesService,
        private _bookings: BookingsService,
        private settingService: SettingsService,
        private store: Store<RoomResultsState>,
    ) {
        super();

        
    }

    public ngOnInit(): void {
        /** allow booking bypass to receive bookings for staff calendar view only */
        this.settingService.bypass_bookings = true;

        this.orgBuildings = this._org.buildings;

        this.subscription('activeForm', this.store.pipe(
            select(selectActiveForm),
            distinctUntilChanged()
          ).subscribe((next: BookingFormData | undefined) => {
            if (next && next?.dateTz) {
                localStorage.setItem('CONCIERGE.date', `${next.dateTz.ms}`);
                localStorage.setItem('CONCIERGE.timezone', `${next.timezone}`);
              const building = this._org.buildings.find(
                 (_) => _.id === next.location.filter(el => el.groupChild)[0]?.meta?.building?.id
                );
                if(building) {
                    this._org.building = building;
                }
                
            }
          }));

          this._org.initialised.pipe(first((_) => _)).subscribe(() => {
            this.orgBuildings = this._org.buildings;
          })

          
    }

    public onBackCallback() {}

    public ngOnDestroy(): void {
         /** disallowed booking bypass when you are moving from staff app calendar view*/
         this.settingService.bypass_bookings = false;
    }

}
