import { Component, Inject, ViewEncapsulation } from '@angular/core';
import {
    dateBuildingFormatString,
    startEndTimeWithZoneFormatString,
} from '@mckinsey-converge/base';
import { Booking, Building, RecurrencePeriod } from '@mckinsey-converge/data-common';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { BaseClass } from '@mckinsey-converge/base';
import { DateTZ } from '@mckinsey-converge/date-tz';

interface data {
    booking: Booking;
    building: Building;
    callback: Function;
    master_booking?: Booking;
    hasLoaderUrl?: boolean;
    refresh?: Function;
		series?:{};
}

@Component({
    selector: 'app-cancel-booking-modal',
    template: `
        <mat-dialog-content class="cancel-dialog-content">
            <div class="button-row" *ngIf="!canceled">
                <button name="CloseButton" mat-icon-button (click)="closeModal()">
                    <div class="close-image"></div>
                </button>
            </div>
            <div class="mck-column modal-heading" [class.canceled]="canceled">
                <img src="assets/icon/stop.svg" />
                <div class="info">{{ subtitle }}</div>
            </div>
            <div class="mck-column modal-body" *ngIf="!canceled">
                <p>{{ data.booking.title }}</p>

                <div
                    class="banner-info-txt flex-column"
                    [ngSwitch]="recurrence_period"
                    *ngIf="data?.series"
                >
                    <p *ngIf="recurrence_period !== 'list'">
                        <ng-template [ngSwitchCase]="'daily'"> Daily from </ng-template>
                        <ng-template [ngSwitchCase]="'weekly'">
                            <p>
                                weekly on {{ data?.master_booking?.recurrence_days[0] | titlecase }}
                            </p>
                            <p>from</p>
                        </ng-template>
                        <ng-template [ngSwitchCase]="'monthly'"> Monthly on </ng-template>

                        {{ data?.master_booking?.dateString }} until
                        {{ data?.master_booking?.endDateString }}
                    </p>
                    <ng-template ngSwitchDefault>
                        <!-- This is one occurrence in a recurring series which occurs on<button name="specificdates" type="button" (click)="toggleSpecificDates()">Specific Dates.</button> -->
                        <span>Specific Dates: </span>
                        <div class="hint">
                            <div class="hint-wrapper flex-column">
                                <div class="flex-row">Date</div>
                                <div class="flex-column">
                                    <div *ngFor="let occurrence of occurrence_details" class="flex-row">
                                        {{ occurrence.dateTz.formatDate('dd MMM yyyy') }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </ng-template>
                </div>

                <p *ngIf="!data?.series">{{ dateString }}</p>
                <p>{{ timeString }}</p>
                <p>{{ buildingAddress }}</p>
                <p>{{ data.booking.room.local_room_name }}</p>
            </div>
            <div class="mck-column modal-body-footer" *ngIf="!canceled">
                <mck-modal-button-row
                    [disableFixed]="true"
                    primaryLabel="Yes"
                    (primarySubmit)="cancelBooking()"
                    secondaryLabel="No"
                    (secondarySubmit)="closeModal()"
                ></mck-modal-button-row>
            </div>
        </mat-dialog-content>
    `,
    styleUrls: ['./cancel-booking-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class CancelBookingModalComponent extends BaseClass {
    public title: string;
    public roomName: string;
    public buildingAddress: string;
    public dateString: string;
    public timeString: string;
    public canceled = false;
    private _specific_dates = false;
    private recurrence_period: RecurrencePeriod;
    public occurrence_details: { start_epoch: number; dateTz: DateTZ; id: string }[];
    private recurrence_interval: number;

    public get subtitle() {
        return this.canceled
            ? `${this.title} booking cancelled`
            : this.data?.series
            ? 'Are you sure you want to cancel this recurring series?'
            : 'Are you sure you want to cancel this booking?';
    }

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: data,
        private dialogRef: MatDialogRef<CancelBookingModalComponent>
    ) {
        super();
        this.dateString = dateBuildingFormatString(this.data.booking.startDateTz, true);
        this.timeString = startEndTimeWithZoneFormatString(this.data.booking.startDateTz, this.data.booking.displayEndDateTz);
        this.title = this.data.booking.title;
        this.buildingAddress = this.data.booking.room.street_and_city;


				if(this.data?.master_booking !== null) {

					this.recurrence_period = this.data?.master_booking?.recurrence_period;
					this.recurrence_interval = this.data?.master_booking?.recurrence_interval;

					this.occurrence_details = this.data?.master_booking?.occurrence_details &&
						this.data?.master_booking?.occurrence_details
							.filter((ocurrence) => ocurrence.id !== this.data?.master_booking?.id)
							.map((ocurrence) => ({
								...ocurrence,
								start_epoch: ocurrence.start_epoch * 1000,
								dateTz: new DateTZ({
									date: ocurrence.start_epoch * 1000,
									is_local_tz: false,
									building_tz: this.data.master_booking.timezone,
								}),
							}));
				}

    }

    public closeModal() {
        this.dialogRef.close();
    }

    public cancelBooking() {
        this.canceled = true;
        this.data.callback();
        this.timeout('closeModal', () => this.closeModal(), 2000);
    }

    public set specific_dates(val: boolean) {
        this._specific_dates = val;
    }

    public get specific_dates(): boolean {
        return this._specific_dates;
    }

    public toggleSpecificDates() {
        this.specific_dates = !this.specific_dates;
    }
}
