import { SelectOption } from '@mckinsey-converge/ui';
import {
    Space,
    SpaceStatus,
    RoomResultsViewModel,
    MapListener
} from '@mckinsey-converge/data-common';
import {
    HashMap,
    Point
} from '@mckinsey-converge/base';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { MapOverlayComponent } from 'src/app/map/map-overlay/map-overlay.component';

export const MIN_ZOOM_LEVEL = 1;
export const MAX_ZOOM_LEVEL = 10;
export const CENTER_POINT: Point = { x: 0.5, y: 0.5 };

const mapSpaceStatusToDisplay = (status: SpaceStatus): string => {
    switch (status) {
        case SpaceStatus.NotBookable:
            return 'Not Bookable';
        case SpaceStatus.Requestable:
            return 'Available by Request';
        case SpaceStatus.Unavailable:
            return 'In Use';
        case SpaceStatus.Available:
        default:
            return 'Available';
    }
};

const mapSpaceStatusToOption = (status: SpaceStatus) => ({
    value: status,
    display: mapSpaceStatusToDisplay(status)
});

export const legendOptions = (): SelectOption<any>[] => [
    SpaceStatus.Available,
    SpaceStatus.Requestable,
    SpaceStatus.Unavailable,
    SpaceStatus.NotBookable
].map(mapSpaceStatusToOption);


export const defaultMapBookingRules = [
    'Rooms showing yellow are restricted and must be requested',
    'Rooms showing green are available to book for one hour',
    'Rooms showing red are booked',
    'Rooms showing grey are not bookable but may be used if they are empty'
];

export const defaultMapBookingSubtitle = 'You may request restricted rooms but they will need to be approved by an admin';

export const generateMapStyleForSpace = (
    space: Space,
    status: SpaceStatus,
    colorStatusMap: HashMap) => {
    const id =
        `${space.map_id}`.indexOf('area') < 0
            ? `area-${space.map_id}-status`
            : space.map_id;
    return (
        {
            id,
            styles: {
                fill: colorStatusMap[`space-${status}`] || '#000',
                opacity: 0.99
            }
        });
};

/**
 * Generates actions for mapview handling.
 * @param space - the space to generate
 * @param clickSpace - called when clicked or touched
 * @param hoverSpace - called when you hover over a space
 * @param leaveSpace - called when you leave a space.
 */
export const generateMapListenersForSpace = (
    space: Space,
    hoverSpace: () => void,
    leaveSpace: () => void,
    clickSpace: () => void
): MapListener[] => {
    const id =
        `${space.map_id}`.indexOf('area') < 0
            ? `area-${space.map_id}-status`
            : space.map_id;
    const listeners: MapListener[] = [];
    listeners.push({
        id,
        event: 'mouseenter',
        callback: hoverSpace
    });
    listeners.push({
        id,
        event: 'mouseleave',
        callback: leaveSpace
    });
    if (space.bookable) {
        listeners.push({
            id,
            event: 'click',
            callback: clickSpace
        });
        listeners.push({
            id,
            event: 'touchend',
            callback: clickSpace
        });
    }
    return listeners;
};


export const spaceToMapFeature = (space: Space): MapFeature => ({
    id: `area-${space.map_id}-status`,
    coordinates: null,
    content: MapOverlayComponent,
    data: {
        ...space
    }
});

export const roomToMapFeature = (room: RoomResultsViewModel, parentComponent?: any, status?: string): MapFeature => ({
    id: `area-${room.space.map_id}-status`,
    coordinates: null,
    content: MapOverlayComponent,
    data: {
        ...room.space,
        parentComponent,
        room,
        status:status
    }
});