import {
    combineLatest,
    Observable
} from 'rxjs';
import { map } from 'rxjs/operators';
import { LoadingModel } from '@mckinsey-converge/loading';

/**
 * Generates the results count based on if data has loaded or not. If not, undefined is returned.
 * Otherwise, it'll return the count of the results.
 */
export const resultCountObservable = <T>(loadingObservable: Observable<LoadingModel<T>>,
                                         resultsObservable: Observable<any[] | undefined>
): Observable<number | undefined> => combineLatest([
    loadingObservable.pipe(map(r => r.hasLoaded())),
    resultsObservable.pipe(map(v => v?.length))
]).pipe(map(([isLoading, viewModelLength]) => isLoading ? viewModelLength : undefined));
