import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BookingCalendarWrapperComponent } from './booking-calendar-wrapper.component';


const routes: Routes = [
    { path: '', component: BookingCalendarWrapperComponent },
    { path: ':level', component: BookingCalendarWrapperComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class BookingCalendarRoutingModule {}
