import {
    Component,
    Input,
    Output,
    EventEmitter
} from '@angular/core';
import {
    Router
} from '@angular/router';
import {
    BaseDirective,
} from '@mckinsey-converge/base';
import {
    generateFeedbackUrl,
    Urls
} from '../../utilities/urls';
import {
    Store
} from '@ngrx/store';
import {
    Booking,
    Building,
    BookingStoreState,
    clearBookingFormData,
    cateringAllowed,
    openBookingSurvey,
    roomHasCateringHours
} from '@mckinsey-converge/data-common';

export enum TopBarHeaderMode {
    Light = 'light',
    Dark = 'dark',
}

export interface statusObject {
    text: string,
    title: string,
    image: string,
}

@Component({
    selector: 'booking-confirmation',
    template: `
        <div class="booking-confirmation mck-row" *ngIf="confirmed || loading">
            <header class="header" *ngIf="hasData()">
                <a-topbar-header
                    [mode]="mode"
                    [backModal]="true"
                    [noSurvey]="true"
                    (closeCallback)="returnHome()">
                </a-topbar-header>
            </header>
            <div class="confirm-wrapper">
            <div class="mck-column mck-center results-container mck-scroll">
                <div class="mck-column mck-scroll mck-content-expand-width ">
                    <div class="content mck-column mck-center mck-content-body">
                        <div class="section" [class.skeleton]="!hasData()">
                            <ng-container *ngIf="hasData()">
                              <h1>{{  data?.isEditSeries || (booking.is_recurrent && booking.recurrence_type === 'master') ? 'Recurring Series' : 'Booking' }}  {{data?.status.title}}</h1>
                            </ng-container>
                            <ng-container *ngIf="!hasData()">
                              <ngx-skeleton-loader class="mck-skeleton-line-1 header"></ngx-skeleton-loader>
                            </ng-container>
                            <div>
                                <div>
                                    <div class="title" *ngIf="hasData()">
                                      <h2 *ngIf="!isUnavailable()">{{ booking.title }}</h2>
                                      <h2 *ngIf="isUnavailable()">Room is no longer available for your chosen date and time</h2>
                                    </div>
                                    <ng-container *ngIf="hasData()">
                                      <div class="booking-data" *ngIf="!data?.isEditSeries && data.booking.recurrence_type !== 'master' ">
                                        <div class="pb" *ngIf="!isUnavailable()">Status:  {{data?.status.text}}</div>
                                        <div>Date: {{data?.dateString}}</div>
                                        <div>Time: {{data?.timeString}}</div>
                                        <div>{{data?.typeString}} meeting ({{data?.durationString}})</div>
                                      </div>
                                      <div class="booking-data"  *ngIf="data?.isEditSeries || data.booking.recurrence_type === 'master' ">
																				<div class="rec-details-period">{{booking?.recurrence_period === 'list' ? 'Specific dates' : booking?.recurrence_period | titlecase}}
																					<span *ngIf="data?.isConflictWithOccurences()?.is_conflict" (click)="data?.openRecurrenceConflictsModal(booking?.room)"> - Conflicts({{data?.isConflictWithOccurences()?.total_Conflicts}}) </span>
																				</div>
																					<div>Start date: {{data?.dateString}}</div>
																					<div>End date: {{data?.endDateString}}</div>
																					<div>Time: {{data?.timeString}}</div>
																					<div>{{data?.type}} meeting ({{data?.durationString}})</div>
																			</div>

                                    </ng-container>
                                    <ng-container *ngIf="!hasData()">
                                        <div class="skeleton">
                                          <div><ngx-skeleton-loader class="mck-skeleton-line-1"></ngx-skeleton-loader></div>
                                          <div><ngx-skeleton-loader class="mck-skeleton-line-50p-thin"></ngx-skeleton-loader></div>
                                          <div class="gap-a"></div>
                                          <div>
                                            <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                            <ngx-skeleton-loader class="mck-skeleton-line-90p-thin"></ngx-skeleton-loader>
                                            <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                        </div>
                                        </div>
                                    </ng-container>
                                    <div class="booking-data">
                                        <ng-container *ngIf="hasData()">
                                            <div>
                                                Address: {{building.address}},
                                                {{building.city}}
                                            </div>
                                            <div>Room: {{booking.space.local_name}}</div>
                                            <div>Room Capacity: {{booking.space.capacity}}</div>
                                        </ng-container>
                                        <ng-container *ngIf="!hasData()">
                                            <div class="gap-b"></div>
                                            <ngx-skeleton-loader class="mck-skeleton-line-90p-thin"></ngx-skeleton-loader>
                                            <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                            <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                            <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                        </ng-container>
                                    </div>
                                </div>
                                <div class="status {{ getStatusColor(data?.status.text.toLowerCase()) }}" [class.skeleton]="!hasData()">
                                    <ng-container *ngIf="hasData()">
                                        <div>
                                            <img [src]="data?.status.image" />
                                            <p>{{ data?.status.text }}</p>
                                        </div>
                                    </ng-container>
                                    <ng-container *ngIf="!hasData()">
                                        <div>
                                            <ngx-skeleton-loader class="mck-skeleton-line-1"></ngx-skeleton-loader>
                                            <p>
                                              <ngx-skeleton-loader class="mck-skeleton-line-1"></ngx-skeleton-loader>
                                            </p>
                                        </div>
                                    </ng-container>
                                </div>
                            </div>
                        </div>
                        <div class="details" [class.skeleton]="!hasData()">
                            <div *ngIf="!isUnavailable()">
                                <div class='booking-data'>
                                    <ng-container *ngIf="hasData()">
                                      <p><label>Host:</label> {{booking.organiser.name}}</p>
                                      <p><label>Attendees:</label> {{attendees}}</p>
                                      <p *ngIf="company"><label>Company:</label> {{company}}</p>
                                      <p><label>Charge Code:</label> {{charge_code}}</p>
                                      <p><label>Headcount Estimate:</label> {{headcount}}</p>
                                      <p><label>Additional Requirements:</label> {{notes}}</p>
                                      <p *ngIf="data.booking.opt_out"><label>Email notification for this booking has been opted out.</label></p>
                                    </ng-container>
                                    <ng-container *ngIf="!hasData()">
                                      <p><ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader></p>
                                      <p><ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader></p>
                                      <p><ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader></p>
                                      <p><ngx-skeleton-loader class="mck-skeleton-line-50p-thin"></ngx-skeleton-loader></p>
                                      <p><ngx-skeleton-loader class="mck-skeleton-line-50p-thin"></ngx-skeleton-loader></p>
                                      <p>
                                        <ngx-skeleton-loader class="mck-skeleton-line-60p-thin"></ngx-skeleton-loader>
                                        <ngx-skeleton-loader class="mck-skeleton-line-90p-thin"></ngx-skeleton-loader>
                                        <ngx-skeleton-loader class="mck-skeleton-line-50p-thin"></ngx-skeleton-loader>
                                      </p>
                                    </ng-container>
                                </div>
                                <div class="catering" *ngIf="roomHasCatering && hasData()" [class.skeleton]="!hasData()">
                                    <div>
                                      <h2><img src="assets/icon/catering.svg"> Catering is available for this meeting room</h2>
                                      <button
                                          mat-stroked-button
                                          color="primary"
                                          class="mck-stroked-button mck-short mck-column mck-center mck-primary"
                                          [disabled]="disableCateringButton"
                                          (click)="startCatering()"
                                      ><span>Add catering</span></button>

                                      <catering-order-table [booking]="booking"></catering-order-table>
                                  </div>
                                </div>
                                <div class="catering skeleton" *ngIf="!hasData()">
                                  <div>
                                    <ngx-skeleton-loader class="mck-skeleton-checkbox"></ngx-skeleton-loader>
                                    <h2><ngx-skeleton-loader class="mck-skeleton-line-70p-thin"></ngx-skeleton-loader></h2>
                                    <h2><ngx-skeleton-loader class="mck-skeleton-line-70p-thin"></ngx-skeleton-loader></h2>
                                    <p>&nbsp;</p>
                                    <ngx-skeleton-loader class="mck-skeleton-load-more-100p"></ngx-skeleton-loader>
                                  </div>

                                </div>
                            </div>
                            <div *ngIf="isUnavailable()" class='unavailable-details'>
                                <h2>Ooops - we're sorry!</h2>
                                <p>Unfortunately your selected room has been booked by another user</p>
                                <p>Please try searching for another room.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="footer mck-column mck-center">
                    <ng-container *ngIf="hasData()">
                        <div>
                            <button mat-button color="primary" class="mck-primary-button" (click)="returnHome()">{{ !isUnavailable() ? 'Close' : 'Back to results' }}</button>
                        </div>
                    </ng-container>
                    <ng-container *ngIf="!hasData()">
                      <div class="skeleton">
                          <ngx-skeleton-loader class="mck-skeleton-load-more"></ngx-skeleton-loader>
                      </div>
                    </ng-container>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./booking-confirmation.component.scss']
})
export class BookingConfirmationComponent extends BaseDirective {
    /** The theme to use **/
    @Input() public mode: TopBarHeaderMode = TopBarHeaderMode.Light;

    @Input() public confirmed: boolean;
    @Input() public loading: boolean;

    @Input() public modal: boolean;
		@Input('dataForConfirmation') public data: any; // consolidated data using for confirmation template

    /** Emitter for close events */
    @Output() public callback = new EventEmitter<void>();

    public currentPage: string;

		public get booking(): Booking {
			return this.data?.booking
		};


    public get building(): Building {
        return this.data?.booking.room.building;
    };

    public get disableCateringButton(): boolean {
      return !cateringAllowed(this.data?.booking, this.building);
    }

    public get charge_code() {
        return (this.data?.booking.equipment_codes || {})[
            this.data?.booking.room.email
        ] || 'No Charge Code'
    }

    public get headcount() {
        return (this.data?.booking.expected_attendees || {})[
            this.data?.booking.room.email
        ] || 0
    }

    public get attendees() {
        const a = Array.isArray(this.data?.booking.attendees) ? this.data?.booking.attendees.map(u => u?.name ? u.name : '') : [];
        return a.join(', ');
    }

    public get company() {
        return this.data?.booking.company.join(', ');
    }

    public get notes() {
        const note = this.data?.booking.notes.find(n => n.type === 'equipment');
        return note ? note.message : '';
    }

    public get feedback() {
        return generateFeedbackUrl(this.currentPage);
    }

    public get roomHasCatering() {
        return roomHasCateringHours(this.building, this.data?.booking?.space) && cateringAllowed(this.data?.booking, this.building);
    }

    public listItems(order) {
        return order.items.map(i => `(${i.amount}) ${i.name}`).join(', ');
    }

    public formatDeliveryTime(order) {
        return this.data?.booking.startDateTz.addValue({ minutes: order?.delivery_time }).formatDate("h:mma");
    }

    constructor(
        private store: Store<BookingStoreState>,
        private _router: Router,
    ) {
        super();
    }

    public getStatusColor (statusText: string): string {
        switch (statusText) {
            case 'confirmed':
                return 'approved'
            case 'unavailable':
                return 'cancelled'
            default:
                return statusText;
        }
    }

    public hasData() {
        return (this.confirmed && !this.loading) || this.isUnavailable();
    }

    public goToHelp() {
        window.open(Urls.Help);
    }

    public async returnHome() {
        
        if (this.modal) {
            this.confirmed = false;
            this.loading = false;
            this.callback.emit();
        } else {
            if(!this.isUnavailable()){
                this.store.dispatch(openBookingSurvey());
                // Clear everything out
                this.store.dispatch(clearBookingFormData());
                this._router.navigate(['/', 'home']);
            } else {
                await this._router.navigate(['/home', 'results'], { queryParams: { display: 'list' }});
            }
        }
    }

    public async startCatering() {
        this.store.dispatch(clearBookingFormData());
        this.confirmed = false;
        this.loading = false;
        this.callback.emit();
        await this._router.navigate(['/', 'home', 'catering', this.data?.booking.id]);
    }

    public isUnavailable() {
        return this.data?.status.text.toLowerCase() === 'unavailable'
    }
}
