import {
    Component,
    Inject,
    OnInit,
    ViewEncapsulation,
} from '@angular/core';
import {
    MatDialogRef,
    MAT_DIALOG_DATA
} from '@angular/material/dialog';
import {
    BaseClass
} from '@mckinsey-converge/base';
import { ComposerService } from '@placeos/composer';
import { first } from 'rxjs/operators';

@Component({
    selector: 'a-survey-modal',
    templateUrl: './survey-modal.component.html',
    styleUrls: ['./survey-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class SurveyModalComponent extends BaseClass implements OnInit {
    constructor(
        @Inject(MAT_DIALOG_DATA) public _data: any,
        private _composer: ComposerService,
        private _dialog_ref: MatDialogRef<SurveyModalComponent>,
    ) {
        super();
    }

    /* present experience question by default */
    public token = '';
    public phase = 'intro';
    public baseUrl = '';

    public questions = {
        created_at: 0,
        updated_at: 0,
        header_question: "What went wrong?",
        header_subtext: "Select one or several options",
        options: [],
        id: ""
    }

    public answers = {
        questions_id: "",
        positive: true, // Experience is positive until the questions are displayed
        option_responses: [],
        comments: ""
    };

    // Show the questions and mark the experience negative. (false positive)
    public showQuestions(): void {
        this.answers.positive = false;
        this.phase = 'questions';
    }

    public ngOnInit(): void {
        /* fetch the question options once composer is initialized */
        this._composer.initialised.pipe(first(_ => _)).subscribe(() => {
            const token = this._composer.auth.token;
            this.token = token;
            this.baseUrl = this._composer.auth.api_endpoint.replace(this._composer.auth.route, '/api/staff/feedback')
            this.getFeedbackQuestionsFromApi(token, this._data)
        });
    }

    public getFeedbackQuestionsFromApi(token: string, questionID: string) {
        new Promise<any>((resolve, reject) => {
            const url = `${this.baseUrl}/questions/${questionID}`;
            let result: any = null;
            this._composer.http.get(url).subscribe(
                (d) => (result = d),
                (e) => {
                    reject(e);
                },
                () => {
                    resolve(result);
                }
            );
        }).then(d => {
            this.questions = d;
            this.answers = {
                questions_id: this.questions.id,
                positive: true,
                option_responses: this.questions.options.map(q => ({ // map the questions to default answers.
                    id: q.id,
                    checked: false,
                })),
                comments: ""
            };
        }).catch(e => {
            console.log('error', e)
        });
    }

    // Handle the checkbox changes
    public handleChange(value: any) {
        this.answers.option_responses.some((o, i) => {
            if (o.id === value.id) {
                this.answers.option_responses[i].checked = !this.answers.option_responses[i].checked;
                return true;
            }
        });
    }

    // Handle the comment input changes
    public handleComment($evemt: any) {
        this.answers.comments = $evemt.target.value;
    }

    // Submit the Survey in it's current state.
    public confirm() {
        new Promise<any>((resolve, reject) => {
            let result: any = null;
            this._composer.http.post(this.baseUrl, this.answers).subscribe(
                (d) => (result = d),
                (e) => {
                    reject(e);
                },
                () => {
                    resolve(result);
                }
            );
        }).then(d => {
            this.phase = 'exit';
            this.closeModal(true);
        }).catch(e => {
            console.log('error', e)
            this.phase = 'exit';
            this.closeModal(true);
        });
        
    }

    // Close the modal
    public closeModal(delayed: boolean = false) {
        if (delayed) {
            this.timeout('closeModal', () => {
                this._dialog_ref.close();
            }, 2000);
        } else {
            this._dialog_ref.close();
        }
    }
}
