import { LoadingModel } from '../../../loading/src/lib/loading.model';
import {
    Building,
    DaysOfWeek,
    ISpaceAvailabilityOptions,
    RecurrencePeriod,
    Space
} from '@mckinsey-converge/data-common';
import { DateTZ } from '@mckinsey-converge/date-tz';

export interface RoomStoreState {
    rooms: RoomState;
}

export interface RoomState {
    query: LoadingModel<Space[]>
    later: LoadingModel<Space[]>
    next: LoadingModel<Space[]>
    byid: LoadingModel<Space[]>
    size: LoadingModel<Space[]>
}

export interface RoomQueryOptions {
    dateTz: DateTZ;
    duration: number;
    locations: string;
    capacity?: number;
    capacity_max?: number;
    diff_capacity?: boolean;
   //Recurrence fields
   recurrence_period?: RecurrencePeriod;
   recurrence_interval?: number;
   recurrence_endTz?: DateTZ;
   recurrence_count?: number;
   recurrence_starts?: Array<DateTZ>;
   recurrence_exceptions?: Array<number>; //SHOULD BE DATETZ
   recurrence_days?: Array<DaysOfWeek>;
   timezone?: string;
   offset?: number;
   buildings?: Building[];
   is_recurrent?: boolean;
   is_multiroom?: boolean;
   bookable?: boolean;
   ignore?: string;
}

export const roomQueryOptionsToSpaceAvailabilityOptions = (options: RoomQueryOptions): ISpaceAvailabilityOptions => ({
    dateTz: options.dateTz,
    duration: options.duration,
    capacity: options.capacity,
    zone_ids: options.locations,
    capacity_max: options.capacity_max,
    diff_capacity: options.diff_capacity,
    /** Fields to include recurrence in the availability search */
    is_recurrent: options?.is_recurrent,
    is_multiroom: options?.is_multiroom,
    recurrence_period: options?.recurrence_period,
    recurrence_interval: options?.recurrence_interval,
    recurrence_endTz: options?.recurrence_endTz,
    recurrence_count: options?.recurrence_count,
    recurrence_starts: options?.recurrence_starts,
    recurrence_days: options?.recurrence_days,
    bookable: options?.bookable,
    ignore: options?.ignore
});

export const roomByIdQueryOptionsToSpaceAvailabilityOptions = (options: RoomQueryOptions): ISpaceAvailabilityOptions => ({
    dateTz: options.dateTz,
    duration: options.duration,
    capacity: options.capacity,
    room_ids: options.locations,
    capacity_max: options.capacity_max
});
