import {
    Component,
    Inject,
    OnInit,
    ViewEncapsulation,
} from '@angular/core';
import {
    Space,
    RoomResultsViewModel,
} from '@mckinsey-converge/data-common';
import { MAP_OVERLAY_DATA } from '@acaprojects/ngx-interactive-map';


interface SpaceWithReturnData extends Space {
    // callback?: Function,
    room?: RoomResultsViewModel,
    parentComponent?: any,
}
/**
 * Replaces the built-in {@link MapPinComponent} based on design.
 */
@Component({
    selector: 'app-map-overlay',
    template: `
        <div class="mck-column mck-center map-overlay-container">
                
                <div class="map-label">
                    <div class="close-outlet flex-row flex-row-end">
                        <div class="flex-row"> 
                            <button mat-icon-button matSuffix (click)="closeOverlay()" (touchend)="closeOverlay()" class="flex-row-end">

                                <div class="app-icon">
                                    <i class="material-icons">close</i>
                                </div>

                            </button> 
                        </div>
                    </div>
                    <div class="flex-column inner-box" >
                        <span>{{data.simple_name || 'No Label'}}</span>
                        <button mat-stroked-button color="primary" *ngIf="data.parentComponent"
                            class="mck-stroked-button mck-short mck-column mck-center mck-primary"
                            (click)="roomDataCallback()"
                            (touchend)="roomDataCallback()">
                            View room detail
                        </button>
                        
                        <div class="status" *ngIf="is_recurrent">{{ data.status ===  'requestable' ? 'Requestable' : 'Bookable'}} {{ data?.room?.space?.availableOccurrences }} out of {{ data?.room?.space?.totalOccurrences }} </div>
                    </div> 
                </div>
            <div class="map-icon"></div>

        </div>
    `,
    styleUrls: ['./map-overlay.component.scss'],
    encapsulation: ViewEncapsulation.Emulated
})
export class MapOverlayComponent implements OnInit  {
    constructor(
        @Inject(MAP_OVERLAY_DATA) public data: SpaceWithReturnData,
        ) {
    }
    public ngOnInit(): void {
    }


    public roomDataCallback() {
        if (this.data.parentComponent.roomSelected && this.data.room) {
            this.data.parentComponent.roomSelected(this.data.room)
        }
    }

    public get is_recurrent () {
       return this.data.parentComponent?.bookingFormData?.value?.is_recurrent || false;
    }

    public closeOverlay() {
        this.data.parentComponent?.closeMapOverlayOutlet();
    }
}
