import {
    Component,
    EventEmitter,
    Input,
    Output,
    OnInit,
    OnChanges,
    SimpleChanges,
} from '@angular/core';
import { BookingFormData, RoomResultsViewModel, setupRoomImages, User } from '@mckinsey-converge/data-common';
import { ImageDirective, SpaceImageObject } from '@mckinsey-converge/base';

@Component({
    selector: 'app-room-result-item',
    template: `
        <div class="mck-row result-container" mat-ripple (click)="roomSelect.emit(viewModel)"
            [ngClass]="{ 'result-container-bookable': availability === 'bookable' && hasData(), 'result-container-requestable': availability === 'requestable' && hasData() }">
            <div>
                <div class="mck-row">
                    <div class="room-request-label" *ngIf="hasData()" [ngClass]="{ 'bookable': availability === 'bookable', 'requestable': availability ===  'requestable' }">
                        {{ availability ===  'requestable' ? 'Requestable' : 'Bookable'}}
                        <span *ngIf="data.is_recurrent">{{ viewModel?.space?.availableOccurrences }} out of {{ viewModel?.space?.totalOccurrences }} </span>
                    </div>
                    <div class="mck-column mck-center placeholder-image">
                        <div class="desktop-cell" *ngIf="hasData()"
                        [ngStyle]="{
                            'background-image': showLabel(foundImages[0].path) ? 'linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 22%, rgba(0,0,0,0) 100%), url(' + foundImages[0].path + ')' : 'url(' + foundImages[0].path +')'
                        }">
                            <span *ngIf="showLabel(foundImages[0].path)">Images Coming Soon</span>
                        </div>
                        <ngx-skeleton-loader
                            class="mck-skeleton-image"
                            *ngIf="!hasData()"></ngx-skeleton-loader>
                    </div>
                    <div class="mck-column mck-center placeholder-image placeholder-image-desktop">
                        <div class="desktop-cell" *ngIf="hasData()"
                            [ngStyle]="{
                            'background-image': showLabel(foundImages[1].path) ? 'linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 22%, rgba(0,0,0,0) 100%), url(' + foundImages[1].path + ')' : 'url(' + foundImages[1].path +')'
                        }">
                            <span *ngIf="showLabel(foundImages[1].path)">Images Coming Soon</span>
                        </div>
                        <ngx-skeleton-loader
                            class="mck-skeleton-image"
                            *ngIf="!hasData()"></ngx-skeleton-loader>
                    </div>
                </div>
            </div>
            <div class="mck-column mck-stretch result-container-content-container">
                <ng-container *ngIf="hasData()">
                    <span>{{viewModel.levelDisplay}}, <b>{{viewModel.title}}</b></span>
                    <span class="type-text" [ngClass]="typeClass">{{type}}</span>
                    <span *ngIf="viewModel.office">{{viewModel.office}}</span>
                    <span *ngIf="viewModel.capacity">{{viewModel.capacity}}</span>
                </ng-container>
                <ng-container *ngIf="!hasData()">
                    <ngx-skeleton-loader class="mck-skeleton-line-1"></ngx-skeleton-loader>
                    <ngx-skeleton-loader class="mck-skeleton-line-2"></ngx-skeleton-loader>
                    <ngx-skeleton-loader class="mck-skeleton-line-3"></ngx-skeleton-loader>
                </ng-container>
            </div>
            <div class="mck-column mck-center" *ngIf="hasData()">
                <div class="right-arrow" alt="Right Arrow" aria-label="Details Arrow">
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./room-result-item.component.scss']
})
export class RoomResultItemComponent extends ImageDirective implements OnInit, OnChanges {

    public rootFolderURL: string = 'assets/img/rooms/';
    public fileSlug: string = '';
    public type: string = '';

    /**
     * If undefined, we display in loading state.
     */
    @Input()
    public viewModel: RoomResultsViewModel | undefined;

    @Input()
    public data: BookingFormData;

    @Input()
    public user: User;

    @Output()
    public roomSelect = new EventEmitter<RoomResultsViewModel>();

    public availability: "requestable" | "bookable" | "booked" | "non-bookable" | null;

    public foundImages: SpaceImageObject[] = [1,2].map(n => {
        return {path: `assets/img/rooms/placeholder-${n}.png`};
    });

    public ngOnInit(): void {
        if (this.viewModel && this.viewModel.space) {
            this.fileSlug = this.viewModel.space.email.split('@')[0];
            setupRoomImages(this, [1,2], this.rootFolderURL, this.fileSlug);
            const t = this.viewModel.space.internal_or_external;// || 'internal';
            this.type = `${t[0]?.toUpperCase()}${t.toLocaleLowerCase().substring(1)}`
        }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (this.availability !== null && (
            changes.data &&
            changes.data.firstChange === true &&
            changes.data.currentValue &&
            changes.data.currentValue.dateTz &&
            changes.data.currentValue.duration
        )) {
            this.availability = this.viewModel.space.byRequest({
                dateTz: this.data.dateTz,
                duration: this.data.duration,
                host: this.user
            }) === false ? "bookable" : "requestable";

        }
    }

    public hasData() {
        return this.viewModel !== undefined;
    }

    public get typeClass() {
        return this.type.toLocaleLowerCase();
    }

    public showLabel(path) {
        return path.includes('placeholder');
    }
    public get timestamp () {
        return (new Date()).getTime()
    }
}
