import { Component, OnInit } from '@angular/core';
import {
    TopBarHeaderMode,
    RoutePayload,
} from 'src/app/components/topbar-header/topbar-header.component';
import { select, Store } from '@ngrx/store';
import {
    Booking,
    User,
    Space,
    BookingFormData,
    selectActiveForm,
    selectFilteredByIdResults,
    BookingStoreState,
    storeBookingFormData,
    DateDurationData,
    OrganisationService,
    SpaceConflictAlertModalData,
    SpaceStatus,
    filterSpacesByAppliedFilters,
    FilteredSpaces,
    Building,
    SpaceFilterReason,
    getLastDateFromList,
    RoomResultsViewModel,
    stringToMinutes,
} from '@mckinsey-converge/data-common';
import {
    BaseDirective,
    dateBuildingFormatString,
    DialogEvent,
    shorterLocalDateFormatString,
    startEndTimeWithZoneFormatString,
} from '@mckinsey-converge/base';
import { BehaviorSubject, fromEvent } from 'rxjs';
import { distinctUntilChanged, take, throttleTime, tap, filter } from 'rxjs/operators';
import { BuildingStoreState } from '@mckinsey-converge/buildings';
import { RoomStoreState, loadByIdResults } from '@mckinsey-converge/rooms';
import { Router, NavigationStart, Event as NavigationEvent } from '@angular/router';
import { UserStoreState } from '@mckinsey-converge/user';
import { selectCurrentUser } from '@mckinsey-converge/user';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { MatDialog } from '@angular/material/dialog';
import {
    ConflictAlertModalComponent,
    ConflictsModalComponent,
    ConflictsModalComponentData,
} from '@mckinsey-converge/ui';

export type RoomResultsState = BookingStoreState &
    RoomStoreState &
    BuildingStoreState &
    UserStoreState;

const EMPTY_ARRAY = [];

@Component({
    selector: 'app-room-details',
    templateUrl: './room-details.component.html',
    styleUrls: ['./room-details.component.scss'],
})
export class RoomDetailsComponent extends BaseDirective implements OnInit {
    public TopBarHeaderMode = TopBarHeaderMode;
    public booking: Booking;
    // @TODO get this from current settings.
    public backRoute: RoutePayload = {
        route: ['/home', 'results'],
        queryParams: { display: 'list' },
    };

    public data = new BehaviorSubject<BookingFormData | undefined>(undefined);
    public byIdResults = new BehaviorSubject<any | undefined>(undefined);
    public open: boolean = true;
    // Set to a config once we know where the files are hosted.
    public rootFolderURL = 'assets/img/rooms/';
    // Set in the room availablity callback
    public mobile: boolean = false;
    public expandedLocation: boolean = false;
    public expandedAvailability: boolean = false;
    // Track event date changes in the UI in this param
    public alternativeWeekStartDateTz: DateTZ = new DateTZ();
    /** Current User */
    public currentUser: User;
    /** Display warning banner by default */
    public showInfoBanner: boolean = true;
    /** Space class created from subscribed active form room data */
    public room: Space;
    /** Requested booking start time from subscribed active form data */
    public requestedDateTz: DateTZ;
    /** Requested booking end time from subscribed active form data */
    public requestedEndDateTz: DateTZ;

    /** Track alert dialog should display's only once for not bookable or unavailable */
    private isAlertDisplayed: boolean = false;
    /** Update availability status of current rooom  */
    public availabilityStatus: string;
    /** Space validity per rule */
    public isSpaceValidAsPerRule: FilteredSpaces;

    public orgBuildings: Building[] = [];
    /** Subtype of a room or space. */
    public roomSubtype: string = '';

    /* recurrence booking - occurence availability */
    public occurenceAvailability: string = '';

    constructor(
        private store: Store<RoomResultsState>,
        private router: Router,
        private _org: OrganisationService,
        public dialog: MatDialog
    ) {
        super();
        /**
         * Router extras expecting from
         * On navigation from result or home page after QR code scan
         */
        if ( this.router.getCurrentNavigation() !== null && this.router.getCurrentNavigation().extras?.state?.payload ) {
					this.store.dispatch(storeBookingFormData({
							payload: this.router.getCurrentNavigation().extras.state.payload,
						})
					);
        }
    }

    public ngOnInit(): void {
        this.availabilityStatus = 'unavailable';
        this.orgBuildings = this._org.buildings;

        this.subscription(
            'currentUser',
            this.store
                .select(selectCurrentUser)
                .pipe(take(1))
                .subscribe((user: User | undefined) => {
                    // Once we know we have the user loaded.
                    this.currentUser = new User(user);
                })
        );

        this.subscription(
            'activeForm',
            this.store
                .pipe(select(selectActiveForm), distinctUntilChanged())
                .subscribe((next: BookingFormData | undefined) => {
                    if (next) {
                        this.data.next(next);
                        // R--- selectedRoom could sure use some clarity, why is everything but the kitchen sink in there?
                        if (!this.data.value?.selectedRoom || !this.data.value?.dateTz.ms) {
                            return;
                        }
                        // Create a full fledged Space object since the selectedRoom.space is just JSON
                        this.room = new Space(this.data.value?.selectedRoom?.space);

                        // Check room validtion of selected room to maintain status on detail page
                        this.checkAvailabiltyAsPerRule();
                        this.setAlternativeWeekStartDate(this.data.value?.dateTz);

                        // Query for the entire day for this room
                        this.fetchRooms(false);
                        this.setRoomSubType();

                        this.requestedDateTz = this.data.value?.dateTz;
                        if (next.is_recurrent) {
													const { is_local_tz, building_tz} = this?.data?.value?.dateTz;
													const endDate = getLastDateFromList( this?.data?.value?.recurrence_starts );
                            this.requestedEndDateTz =  new DateTZ({ date: endDate.date.valueOf(), is_local_tz, building_tz })
                            this.occurenceAvailability = `${this?.room?.availableOccurrences} out of ${this?.room?.totalOccurrences}`;
                        }
                    }
                })
        );

        this.subscription(
            'byId',
            this.store.pipe(select(selectFilteredByIdResults)).subscribe((next) => {
                this.byIdResults.next(
                    next.success?.spaces?.length
                        ? next.success.spaces[0].settings.bookings
                        : EMPTY_ARRAY
                );
            })
        );

        const breakpoint = 450;
        if (window) {
            this.mobile = window.innerWidth < breakpoint;
        }

        fromEvent(window, 'resize')
            .pipe(throttleTime(500))
            .pipe(
                tap({
                    next: (val) => {
                        const target = val.target as Window;
                        this.mobile = target.innerWidth < breakpoint;
                    },
                })
            )
            .subscribe();
    }

    public setRoomSubType() {
        //test
        const room_type = `${this?.room.internal_or_external}_room_types`;
        const temp: string = Array.isArray(this?.room?.room_type)
            ? this?.room?.room_type[0]
            : this?.room?.room_type;
        const type = this?._org?.getOrganizationFiltersByRoomType(room_type)[temp];
        this.roomSubtype = type !== undefined ? `( ${type} Room )` : '';
    }

    public isConflictWithOccurences() {
        const conflicts = this?.room?.totalOccurrences - this?.room?.availableOccurrences;
        return { is_conflict: !!conflicts, total_Conflicts: conflicts };
    }

    /**
		 * Opens a modal to display recurrence conflicts for a given room.
		 *
		 * @param {Space} room - The room for which recurrence conflicts are being displayed.
		 *
		 * This function opens a modal dialog to show recurrence exceptions (conflicts)
		 * for the specified room.
		 *
		 * TO DO: SAME FUNCTION BEING USED IN apps/staff/src/app/booking/room-details/room-details.component.ts
		 */
    public openRecurrenceConflictsModal(room: Space) {
        let { recurrence_exceptions } = this.data.value;

        const recurr_ref = this.dialog.open<ConflictsModalComponent, ConflictsModalComponentData>(
            ConflictsModalComponent,
            {
                maxWidth: '95vw',
                width: '32em',
                data: {
                    room_name: room.simple_name,
                    conflicts: recurrence_exceptions,
                    timezone: room.building.timezone,
                },
            }
        );
        this.subscription(
            'conflicts_modal_events',
            recurr_ref.componentInstance.event.subscribe((event: DialogEvent) => {
                recurr_ref.close();
            })
        );
        recurr_ref.afterClosed().subscribe(() => this.unsub('conflicts_modal_events'));
    }

		/**
		 * Retrieves the end date string for the booking, considering recurrence if applicable.
		 * If the booking is recurrent.
		 *
		 * @returns {string} The formatted end date string in a shorter local date format.
		 */
		 public get endDateString(): string {
			// if this.requestedEndDateTz is not in use anywhere here in this file than remove that property and use const here
			if(!this.requestedEndDateTz) {
				return '';
			}
			return this.requestedEndDateTz.formatDate('ccc d MMM yyyy'); // shorterLocalDateFormatString(this.requestedEndDateTz);
		}

    /**
     * Functino to check if room is available as per room/user rules
     */
    public checkAvailabiltyAsPerRule() {
        this.orgBuildings =
            !this.orgBuildings.length && this.data.value?.buildings
                ? this.data.value?.buildings
                : this.orgBuildings;
        this.isSpaceValidAsPerRule = filterSpacesByAppliedFilters(
            [],
            [this.room],
            this.data.value,
            this.orgBuildings,
            this.currentUser
        );
    }

    /**
     * Function popup advise alert dialog if scanned (QR code) room details found conflicts with other booking/s
     * @param room
     */
    alertIfItHasConflict(isBookable, isAvailable) {
        this.isAlertDisplayed = true;
        const data = {
            headerText: !isBookable
                ? SpaceConflictAlertModalData.NotBookableHeaderText
                : !isAvailable
                ? SpaceConflictAlertModalData.NotAvailableHeaderText
                : '',
            message:
                this.isSpaceValidAsPerRule.specificReason === SpaceFilterReason.OfficeRules
                    ? SpaceConflictAlertModalData.NotBookableDueToOfficeRule
                    : !isBookable
                    ? SpaceConflictAlertModalData.NotBookableMessage
                    : !isAvailable
                    ? SpaceConflictAlertModalData.NotAvailableMessage
                    : '',
            buttonLabel: !isBookable
                ? SpaceConflictAlertModalData.NotBookableButtonLabel
                : !isAvailable
                ? SpaceConflictAlertModalData.NotAvailableButtonLabel
                : '',
            callback: !isBookable
                ? this.goToBackRoute.bind(this)
                : !isAvailable
                ? this.expandAvailability.bind(this, true)
                : null,
            shouldClose: isBookable,
        };
        const dialog = this.dialog.open(ConflictAlertModalComponent, {
            data: data,
            panelClass: 'mck-alert',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
            disableClose: !isBookable,
        });
    }

    public warnIfItHasConflict({ room: room, hasConflict: hasConflict }) {
        if (
            (!room.bookable || !this.isSpaceValidAsPerRule.spaces.length) &&
            !this.isAlertDisplayed
        ) {
            // room is not bookable(not available to book) and no warning dialog displayed yet
            this.alertIfItHasConflict(false, true);
            this.updateAvailabilityStatus(SpaceStatus.Unavailable);
        } else if (!room.available && hasConflict && !this.isAlertDisplayed) {
            //room is not available AT THIS TIME to book (booked) and has conflict with other booking and no warning dialog displayed yet
            this.alertIfItHasConflict(room.bookable, !hasConflict); // (true, true/false)
            this.updateAvailabilityStatus(SpaceStatus.Booked);
        } else {
            /**
             * Update status of availability - Uavailable/Booked/Requestable/Bookable
             * If room is not bookable(not available to book) and warning dialog dispalyed once
             * If room is bookable but not available to boot AT THIS TIME (or and has conflict with other booking) and warning dialog displayed once
             * If room is bookable and availanle to book AT THIS TIME then check the type of room Requestable? or Bookable
             */
            const status =
                !room.bookable || !this.isSpaceValidAsPerRule.spaces.length
                    ? SpaceStatus.Unavailable
                    : !room.available && hasConflict
                    ? SpaceStatus.Booked
                    : this.is_request
                    ? SpaceStatus.Requestable
                    : SpaceStatus.Bookable;
            this.updateAvailabilityStatus(status);
        }
    }

    public updateAvailabilityStatus(primaryCheck: string, secondaryCheck?: string) {
        this.availabilityStatus =
            primaryCheck !== ''
                ? primaryCheck
                : this.data.value.is_recurrent ||
                  ['unavailable', 'booked'].indexOf(this.availabilityStatus) === -1
                ? secondaryCheck
                : this.availabilityStatus;
    }

    public get catering(): string {
        return (
            DateNow(new Date())
                .setValue({ minute: 0, hour: this.room?.building?.catering_hours.start })
                .formatDate('ha')
                .toLocaleLowerCase() +
            ' - ' +
            DateNow(new Date())
                .setValue({ minute: 0, hour: this.room?.building?.catering_hours.end })
                .formatDate('ha')
                .toLocaleLowerCase()
        );
    }

    public get dateString(): string {
				if(!this.requestedDateTz) return '';
        return this.requestedDateTz.formatDate('ccc d MMM yyyy');; //shorterLocalDateFormatString(this.requestedDateTz);
        
    }

    public get focusedRoomMapKey(): string {
        return `area-${this.room?.map_id}-status`;
    }

    /** Whether we are only allowed to 'request' the selected space */
    public get is_request(): boolean {
        const data = this.data.value;

        /* istanbul ignore else */
        if (!data?.selectedRoom?.space) {
            return false;
        }

        const space = new Space(data.selectedRoom.space);
        const isRequest = space.byRequest({
            dateTz: data.dateTz,
            duration: data.duration,
            host: this.currentUser,
        });
        this.updateAvailabilityStatus(
            '',
            isRequest ? SpaceStatus.Requestable : SpaceStatus.Bookable
        );
        return isRequest;
    }

    public get roomFeatures(): string[] {
        // const room_type = `${this.room.internal_or_external}_room_types`
        const combinedFeatures = {
            // ...this._org.getOrganizationFiltersByRoomType(room_type),
            ...this._org.getOrganizationFiltersByRoomType('equipment_types'),
        };
        const features = this.room !== undefined ? this.room?.featuresArray.map(
            (feature: string) => combinedFeatures[feature]
        ) : [];

        return [...new Set(features)].filter((f) => !!f);
    }

    public get roomHasCatering() {
        // Building scope catering available is true or false
        const building = this.data.value?.selectedRoom?.building;
        let hasCatering = building ? building.has_catering : false;
        // but if Room catering is false it overrides the building
        if (this.room?.has_catering === false) {
            hasCatering = false;
        }
        return hasCatering;
    }

    public get roomImagesFileSlug(): string {
        return this.room?.email.split('@')[0];
    }

    public get mapUrl(): string {
        return this.room?.level?.map_url;
    }

    public get timeString(): string {
			if(!this.requestedDateTz) return '';
			return startEndTimeWithZoneFormatString(
					this.requestedDateTz,
					this.requestedDateTz.addValue({ minutes: this.data.value?.duration })
			);
    }

    /**
     * Get the timezone form the subscribed activeForm
     */
    public get timezone(): string {
        // R--- this.room.timezone wasn't reliable enough on page refresh, something to look into
        return this.data.value.selectedRoom?.building.timezone;
    }

    public setAlternativeWeekStartDate(bookingDate: DateTZ) {
        if (!bookingDate) {
            return;
        }

        const booking_date = bookingDate.clone();
        if (this.mobile) {
            this.alternativeWeekStartDateTz = booking_date;
        } else {
            this.alternativeWeekStartDateTz = booking_date.startOfValue('week');
        }
    }

    public updateCalendarWeekView(alternativeWeekStartDateTz: DateTZ) {
        this.alternativeWeekStartDateTz = alternativeWeekStartDateTz;
        this.fetchRooms(!this.mobile, this.alternativeWeekStartDateTz);
    }

    public confirmChanges(data: DateDurationData) {
        // R-- refactor room-availability-preview to return DateTZ so setAlternativeWeekStartDate can use it.
        this.mobile = data.mobile;
        this.setAlternativeWeekStartDate(data.dateTz);

        this.fetchRooms(!this.mobile, this.alternativeWeekStartDateTz);
        // submit
        this.store.dispatch(
            storeBookingFormData({
                payload: {
                    ...this.data.value,
                    dateTz: data.dateTz,
                    duration: data.duration,
                },
            })
        );
    }

    public fetchRooms(allWeek: boolean = true, alternativeWeekStartDateTz: DateTZ = null) {
        if (!this.room?.id) {
            return;
        }

        const day = 24 * 60;
        const week = day * 7;

        let start_date = (alternativeWeekStartDateTz || this.data.value.dateTz).clone();
        if (allWeek && start_date.dateWeekday !== 7) {
            start_date = start_date.startOfValue('week');
        }

        const startOfDay = start_date.startOfValue('day').setValue({ second: 0, millisecond: 0 });
        const startOfWeek = start_date.setValue({ second: 0, millisecond: 0 }).startOfValue('day');

        // Query for the entire day for this room
        const query = {
            dateTz: allWeek ? startOfWeek : startOfDay,
            duration: allWeek ? week : day,
            locations: this.room?.id,
            capacity: this.data.value?.roomSize.value,
        };
        this.store.dispatch(loadByIdResults.request(query));
    }

    public removeDateOffset() {
        const data = this.data.value;
        let { dateTz, offset } = data;
        dateTz = dateTz.subtractValue({ minutes: ((typeof offset === 'string' && offset == '') || (typeof offset === 'undefined')) ? 0 : Number(offset) });
        this.store.dispatch(
            storeBookingFormData({
                payload: {
                    ...data,
                    dateTz: dateTz,
                    offset: 0,
                },
            })
        );
    }

    public goToBackRoute() {
        this.removeDateOffset();
        this.router.navigate(this.backRoute.route, { queryParams: this.backRoute.queryParams });
    }

    public expandMap(open: boolean) {
        this.expandedLocation = open;
    }

    public expandAvailability(open: boolean) {
        this.fetchRooms(!this.mobile);
        this.expandedAvailability = open;
    }

    public handleClose() {
        this.expandedLocation = false;
        this.expandedAvailability = false;
        this.setAlternativeWeekStartDate(this.data.value?.dateTz);
        this.fetchRooms(false);
    }

    /**
     * Continue to Booking Form
     */
    public bookRoom() {
        // submit
        this.store.dispatch(
            storeBookingFormData({
                payload: {
                    ...this.data.value,
                },
            })
        );

        const bookingRoute = this.data.value.id ? 'edit' : 'create';
        this.router.navigate(['/home', bookingRoute]);
    }

    public hideInfoBanner() {
        this.showInfoBanner = false;
    }
}
