import { LoadingModel } from '@mckinsey-converge/loading';
import { createReducer } from '@ngrx/store';
import {
    loadLaterThatDayResults,
    loadNextDayResults,
    loadRoomsForResults,
    loadByIdResults,
    loadDiffSizeResults,
    loadRoomsForResultsMap
} from './rooms.actions';
import { RoomState } from './rooms.types';

export const initialRoomsState: RoomState = {
    query: LoadingModel.empty(),
    later: LoadingModel.empty(),
    next: LoadingModel.empty(),
    byid: LoadingModel.empty(),
    size: LoadingModel.empty(),
};

export const roomsReducer = createReducer(initialRoomsState,
    ...loadRoomsForResults.ons,
    ...loadRoomsForResultsMap.ons,

    ...loadLaterThatDayResults.ons,
    ...loadNextDayResults.ons,
    ...loadByIdResults.ons,
    ...loadDiffSizeResults.ons);
