import { Component, ViewEncapsulation, Inject } from '@angular/core';
import { BookingFormData, storeBookingFormData, SeriesAction} from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { Store } from '@ngrx/store';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
// import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-room-booking-modal',
    template: `
        <div class="room-booking-modal-container">
        <mck-modal header="Search for a Meeting Room">
            <app-room-booking-form
                #roomBookingForm
                [showCancel]="true"
                [triggerSubmit]="trigger"
                (submitForm)="submitForm($event)"
                (cancelSubmit)="close()">
            </app-room-booking-form>
        </mck-modal>
        <mat-dialog-actions class="booking-modal-actions">
            <mck-modal-button-row
                primaryLabel="Search"
                (primarySubmit)="triggerSubmit()"
                [secondaryLabel]="roomBookingForm.showCancel ? 'Cancel' : ''"
                (secondarySubmit)="roomBookingForm.cancelSubmit.emit()">
            </mck-modal-button-row>
        </mat-dialog-actions>
        </div>
    `,
    styleUrls: ['./room-booking-modal.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class RoomBookingModalComponent extends BaseDirective {
    public trigger = Date();

    constructor(
        private store: Store,
        private dialogRef: MatDialogRef<RoomBookingModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: any )  {
        super();
    }

    public triggerSubmit() {
        this.trigger = Date();
    }

    public submitForm(next: BookingFormData) {
        /// submit

        const ignore = this._data?.action === SeriesAction.EDIT ? next.id : null;
        const bookable = this._data?.action === SeriesAction.EDIT || null;

        this.store.dispatch(storeBookingFormData({
            payload: {
                ...next,
                ignore, // ignore id in case of recurring edit series
                bookable, // true in case of recurring edit series
                action: this._data?.action || '' // 'edit' in case of recurring edit series
            }
        }));
        this.close('DoNotClearActiveForm'); // do not clear active form if for submit as form need forther in result module
        this._data && this._data.navigateTo && this._data.navigateTo(); // navigate to result in case of staff recurring edit

    }

    public close(flag: string = '') {
        this.dialogRef.close(flag);
    }
}
