import {
    Component,
    EventEmitter,
    Input,
    Output,
    OnInit,
} from '@angular/core';
import { BookingResultsViewModel } from '../manage-bookings/manage-bookings.viewmodel';
import {
    BaseDirective,
    shorterLocalDateFormatString,
    timeWithZoneFormatString,
    startEndTimeFormatString,
    humaniseDuration,
    closestToTimeSlotTz,
} from '@mckinsey-converge/base';
import { DateNow, DateTZ, localTimezone } from "@mckinsey-converge/date-tz";
import { Booking, BookingFormData, BookingStoreState, bookingTypeOptions, Building, BuildingCity, clearBookingFormData, defaultRoomSizeOption, DEFAULT_BOOKING_DURATION, flattenDisplayOffices, roomFormDataToQuery, roomSizeOptions, storeBookingFormData } from '@mckinsey-converge/data-common';
import { buildingStateSelector, BuildingStoreState, selectBuildingOptionsGroupedByCity } from '@mckinsey-converge/buildings';
import {
    select,
    Store
} from '@ngrx/store';
import {
    BehaviorSubject,
} from 'rxjs';

import { distinctUntilChanged, map } from 'rxjs/operators';
import { SelectOption } from '@mckinsey-converge/ui';
import { loadRoomsForResults, loadRoomsForResultsMap } from '@mckinsey-converge/rooms';
import { MyBookingsStoreState } from '@mckinsey-converge/bookings';
import { UserStoreState } from '@mckinsey-converge/user';

export type RoomBookingStoreState = BookingStoreState & BuildingStoreState & MyBookingsStoreState & UserStoreState;
@Component({
    selector: 'app-booking-result-item',
    templateUrl: './booking-result-item.component.html',
    styleUrls: ['./booking-result-item.component.scss']
})
export class BookingResultItemComponent extends BaseDirective implements OnInit {
    /**
     * If undefined, we display in loading state.
     */
    @Input()
    public viewModel: BookingResultsViewModel | undefined;
    
    @Input()
    public bookings: BookingResultsViewModel[];

    @Input()
    public list: string;

    @Input() public index: number;

    @Input() public small: boolean = false;

    @Output()
    public bookingSelect = new EventEmitter<BookingResultsViewModel>();

    public buildings = new BehaviorSubject<SelectOption<any>[]>([]);

    public roomName: string;
    public roomLocation: string;
    public host: string;
    public status: string;
    public timeStringParts: string[] = [' ', ' '];
    public localTimeStringParts: string[] = [' ', ' '];
    public isAnotherTimezone: boolean = false;
    public statusText: string;
    public statusImage: string;
    public durationString: string;
    public timeRangeString: string;
    public building: any;
    public get dateString() {
        if (!this.bookings || !this.bookings.length) return '';
        const bkn1 = this.bookings[this.index].booking;
        const d1 = new DateTZ({ date: bkn1.startDateTz.ms, is_local_tz: false, building_tz: bkn1.startDateTz.building_tz });
        if (this.index === 0) {
            return shorterLocalDateFormatString(d1);
        }
        const bkn2 = this.bookings[this.index - 1].booking;
        const d2 = new DateTZ({ date: bkn2.startDateTz.ms, is_local_tz: false, building_tz: bkn2.startDateTz.building_tz });
        if (!d1.isSameDate(d2, 'day')) {
            return shorterLocalDateFormatString(d1);
        }
        return '';
    }
    public get todayString() {
        if (!this.bookings || !this.bookings.length) {
            return '';
        }
        
        // Strip the booking of it's timezone infomation so isSameDate is just based on timestamp and local time.
        const d1 = new DateTZ({ date: this.bookings[this.index].booking.startDateTz.ms });
        return DateNow(new Date()).isSameDate(d1, 'day') ? '(Today)' : '';
    }

    public get attendeesCount(): number {
        // allAttendees is booking.attendees + 1 (booking.organizer)
        return this.viewModel.booking?.allAttendees.length; 
    }

    public get has_catering(): number {
        return this.viewModel.booking?.has_catering && this.viewModel.booking?.catering?.length;
    }

    public get upcoming() {
        return ['tentative', 'accepted', 'approved'].includes(this.status);
    }

    constructor(private store: Store<RoomBookingStoreState>) {
        super();
    }

    public ngOnInit(): void {
        this.setupDisplayFields();
        this.subscription('buildings', this.store.pipe(
            select(buildingStateSelector),
            select(selectBuildingOptionsGroupedByCity),
            map((grouped: Map<BuildingCity, Building[]>) => {
                return flattenDisplayOffices(grouped);
            }),
            distinctUntilChanged()
        ).subscribe(next => {
            this.buildings.next(next);
        }));
    }

    public hasData() {
        return this.viewModel !== undefined;
    }

    public getTooltipText(){
        return this.viewModel.booking.local_room_name.split(',');
    }

    public setupDisplayFields() {  
        if (!this.viewModel?.booking) {
            return;
        }
        this.timeStringParts = timeWithZoneFormatString(this.viewModel.booking.startDateTz).split(' ');
        this.timeRangeString = startEndTimeFormatString(this.viewModel.booking.startDateTz, this.viewModel.booking.displayEndDateTz);
        this.isAnotherTimezone = !this.viewModel.booking.startDateTz.is_local_tz;
        this.localTimeStringParts = timeWithZoneFormatString(this.viewModel.booking.startDateTz.toZone(localTimezone())).split(' ');
        this.host = this.viewModel.booking.organiser.name;
        this.roomName = this.viewModel.booking.local_room_name.split(',')[0];
        this.roomLocation = this.viewModel.booking.room.street_and_city;
        this.durationString = humaniseDuration(this.viewModel.booking.duration, 'medium');

        if (this.viewModel.booking.status) {
            // mark bookings before today expired
            this.status = (DateNow(new Date()).isAfterDate(this.viewModel.booking.endDateTz) && this.list !== 'cancelled') ? 'expired' : this.viewModel.booking.status;
            switch(this.status) {
                case 'tentative':
                    this.statusImage = 'assets/icon/booking_pending.svg';
                    this.statusText = 'Pending';
                    break;
                case 'accepted':
                    this.statusImage = 'assets/icon/booking_confirmed.svg';
                    this.statusText = 'Confirmed';
                    break;
                case 'approved':
                    this.statusImage = 'assets/icon/booking_confirmed.svg';
                    this.statusText = 'Confirmed';
                    break;
                case 'declined':
                    this.statusImage = 'assets/icon/booking_cancelled.svg';
                    this.statusText = 'Cancelled';
                    break;
                case 'cancelled':
                    this.statusImage = 'assets/icon/booking_cancelled.svg';
                    this.statusText = 'Cancelled';
                    break;
                case 'expired':
                    this.statusImage = 'assets/icon/booking_expired.svg';
                    this.statusText = 'Expired';
                    break;
                default: // TODO
                    this.statusImage = 'assets/icon/booking_expired.svg';
                    this.statusText = 'Expired';
                break;
            }
        }
        return;
    }

}
