import {
    Component,
    ChangeDetectionStrategy,
    Input,
    OnInit,
    OnChanges,
    ElementRef,
    ViewChild,
    EventEmitter,
    Output,
    SimpleChanges,
    ChangeDetectorRef,
} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { fromEvent, Subject, BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { throttleTime, tap } from 'rxjs/operators';
import { CalendarEvent, CalendarEventTimesChangedEvent } from 'angular-calendar';
import { BaseClass, durationHumanized, isLessThanBreakpoint } from '@mckinsey-converge/base';
import {
    Booking,
    Building,
    BuildingLevel,
    Space,
    User,
    rulesForSpace,
    SpaceRules,
} from '@mckinsey-converge/data-common';
import { generateFeedbackUrl, Urls } from '../../utilities/urls';
import { TopBarHeaderMode } from '../room-location-preview/room-location-preview.component';
import { RoomAvailabilityPreviewModalComponent } from '../room-availability-preview-modal/room-availability-preview-modal.component';
import {
    DateNow,
    DateTZ,
    convertLocalTimestampToTimezonedDateTz,
    convertTimezonedTimestampToLocalDateTz,
    timezoneOffset,
} from '@mckinsey-converge/date-tz';
import { utcToZonedTime } from 'date-fns-tz';
import { FormControl } from '@angular/forms';
import { DateTime } from 'luxon';

const WEEKEND_DAYS = [6, 7];
const colors = {
    grey: {
        secondary: '#D2D2D2',
        primary: '#A19F9F',
    },
    blue: {
        secondary: '#C0DBF5',
        primary: '#00A9F4',
    },
    red: {
        secondary: '#EB5757',
        primary: '#EB5757',
    },
};

interface CalendarClickEvent {
    date: Date;
}

export interface BookingBumper {
    top?: string;
    bottom?: string;
    height: string;
    backgroundColor: string;
}
export interface BookingBumpers {
    top: BookingBumper;
    bottom: BookingBumper;
}

export type BookingPlus = Booking & { start: number; end: number }; // R--- why again?
export type CalendarEventPlus = CalendarEvent<any> & { bumper?: BookingBumpers; booking?: Booking };

@Component({
    selector: 'room-availability-preview',
    templateUrl: './room-availability-preview.component.html',
    styleUrls: ['./room-availability-preview.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RoomAvailabilityPreviewComponent extends BaseClass implements OnInit, OnChanges {
    @ViewChild('scrollContainer') scrollContainer: ElementRef<HTMLElement>;

    @Input() public alternativeWeekStartDateTz: DateTZ;
    // Used when editing a booking
    @Input() public bookingId?: string;
    @Input() public bookings: BookingPlus[];
    /** retain as mutable */
    @Input() public building: Building;
    // R--- could probably use the currentBooking ID edit,
    // The bookingId was an after thought
    @Input() public currentBooking: Booking;
    @Input() public currentUser: User;
    @Input() public dateTz: DateTZ;
    @Input() public duration: number;
    @Input() public expanded: boolean;
    @Input() public level: BuildingLevel;
    /** The theme to use **/
    @Input() public mode: TopBarHeaderMode = TopBarHeaderMode.Light;
    @Input() public space: Space;
    // @Input() public timezone: string;

    @Output() public confirmChanges: EventEmitter<any> = new EventEmitter();
    @Output() public handleExpand: EventEmitter<any> = new EventEmitter();
    @Output() public updateCalendarWeekView: EventEmitter<DateTZ> = new EventEmitter();
    @Output() public warnIfItHasConflict: EventEmitter<any> = new EventEmitter();

    constructor(public dialog: MatDialog) {
        super();
    }

    public breakpoint = 768;
    public mobile = false;
    public showDatePicker = false;
    public events: CalendarEventPlus[] = [];
    public dayStartHour: number;
    public dayEndHour: number;
    public viewDateTz: DateTZ;
    public weekViewDateTz: DateTZ;
    public weekViewRangeText: string;
    public showWeekend: boolean = false;
    public currentPage: string;
    public calendarPopupValue = new BehaviorSubject<DateTZ>(DateNow(new Date()));
    public pendingChange: boolean = false;
    public pendingDateTz: DateTZ;
    public pendingDuration: number;
    public eventSnapSize: number = 15;
    public dayRefresh: Subject<any> = new Subject();
    public weekRefresh: Subject<any> = new Subject();
    public addMarginOnConflict: boolean = false; // helps to add html class to adjust margin top in case of QR code scan conflict
    public event: CalendarEventPlus = {
        start: new Date(),
        end: new Date(),
        title: '',
        color: colors.blue,
        cssClass: 'selected',
        actions: null,
        resizable: {
            beforeStart: false,
            afterEnd: false,
        },
        draggable: false,
    };

    public get timezone(): string {
        return this.dateTz.building_tz;
    }

    public get one_day_in_millis() {
        return 86400000;
    }

    public get thirty_minutes_in_millis() {
        return 1800000;
    }
    public rules: SpaceRules;
    public modal_data: any = {};
    public scrollFired: boolean = false;

    // Values to display a span on the date picker, but the date picker doesn't support this.
    public date_span = {
        start: 0,
        end: 0,
    };

    // I can't think of any reason the calendar widget needs to go back further than 2 years, if even that.
    public fromDateTz = DateNow(new Date()).subtractValue({ years: 2 });

    public dateControl = new FormControl(new Date().valueOf());

    public isEventDate(d: CalendarClickEvent) {
        const date = new DateTZ({ date: d.date.valueOf() }); // local time I think.. problem TODO
        return this.pendingDateTz.isSameDate(date, 'day');
    }

    public get inputDate(): Observable<DateTZ> {
        return this.calendarPopupValue.pipe(map((d) => d));
    }

    public get feedback() {
        return generateFeedbackUrl(this.currentPage);
    }

    public ngOnInit(): void {
        // R--- retrun to document
        this.mobile = isLessThanBreakpoint(this.breakpoint);
        this.pendingDateTz = this.dateTz.clone();
        const date = this.dateTz.clone();
        const currentBookingStartDate = this.currentBooking
            ? this.currentBooking.startDateTz.addValue({ seconds: 0 })
            : date;
        this.alternativeWeekStartDateTz = date.startOfValue('week').startOfValue('day');

        // Not sure if this is correct
        this.weekViewDateTz = this.alternativeWeekStartDateTz.clone();

        this.calendarCenter();
        this.pendingDuration = this.duration;
        this.viewDateTz = new DateTZ({ 
            date: DateTime.fromISO(this.pendingDateTz.formatDate('yyyy-MM-dd'))
        }).setValue({ hour: 12, minute: 0 });
        this.setCalendarValue(this.pendingDateTz);
        this.showWeekend =
            WEEKEND_DAYS.includes(currentBookingStartDate.dateWeekday) ||
            WEEKEND_DAYS.includes(date.dateWeekday);
        this.setupEventName();
        this.transformBookings();
        fromEvent(window, 'resize')
            .pipe(throttleTime(500))
            .pipe(
                tap({
                    next: (val) => {
                        const target = val.target as Window;
                        this.mobile = target.innerWidth < this.breakpoint;
                        this.expanded ? this.weekRefresh.next() : this.dayRefresh.next();
                    },
                })
            )
            .subscribe();

        this.rules = rulesForSpace({
            time: this.dateTz.ms,
            duration: this.duration,
            user: this.currentUser,
            rules: this.building?.booking_rules,
            space: this.space,
        });

        // Update the display when the screen resizes.
        fromEvent(window, 'resize')
            .pipe(throttleTime(500))
            .pipe(
                tap({
                    next: (val) => {
                        this.mobile = isLessThanBreakpoint(this.breakpoint);
                        this.calendarCenter();
                        this.setDislayValues();
                    },
                })
            )
            .subscribe();
        this.dateControl.setValue(this.dateTz);
        this.transformBookings();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.expanded) {
            // set if the event is editable
            this.event.draggable = this.expanded;
            this.event.resizable = {
                beforeStart: this.expanded,
                afterEnd: this.expanded,
            };
            const date = this.dateTz;
            this.setCalendarValue(date);
        }

        if (this.dayStartHour) this.scrollTo();

        if (!this.pendingDateTz) return;

        if (changes.bookings || changes.expanded) {
            this.transformBookings();
        }
        if (changes.alternativeWeekStartDateTz) {
            this.setDislayValues();
        }
        if (changes.expanded) {
            this.expanded ? this.weekRefresh.next() : this.dayRefresh.next();
        }
    }

    public setDislayValues() {
        const tz_offset = timezoneOffset(this.alternativeWeekStartDateTz.building_tz).hours
        const week_start_date = this.alternativeWeekStartDateTz.subtractValue({hours: tz_offset}).clone(); // Operation needed to trick the mwl-calendar-week-view to display the correct day if the day changes between timezones 
        this.weekViewDateTz = this.weekViewDateTz || this.alternativeWeekStartDateTz.clone();
        let week_view_date = this.weekViewDateTz.startOfValue('week'); // new DateTZ({date: this.weekViewDate?.valueOf()}).startOfValue('week')
        if (!this.showWeekend) {
            // Advance from Sunday to Monday
            week_view_date = week_view_date.addValue({ days: 1 });
        }

        if (this.mobile) {
            this.weekViewDateTz = week_start_date
            this.weekViewRangeText = `${this.weekViewDateTz.formatDate('d MMMM')}`;
            this.calendarPopupValue.next(this.weekViewDateTz);
        } else {
            this.weekViewDateTz = week_start_date.addValue({ days: this.showWeekend ? 0 : 1 }); // new Date(week_start_date.addValue({ days: this.showWeekend ? 0 : 1 }).ms);
            this.weekViewRangeText = `${week_view_date.formatDate('d MMMM')} - ${week_view_date
                .addValue({ days: this.showWeekend ? 6 : 4 })
                .formatDate('d MMMM yyyy')}`;
            this.calendarPopupValue.next(week_view_date);
        }
    }

    public getBumpers(booking: Booking, selected: boolean = false) {
        const email = booking.space.email || this.space.email;
        const setup = booking.setup[email] ? booking.setup[email] / 60 : 0;
        const breakdown = booking.breakdown[email] ? booking.breakdown[email] / 60 : 0;

        return {
            top: {
                top: `-${setup - 1}px`,
                height: `${setup}px`,
                backgroundColor: selected ? colors.blue.secondary : colors.grey.secondary,
            },
            bottom: {
                bottom: `-${breakdown - 1}px`,
                height: `${breakdown}px`,
                backgroundColor: selected ? colors.blue.secondary : colors.grey.secondary,
            },
        };
    }

    public setupEventName() {
        this.setDislayValues();
        const pending_date = this.pendingDateTz.clone();
        const start_date = pending_date.setValue({ second: 0, millisecond: 0 });
        this.event.start = start_date.JSDate;
        this.event.end = start_date.addValue({ minutes: this.pendingDuration }).JSDate;
        const startHour = parseInt(pending_date.formatDate('H'), 10);

        if (startHour === 0) {
            this.dayStartHour = startHour;
        } else if (startHour < 6) {
            const offset = startHour > 3 ? startHour - 2 : startHour;
            this.dayStartHour = startHour - offset;
        } else {
            // The min of 18 is needed so start times after 6pm
            // don't push the calendar view beyond midnight.
            const offset = Math.min(18, startHour - 3);
            this.dayStartHour = offset;
        }

        if (!this.scrollFired && this.dayStartHour) {
            this.scrollTo();
        }

        this.dayEndHour = Math.max(24, this.dayStartHour + 6);
        this.event.title = `${pending_date
            .formatDate('h:mma')
            .toLocaleLowerCase()} (${durationHumanized(this.pendingDuration, true)})`;
        this.event.cssClass = `selected ${this.showWeekend ? 'show-weekend' : ''} ${
            this.mobile && this.expanded ? 'mobile' : ''
        } `;

        if (this.currentBooking) {
            this.event.bumper = this.getBumpers(this.currentBooking);
            this.event.booking = this.currentBooking;
        }
        this.expanded ? this.weekRefresh.next() : this.dayRefresh.next();
    }

    public transformBookings() {
        this.calendarSpan();
        const events = [];
        if (Array.isArray(this.bookings)) {
            this.bookings.forEach((raw_booking) => {
                const booking = new Booking(raw_booking);
                const { mode, duration } = this.displayClass(
                    booking.startDateTz.ms,
                    booking.endDateTz.ms
                );

                // back off a ms when end of day to avoid all day wrapping to next day.
                let end = booking.endDateTz;
                const endTime = booking.endDateTz;
                if (endTime.minutes === 0 && endTime.seconds === 0) {
                    // back off a ms when the end of day to avoid all day wrapping to next day.
                    end = booking.endDateTz.subtractValue({ seconds: 1 });
                }

                // When editing skip over the current booking
                if (booking.id === this.bookingId) return;

                const utcStart = booking.startDateTz; // building time
                const utcEnd = end; // building time
                // adjust the time to appear as the same time locally ( but technically incorrect for building timezone )
                const adjustedStartDate = utcToZonedTime(utcStart.ms, booking.timezone); // return local time
                const adjustedEndDate = utcToZonedTime(utcEnd.ms, booking.timezone); // return local time

                events.push({
                    start: adjustedStartDate,
                    end: adjustedEndDate,
                    title: `Booked by ${booking.booker['name']}`,
                    color: colors.grey,
                    actions: null,
                    cssClass: `booked ${this.mobile && 'mobile'} ${
                        this.showWeekend ? 'show-weekend' : ''
                    } ${mode}`,
                    bumper: this.getBumpers(booking),
                    resizable: {
                        beforeStart: false,
                        afterEnd: false,
                    },
                    draggable: false,
                    booking,
                });
            });
        }

        const { mode, duration } = this.displayClass(
            this.event.start.getTime(),
            this.event.end.getTime()
        );
        const event = Object.assign({}, this.event);
        const endTime = new DateTZ({ date: this.event.start.valueOf() }).addValue({
            minutes: this.pendingDuration,
        }); // building time
        if (endTime.minutes === 0 && endTime.second === 0) {
            // back off a ms when the end of day to avoid all day wrapping to next day.
            event.end = endTime.subtractValue({ milliseconds: 1 }).JSDate; // building time
        }

        // adjust the time to appear as the same time locally ( but technically incorrect for building timezone )
        // will need to adjust the other way with drag and drop and click actions.
        event.start = utcToZonedTime(event.start, this.timezone); // local time
        event.end = utcToZonedTime(event.end, this.timezone); // local time

        event.cssClass = `selected ${this.mobile && this.expanded ? 'mobile' : ''}  ${mode}`;
        if (this.currentBooking) {
            event.bumper = this.getBumpers(this.currentBooking, true);
            event.booking = this.currentBooking;
        }

        events.push(event);
        this.events = events.slice();
        this.updateTileColor();
    }

    public updateTileColor() {
        const pending_date = this.pendingDateTz.clone();
        const start = pending_date.setValue({ second: 0, millisecond: 0 });
        const end = start.addValue({ minutes: this.pendingDuration });
        const booked = this.events.filter((e) => !e.draggable); // this.events are in local time
        const conflict = this.hasConflict(start, end, booked);
        const isEvents = this.events.filter((e) => e.draggable);
        if(isEvents.length ) {
            isEvents[0].color = conflict ? colors.red : colors.blue;
            this.addMarginOnConflict = conflict ? true : false;
            
        } 
        !this.expanded && this.warnIfItHasConflict.emit({room: this.space, hasConflict: conflict});
    }
    public displayClass(start: number, end: number) {
        const duration = end - start;
        return {
            duration,
            mode: duration > this.thirty_minutes_in_millis ? 'extended' : 'minimal',
        };
    }

    public setCalendarValue(dateTz: DateTZ) {
        let toDate = DateNow().startOfValue('minute');
        if (dateTz) {
            toDate = dateTz.startOfValue('minute');
        }
        if (!this.mobile && toDate.dateWeekday !== 7) {
            toDate = toDate.startOfValue('week');
        }
        this.updateCalendarWeekView.emit(toDate);
        this.weekRefresh.next();
        this.alternativeWeekStartDateTz = toDate;
        this.setDislayValues();
    }

    /**
     * Convert the true bulding based time object to a timestamp skewed to the local time
     *
     * @param buildingTz - DateTZ object of time in bulding timezone
     * @returns number - timestamp in local time skewed from bulding time
     */
    public convertTimezonedValueForCalendar(buildingTz: DateTZ) {
        return convertTimezonedTimestampToLocalDateTz(buildingTz.ms, this.timezone).ms;
    }

    public hasConflict(startTz, endTz, booked) {
        // Convert the start and end building times to the local timezone for comparison with the localized bookings.
        const localizedStartTime = startTz.ms;
        const localizedEndTime = endTz.ms;
        let email = this.space?.email || this.currentBooking?.room?.email;
        return booked.some((b) => {
            email = email || b.booking?.room?.email;
            // Booking here are local time to render on the calendar correctly
            const setup = b.booking?.setup[email] || 0;
            // R---  This weird augmented booking with start and end needs to be with DateTZ and a getter when ms is needed.
            const bookingStartTime = new DateTZ({ date: b.start.valueOf() }); // local times
            const bookingEndTime = new DateTZ({ date: b.end.valueOf() }); // local times
            const bstart = bookingStartTime.subtractValue({ seconds: setup }).ms;
            const breakdown = b.booking?.breakdown[email] || 0;
            const bend = bookingEndTime.addValue({ seconds: breakdown }).ms;

            if (
                (localizedStartTime >= bstart && localizedStartTime < bend) || // booking starts durring a meeting
                (localizedStartTime <= bstart && localizedEndTime > bstart) || // booking ends durring meeting
                (localizedStartTime >= bstart && localizedEndTime <= bend) || // booking starts and ends durring a meeting
                (localizedStartTime <= bstart && localizedEndTime >= bend) // booking overlaps entire meeting
            ) {
                return b.booking?.id !== this.currentBooking?.id;
            }
            return false;
        });
    }

    public eventTimesChanged({ event, newStart, newEnd }: CalendarEventTimesChangedEvent): void {
        const s = convertLocalTimestampToTimezonedDateTz(
            newStart.valueOf(),
            this.timezone
        );
        if (s.ms < Date.now()) return;
        // newStart and newEnd are local times from the calendar component
        // merging in with localized bookings in this.events
        this.events = this.events.map((iEvent) => {
            if (event.id && iEvent.id === event.id) {
                return {
                    ...event,
                    start: newStart,
                    end: newEnd,
                };
            }
            return iEvent;
        }); // local times

        let buildingStartDateTz = new DateTZ(
            { date: newStart.valueOf()}
        );
        let buildingEndDateTz = new DateTZ(
            { date: Math.ceil(newEnd.valueOf()/900000)*900000 } //For latest bug in CNG-309, make sure end time follows a 15-min interval
        );

        // Clear out the seconds and miliseconds to avoid collision by seconds.
        buildingStartDateTz.setValue({ millisecond: 0, second: 0 });

        if (buildingStartDateTz < DateNow(new Date())) {
            // compare building time to now
            // Abandon updates if the past
            this.transformBookings();
            return;
        }

        let pendingDuration = Math.floor(
            (buildingEndDateTz.ms - buildingStartDateTz.ms) / 60 / 1000
        );
        // Make adjustments to the time and duration so the snap to behavior aligns
        // with the Mckinsey time and duration rules.
        const new_start = buildingStartDateTz.clone(); //new DateTZ({date: buildingNewStart.getTime()});  // building time
        if (pendingDuration >= 60) {
            // check that duration is inc of 1/2 hr
            const d = pendingDuration % 30;
            // if (d) {
                // pendingDuration = Math.floor(pendingDuration + (30 - d));
                // buildingEndDateTz = new_start.addValue({ minutes: pendingDuration }); // building time
            // }
            // check that the start time is 1/2 hr
            const m = parseInt(new_start.formatDate('m'), 10);
            const md = m % 30;
            // if (md) {
                // buildingStartDateTz = new_start.addValue({ minutes: md }); // building time
                // buildingEndDateTz = new_start.addValue({ minutes: pendingDuration }); // building time
            // }
            // Update snap to rules
            // this.eventSnapSize = 30; //done for CNG-309
        } else {
            // check that duration is inc of 1/4 hr
            const d = pendingDuration % 15;
            if (d) {
                // pendingDuration = Math.floor(pendingDuration + (15 - d));
                // buildingEndDateTz = new_start.addValue({ minutes: pendingDuration }); // building time
            }
            this.eventSnapSize = 15;
        }

        // Drag and Drop / Resize duration
        const dnd_duration = buildingEndDateTz.ms - buildingStartDateTz.ms;
        if (dnd_duration >= this.one_day_in_millis - 1) {
            // Add that millisecond back in so the angular calender drag and drop snapping still works.
            buildingEndDateTz = buildingEndDateTz.addValue({ milliseconds: 1 });
        }

        if (this.rules?.max_length) {
            if (dnd_duration / 60000 > this.rules?.max_length) {
                // Abandon updates if exceeeds max duration
                this.transformBookings();
                return;
            }
        }

        // Setup values for conflict checking, add on the setup and breakdown times form comparison
        let conflictCheckStartTime = buildingStartDateTz.clone();
        let conflictCheckEndTime = buildingEndDateTz.clone();
        // Setup and Breakdown
        if (this.currentBooking) {
            const setup = this.currentBooking.setup[this.currentBooking.space.email] || 0;
            if (setup) {
                const start = buildingStartDateTz.clone();
                conflictCheckStartTime = start.subtractValue({ seconds: setup });
            }
            const breakdown = this.currentBooking.breakdown[this.currentBooking.space.email] || 0;
            if (breakdown) {
                const end = buildingEndDateTz.subtractValue({ seconds: 1 });
                conflictCheckEndTime = end.addValue({ minutes: breakdown });
            }
        }

        // check existing events
        const start = conflictCheckStartTime;
        const end = conflictCheckEndTime;
        const booked = this.events.filter((e) => e.draggable === false); // this.events are in local time
        const conflict = this.hasConflict(start, end, booked);
        this.dateControl.setValue(start);
        if (conflict) {
            // Abandon updates if conflict
            this.transformBookings();
            return;
        }

        // check if the time or duration has changed
        if (this.pendingDateTz !== buildingStartDateTz) {
            this.pendingChange = true;
        } else if (this.pendingDuration !== pendingDuration) {
            this.pendingChange = true;
        }

        // set the new Pending Date
        this.pendingDateTz = convertLocalTimestampToTimezonedDateTz(buildingStartDateTz.ms, this.timezone); // building time
        this.pendingDuration = pendingDuration;

        this.setupEventName();
        this.transformBookings();
    }

    public hourClicked(event: CalendarClickEvent) {
        // convert the calender local input to the building times
        const buildingStartDate = convertLocalTimestampToTimezonedDateTz(
            event.date.valueOf(),
            this.timezone
        ); // return building time
        let startTz = buildingStartDate.clone();
        let endTz = startTz.addValue({ minutes: this.pendingDuration });

        if (buildingStartDate.ms < Date.now()) {
            // compare building time to now
            // Abandon past events
            return;
        }

        // this.pendingDate is in building time
        if (this.pendingDateTz.ms !== startTz.ms) {
            // comparing building to building
            // Setup and Breakdown
            if (this.currentBooking) {
                const setup = this.currentBooking.setup[this.currentBooking.space.email] || 0;
                if (setup) {
                    startTz = startTz.subtractValue({ seconds: setup });
                }
                const breakdown =
                    this.currentBooking.breakdown[this.currentBooking.space.email] || 0;
                if (breakdown) {
                    endTz = endTz.addValue({ minutes: breakdown });
                }
            }

            // check existing events
            const booked = this.events.filter((e) => e.draggable === false);
            // building start and end time but localized bookings
            const conflict = this.hasConflict(startTz, endTz, booked);
            if (conflict) {
                // Abandon updates if conflict
                this.transformBookings();
                return;
            }

            this.pendingChange = true;
            this.pendingDateTz = buildingStartDate; // building time

            this.setupEventName();
            this.transformBookings();
        }
    }

    public displayConfirmationModal() {
        this.modal_data = {
            pendingDateTz: this.pendingDateTz, // building time , new time
            pendingDuration: this.pendingDuration,
            dateTz: this.dateTz, // building time, existing time
            duration: this.duration,
            space: new Space(this.space),
            rules: this.rules,
            bookingId: this.bookingId,
            currentBooking: this.currentBooking,
        };

        const dialogRef = this.dialog.open(RoomAvailabilityPreviewModalComponent, {
            data: this.modal_data,
            panelClass: 'mck-modal',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });

        dialogRef.componentInstance.submitForm.subscribe((d: any) => {
            // Apply the confirmed changes
            this.pendingChange = true;
            this.pendingDateTz = d.dateTz;
            this.pendingDuration = d.duration;

            dialogRef.componentInstance.applyChanges({
                dateTz: d.dateTz,
                duration: this.pendingDuration,
                pendingDateTz: this.pendingDateTz,
                pendingDuration: this.pendingDuration,
            });
            this.setupEventName();
            this.transformBookings();
            // Change View
            this.setCalendarValue(d.dateTz);
        });

        dialogRef.afterClosed().subscribe((d) => {
            // Update the current with pending record
            if (d) {
                // convert the local input values returned into the same time in the building timezone
                const buildingDate = convertLocalTimestampToTimezonedDateTz(
                    d.dateTz.ms,
                    this.timezone
                );
                this.dateTz = buildingDate;
                this.duration = d.duration;
                this.viewDateTz = new DateTZ({ 
                    date: DateTime.fromISO(buildingDate.formatDate('yyyy-MM-dd'))
                }).setValue({ hour: 12, minute: 0 })
                this.pendingChange = false;
                this.confirmChanges.emit({ ...d, mobile: this.mobile, save: true });
            } else {
                // this is ok
                const md = this.modal_data;
                this.dateTz = md.dateTz;
                this.duration = md.duration;
                this.pendingDateTz = md.dateTz;
                this.pendingDuration = md.duration;
                this.viewDateTz = new DateTZ({ 
                    date: DateTime.fromISO(md.dateTz.formatDate('yyyy-MM-dd'))
                }).setValue({ hour: 12, minute: 0 })
                this.pendingChange = false;
                // This is reverting the changes
                this.confirmChanges.emit({
                    dateTz: md.dateTz,
                    duration: this.duration,
                    mobile: this.mobile,
                    save: !!d,
                });
                this.setupEventName();
                this.transformBookings();
                this.weekRefresh.next();
                this.alternativeWeekStartDateTz = this.viewDateTz;
                this.setDislayValues();
            }
        });
    }

    public goToHelp() {
        window.open(Urls.Help);
    }

    public scrollTo() {
        if (this.scrollFired) return;
        this.timeout(
            'scroll_to',
            () => {
                const ele = document.getElementById('calendarScrollContainer');
                if (ele) {
                    ele.scrollTo({
                        top: this.dayStartHour * 64,
                        left: 0,
                        behavior: 'smooth',
                    });
                    this.scrollFired = true;
                }
            },
            500
        );
    }

    public toggleDatePicker() {
        this.showDatePicker = !this.showDatePicker;
    }

    public collapse() {
        this.handleExpand.emit(false);
        // Reset to the initial view
        this.pendingDateTz = this.dateTz; // = new Date(this.date);
        this.pendingDuration = this.duration;
        this.viewDateTz = new DateTZ({ 
            date: DateTime.fromISO(this.dateTz.formatDate('yyyy-MM-dd'))
        }).setValue({ hour: 12, minute: 0 })
        this.pendingChange = false;
        this.setupEventName();
        this.transformBookings();
        this.dayRefresh.next();
    }

    public calendarBack() {
        let today = this.alternativeWeekStartDateTz; // new DateTZ({date: this.alternativeWeekStartDate});
        if (this.mobile) {
            today = today.subtractValue({ days: 1 });
        } else {
            today = today.subtractValue({ weeks: 1 }).startOfValue('day');
        }
        this.updateCalendarWeekView.emit(today);
        this.weekRefresh.next();
        this.alternativeWeekStartDateTz = today;
        this.setDislayValues();
    }

    public calendarSpan() {
        let today = this.pendingDateTz; // new DateTZ({date: this.pendingDate.valueOf()});
        if (this.mobile || !this.expanded) {
            this.date_span.start = today.startOfValue('day').ms;
            this.date_span.end = today.endOfValue('day').ms;
        } else {
            this.date_span.start = today.startOfValue('week').ms;
            this.date_span.end = today.endOfValue('week').ms;
        }
    }

    public calendarCenter() {
        let today = this.pendingDateTz.startOfValue('day');
        if (!this.mobile) {
            today = today.startOfValue('week');
        }

        this.updateCalendarWeekView.emit(today);
        this.weekRefresh.next();
        this.alternativeWeekStartDateTz = today;
        this.setDislayValues();
    }

    public calendarForward() {
        let today = this.alternativeWeekStartDateTz // new DateTZ({date: this.alternativeWeekStartDate});
        if (this.mobile) {
            today = today.addValue({ days: 1 });
        } else {
            today = today.addValue({ weeks: 1 }).startOfValue('day');
        }
        this.updateCalendarWeekView.emit(today);
        this.weekRefresh.next();
        this.alternativeWeekStartDateTz = today;
        this.setDislayValues();
    }

    public toggleWeekend() {
        this.showWeekend = !this.showWeekend;
        this.setupEventName();
    }
}
