import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import {NavItem} from './nav-item';
import { MatMenu } from '@angular/material/menu';

@Component({
  selector: 'cng-menu-item',
  templateUrl: './cng-menu-item.component.html',
  styleUrls: ['./cng-menu-item.component.scss']
})
export class CngMenuItemComponent implements OnInit {
  @Input() items: NavItem[];
  @Input() activeOption: string;
  @ViewChild('childMenu', {static: true}) public childMenu: any;

  @Output() public onChildSelect = new EventEmitter<{}>();
  
  constructor(public router: Router) {
  }

  ngOnInit() {
  }

  public notifySelection(node) {
    this.onChildSelect.emit(node);
  }
}
