import { Space } from '@mckinsey-converge/data-common';
import { SelectOption } from '@mckinsey-converge/ui';
import { DateTZ } from "@mckinsey-converge/date-tz";

export const todayFromDate = (date: number) => new DateTZ({date});

/**
 * Adds 60 mins.
 */
export const laterThatDayFromDateTz = (dateTz: DateTZ) => {
    return dateTz.addValue({ minutes: 60 })
};

/**
 * Next business day. In most cases 1 day, but on Fridays, it follows to the monday.
 * @param date
 */
export const nextBusinessDay = (dateTz: DateTZ) => {
    let retDate = dateTz.addValue({ days: 1 });
    if (retDate.dateWeekday >= 6) {
        retDate = retDate.setValue({ weekday: 8 });
    }
    return retDate;
};

export const mapRoomToSelectOption = (room?: Space): SelectOption<any> => room ? ({
    value: room.id,
    shortDisplay: room.local_name,
    display: `${room.local_name}, ${room.level?.name}`
}) : undefined;
