    /** Generate time blocks to display on the day view */
    private generateTimeBlocks(): Identity[] {
        const blocks: Identity[] = [];
        const date = DateNow(new Date()).startOfValue('hour');
        for (let i = 0; i < 24; i++) {
            blocks.push({
                id: date.setValue({ hour: i }).formatDate('HH:mm'),
                name: date.setValue({ hour: i }).formatDate('h:mma').toLocaleLowerCase(),
            });
        }
        return blocks;
    }
