    private filterEvents(): Booking[] {
        if (!this.space) {
            return [];
        }
    
        return this.event_list.filter((event) => {
            // Mark as loading if override state matches.
            if (this.override_state && this.override_state === event.icaluid) {
                this.loading[event.id] = true;
            }
    
            const isRecurrentMaster = event.is_recurrent && event.recurrence_type === 'master';
            const isRoomEmailMatch = event.multi_rooms.some(room => room.email === this.space.email);
    
            // Determine if the event should be included based on setup or breakdown conditions.
            const isIncludedInSetupOrBreakdown = event.multi_rooms.some(room => {
                return event.setup[room.email] || event.breakdown[room.email];
            });
    
            return !isRecurrentMaster && isRoomEmailMatch && (!this.overflow_only || isIncludedInSetupOrBreakdown);
        }).map(event => new Booking({...event.toJSON()}));
    }

