import {
    distinctUntilChanged,
    filter
} from 'rxjs/operators';
import * as deepEqual from 'fast-deep-equal';

/**
 * Useful for nested objects to prevent unnecessary triggers in RX chain.
 */
export const deepDistinctUntilChanged = () => distinctUntilChanged(deepEqual);

/**
 * Won't emit if value is undefined.
 */
export const filterUndefined = <T>() => filter<T>(f => !!f);
