import {Component,OnInit,Input,Output,EventEmitter,
  } from '@angular/core';
  import {
    Booking,
    selectActiveForm,
    BookingFormData,
		BookingStoreState,
  } from '@mckinsey-converge/data-common';
  import {
		Store,
    select,
  } from '@ngrx/store';
  import {
    distinctUntilChanged, take,
  } from 'rxjs/operators';
  import { BaseDirective } from '@mckinsey-converge/base';
	import {
		RoomStoreState,
	} from '@mckinsey-converge/rooms';
	import {
		BuildingStoreState
	} from '@mckinsey-converge/buildings';
	import { UserStoreState } from '@mckinsey-converge/user';

	export type RoomResultsState = BookingStoreState
  & RoomStoreState
  & BuildingStoreState
  & UserStoreState;


  @Component({
    selector: 'app-booking-edit-wrapper',
    template: `  <div >
										<app-booking-edit
												*ngIf="!loading"
												[booking]="booking"
												(closeCallback)="toggleUpdateView($event)"
										></app-booking-edit>
										<ngx-skeleton-loader *ngIf="loading" class="mck-skeleton-load-more-100p"></ngx-skeleton-loader>
								</div>`,
    styleUrls: ['./booking-create.component.scss']
  })
  export class BookingEditWrapperComponent extends BaseDirective implements OnInit {

		public booking: Booking;
		public loading: boolean = true;

    constructor(
        public store: Store<RoomResultsState>,
      ) {
        super();
      }

    public ngOnInit(): void {

      this.subscription('activeForm',
        this.store.pipe(take(1),
					select(selectActiveForm),
        	distinctUntilChanged()
					).subscribe((next: BookingFormData | undefined) => {
						this.loading = false;
        		if (next && next?.dateTz) {
          		this.booking = new Booking(next);
        		}
      }));

    }

  }
